<template>
    <a-layout>
        <a-layout-header>
            <a-row>
                <a-col :span="1">
                    <div class="logo"></div>
                </a-col>
                <a-col :span="7">
                    <div class="customerInformation">
                        <p
                            class="pageHead"
                            :title="`${this.userinfo.curCustomerId}-${this.userinfo.curCustomerName}`"
                        >
                            客户:{{
                                `${this.userinfo.curCustomerId}-${this.userinfo.curCustomerName}`
                            }}
                        </p>
                        <p
                            class="pageHead"
                            :title="this.userinfo.curProjectName"
                        >
                            项目:{{ this.userinfo.curProjectName }}
                        </p>
                    </div>
                </a-col>
                <a-col :span="7">
                    <div class="title">
                        关联方对账 &nbsp; &nbsp; &nbsp; &nbsp; 已确认:
                        {{ optionListCountObj.complete }}
                        未确认:
                        {{ optionListCountObj.unfinished }}
                    </div>
                </a-col>
                <a-col :span="9">
                    <div v-if="item.mergeCustomerId" class="topBtn">
                        <a-button
                            @click="clickCancel"
                            v-if="confirmCompletionButFun(1)"
                            type="primary"
                            >取 消</a-button
                        >
                        <a-button
                            @click="clickEnterModelShow"
                            v-if="confirmCompletionButFun(0)"
                            type="primary"
                            >确 认 完 成</a-button
                        >
                        <a-button
                            v-if="
                                item.checkComplete != '1' &&
                                showIf &&
                                activeKey == '2'
                            "
                            @click="save"
                            type="primary"
                            >保 存</a-button
                        >

                        <a-button
                            v-if="
                                item.checkComplete != '1' &&
                                showIf &&
                                activeKey == '2'
                            "
                            @click="clickReset"
                            type="primary"
                            >清 除</a-button
                        >

                        <a-button
                            v-if="
                                item.checkComplete != '1' &&
                                showIf &&
                                activeKey == '2'
                            "
                            @click="clickRecommend"
                            style="padding-left: 30px"
                            type="primary"
                            ><img
                                style="position: absolute; left: 4px; top: 2px"
                                src="../assets/images/xxr24.png"
                                alt=""
                            />推 荐 分 析</a-button
                        >
                        <a-button
                            v-if="item.checkComplete != '1' && showIf"
                            @click="init"
                            type="primary"
                            >财 务 数 据 初 始 化</a-button
                        >
                        <a-button
                            v-if="activeKey == '1'"
                            @click="showHidden"
                            class="rightButton"
                            type="primary"
                            >{{
                                showHiddenFlag
                                    ? '查 看 发 生 额'
                                    : '隐 藏 发 生 额'
                            }}</a-button
                        >
                    </div>
                </a-col>
            </a-row>
        </a-layout-header>
        <a-layout-content>
            <a-spin tip="加载中..." :spinning="loading">
                <div class="content">
                    <a-button
                        type="primary"
                        :style="{
                            left: showLeft ? '14%' : '0',
                        }"
                        class="shrink-button"
                        @click="showLeft = !showLeft"
                    >
                        <a-icon type="caret-left" v-if="showLeft" />
                        <a-icon type="caret-right" v-if="!showLeft" />
                    </a-button>
                    <!-- 侧边栏 -->
                    <div class="left" v-if="showLeft">
                        <a-select
                            @change="associaChange"
                            :options=changeList
                            placeholder="请输入"
                            :default-value="['all']"
                            style="width: 200px"
                            show-search
                            :filter-option="filterOption"
                        >
                        </a-select>
                        <a-list
                            item-layout="horizontal"
                            :data-source="filteredOptionsList"
                        >
                            <a-list-item
                                slot="renderItem"
                                slot-scope="item"
                            >
                                <a-list-item-meta>
                                    <a
                                        :style="{
                                            color:
                                                item.mergeCustomerName ==
                                                mergeCustomerName
                                                    ? '#1890ff'
                                                    : '',
                                        }"
                                        slot="title"
                                        @click.stop="
                                            () => {
                                                activeKey = '1';
                                                getTableList(item);
                                            }
                                        "
                                        >
                                            {{ item.mergeCustomerNameList }}
                                            <a-icon :type="iconMap[item.mergeScope]" :title="item.mergeCustomerName"/>
                                        <a-icon
                                            v-if="item.checkComplete == 1"
                                            style="color: #46c37b"
                                            type="check"
                                        />
                                    </a>
                                </a-list-item-meta>
                            </a-list-item>
                        </a-list>
                    </div>
                    <!-- 右侧内容 -->
                    <div
                        class="right"
                        :style="{
                            width: !showLeft ? '100%' : null,
                        }"
                    >
                        <span
                            class="ucheckManName ofh"
                            v-if="
                                item.__ucheckManName &&
                                item.__ucheckManName.userName
                            "
                            :title="`核对人: ${
                                item.__ucheckManName.userName
                            }&nbsp; &nbsp; 核对时间:${XEUtils.toDateString(
                                new Date(item.checkTime),
                                'yyyy-MM-dd'
                            )}&nbsp; &nbsp; 核对理由:${item.checkReason}`"
                        >
                            核对人:
                            {{ item.__ucheckManName.userName }}
                            &nbsp; &nbsp; 核对时间:{{
                                XEUtils.toDateString(
                                    new Date(item.checkTime),
                                    'yyyy-MM-dd'
                                )
                            }}
                            &nbsp; &nbsp; 核对理由:{{ item.checkReason }}
                        </span>
                        <!-- tabs -->
                        <a-tabs v-model="activeKey" type="card">
                            <!-- 科目核对 -->
                            <a-tab-pane key="1" tab="科目核对">
                                <div class="table_1">
                                    <span
                                        :style="{
                                            display: 'inline-block',
                                            width: tableWdith,
                                        }"
                                    >
                                        <p
                                            style="height: 22px"
                                            :title="`${this.userinfo.curCustomerId}-${this.userinfo.curCustomerName}`"
                                        >
                                            客户:{{
                                                `${this.userinfo.curCustomerId}-${this.userinfo.curCustomerName}`
                                            }}
                                        </p>
                                        <a-table
                                            class="tab1"
                                            :columns="columns"
                                            :scroll="{
                                                y: maxH,
                                                x: 'scollXWidth',
                                            }"
                                            :data-source="data1"
                                            rowKey="__uid"
                                            tableLayout="fixed"
                                            :pagination="false"
                                            :components='components'
                                            
                                        >
                                            <div
                                                slot="oppositeSubjectName"
                                                slot-scope="text, record"
                                                class="ofh"
                                                style="text-align: right"
                                                :title="text"
                                                :class="linkSty(text, record)"
                                                @click="openWind(text, record)"
                                            >
                                                {{ text }}
                                            </div>
                                            <div
                                                slot="remain"
                                                slot-scope="text, record"
                                                class="ofh"
                                                style="text-align: right"
                                                :title="
                                                    XEUtils.commafy(
                                                        record.remain,
                                                        {
                                                            digits: 2,
                                                        }
                                                    )
                                                "
                                            >
                                                <div
                                                    :style="{
                                                        color: record.remainColor
                                                            ? 'red'
                                                            : '',
                                                    }"
                                                >
                                                    {{
                                                        XEUtils.commafy(
                                                            record.remain,
                                                            {
                                                                digits: 2,
                                                            }
                                                        )
                                                    }}
                                                </div>
                                                <div class="colBule">
                                                    {{
                                                        record.remainAdjust > 0
                                                            ? '+'
                                                            : ''
                                                    }}
                                                    {{
                                                        record.remainAdjust
                                                            ? `${XEUtils.commafy(
                                                                  record.remainAdjust,
                                                                  { digits: 2 }
                                                              )}`
                                                            : ''
                                                    }}
                                                </div>
                                                <div class="colLime">
                                                    {{
                                                        record.remainTotal
                                                            ? `${XEUtils.commafy(
                                                                  record.remainTotal,
                                                                  { digits: 2 }
                                                              )}`
                                                            : ''
                                                    }}
                                                </div>
                                            </div>
                                            <div
                                                slot="debitOcc"
                                                class="ofh"
                                                :style="
                                                    accountCheckLinke(record)
                                                "
                                                slot-scope="text, record"
                                                @click.stop="
                                                    queryMerchantCheckVoucherDetail(
                                                        record
                                                    )
                                                "
                                            >
                                                <div>
                                                    {{
                                                        XEUtils.commafy(
                                                            record.debitOcc,
                                                            {
                                                                digits: 2,
                                                            }
                                                        )
                                                    }}
                                                </div>
                                                <div class="colBule">
                                                    {{
                                                        record.debitAdjust > 0
                                                            ? '+'
                                                            : ''
                                                    }}
                                                    {{
                                                        record.debitAdjust
                                                            ? `${XEUtils.commafy(
                                                                  record.debitAdjust,
                                                                  {
                                                                      digits: 2,
                                                                  }
                                                              )}`
                                                            : ''
                                                    }}
                                                </div>
                                                <div class="colLime">
                                                    {{
                                                        record.debitOccTotal
                                                            ? `${XEUtils.commafy(
                                                                  record.debitOccTotal,
                                                                  { digits: 2 }
                                                              )}`
                                                            : ''
                                                    }}
                                                </div>
                                            </div>
                                            <div
                                                slot="creditOcc"
                                                class="ofh"
                                                :style="
                                                    accountCheckLinke(record)
                                                "
                                                slot-scope="text, record"
                                                data-dropflag="1"
                                                @click.stop="
                                                    queryMerchantCheckVoucherDetail(
                                                        record
                                                    )
                                                "
                                            >
                                                <div>
                                                    {{
                                                        XEUtils.commafy(
                                                            record.creditOcc,
                                                            {
                                                                digits: 2,
                                                            }
                                                        )
                                                    }}
                                                </div>
                                                <div class="colBule">
                                                    {{
                                                        record.creditAdjust > 0
                                                            ? '+'
                                                            : ''
                                                    }}
                                                    {{
                                                        record.creditAdjust
                                                            ? `${XEUtils.commafy(
                                                                  record.creditAdjust,
                                                                  {
                                                                      digits: 2,
                                                                  }
                                                              )}`
                                                            : ''
                                                    }}
                                                </div>
                                                <div class="colLime">
                                                    {{
                                                        record.creditOccTotal
                                                            ? `${XEUtils.commafy(
                                                                  record.creditOccTotal,
                                                                  { digits: 2 }
                                                              )}`
                                                            : ''
                                                    }}
                                                </div>
                                            </div>
                                            <div
                                                slot="balance"
                                                class="ofh"
                                                style="text-align: right"
                                                slot-scope="text, record"
                                                :title="
                                                    XEUtils.commafy(
                                                        record.balance,
                                                        {
                                                            digits: 2,
                                                        }
                                                    )
                                                "
                                            >
                                                <div
                                                    :style="{
                                                        color: record.balanceColor
                                                            ? 'red'
                                                            : '',
                                                    }"
                                                >
                                                    {{
                                                        XEUtils.commafy(
                                                            record.balance,
                                                            {
                                                                digits: 2,
                                                            }
                                                        )
                                                    }}
                                                </div>
                                                <div class="colBule">
                                                    {{
                                                        record.balanceAdjust > 0
                                                            ? '+'
                                                            : ''
                                                    }}
                                                    {{
                                                        record.balanceAdjust
                                                            ? `${XEUtils.commafy(
                                                                  record.balanceAdjust,
                                                                  {
                                                                      digits: 2,
                                                                  }
                                                              )}`
                                                            : ''
                                                    }}
                                                </div>
                                                <div class="colLime">
                                                    {{
                                                        record.balanceTotal
                                                            ? `${XEUtils.commafy(
                                                                  record.balanceTotal,
                                                                  { digits: 2 }
                                                              )}`
                                                            : ''
                                                    }}
                                                </div>
                                            </div>
                                            <template
                                                #subjectName="text, record"
                                            >
                                                <span
                                                    :style="{
                                                        color: record.tip
                                                            ? 'red'
                                                            : '',
                                                        fontSize: '12px',
                                                    }"
                                                >
                                                    <a-tooltip
                                                        placement="topLeft"
                                                    >
                                                        <template slot="title">
                                                            <span
                                                                >存在多个对方科目未分析完成的情况，请点击对方科目完成对应凭证分析</span
                                                            >
                                                        </template>
                                                        <a-icon
                                                            type="close-circle"
                                                            v-if="record.tip"
                                                        />
                                                    </a-tooltip>

                                                    {{ text }}
                                                </span>
                                            </template>
                                        </a-table>
                                    </span>
                                    <span
                                        :style="{
                                            display: 'inline-block',
                                            width: tableWdith,
                                        }"
                                    >
                                        <p
                                            style="height: 22px"
                                            :title="`${
                                                this.item.mergeCustomerName
                                                    ? this.item
                                                          .mergeCustomerName
                                                    : ''
                                            }`"
                                        >
                                            {{
                                                `${
                                                    this.item.mergeCustomerName
                                                        ? this.item
                                                              .mergeCustomerName
                                                        : ''
                                                }`
                                            }}
                                            <a-tooltip
                                                placement="topLeft"
                                                title="企业微信提醒"
                                            >
                                                <a-button
                                                    v-if="
                                                        this.item
                                                            .mergeCustomerName
                                                    "
                                                    type="primary"
                                                    icon="wechat"
                                                    :loading="submitLoading"
                                                    @click="
                                                        sendCheckMessageConfirm
                                                    "
                                                />
                                            </a-tooltip>
                                        </p>
                                        <a-table
                                            :columns="columnsCopy"
                                            :data-source="data2"
                                            tableLayout="fixed"
                                            rowKey="__uid"
                                            :scroll="{
                                                y: maxH,
                                                x: 'scollXWidth',
                                            }"
                                            :pagination="false"
                                            :rowClassName="rowClassName"
                                            :components='components'

                                        >
                                            <div
                                                slot="remain1"
                                                class="ofh"
                                                slot-scope="text, record"
                                                style="text-align: right"
                                                :title="
                                                    XEUtils.commafy(
                                                        record.remain1,
                                                        {
                                                            digits: 2,
                                                        }
                                                    )
                                                "
                                            >
                                                <div
                                                    :style="{
                                                        color: record.remainColor
                                                            ? 'red'
                                                            : '',
                                                    }"
                                                >
                                                    {{
                                                        XEUtils.commafy(
                                                            record.remain1,
                                                            {
                                                                digits: 2,
                                                            }
                                                        )
                                                    }}
                                                </div>
                                                <div class="colBule">
                                                    {{
                                                        record.remainAdjust1 > 0
                                                            ? '+'
                                                            : ''
                                                    }}
                                                    {{
                                                        record.remainAdjust1
                                                            ? `${XEUtils.commafy(
                                                                  record.remainAdjust1,
                                                                  { digits: 2 }
                                                              )}`
                                                            : ''
                                                    }}
                                                </div>
                                                <div class="colLime">
                                                    {{
                                                        record.remainTotal
                                                            ? `${XEUtils.commafy(
                                                                  record.remainTotal,
                                                                  { digits: 2 }
                                                              )}`
                                                            : ''
                                                    }}
                                                </div>
                                            </div>
                                            <div
                                                slot="debitOcc1"
                                                class="ofh"
                                                :style="
                                                    accountCheckLinke(record)
                                                "
                                                slot-scope="text, record"
                                                :class="unequal(text, record)"
                                                @click.stop="
                                                    queryMerchantCheckVoucherDetail(
                                                        record
                                                    )
                                                "
                                            >
                                                <div>
                                                    {{
                                                        XEUtils.commafy(
                                                            record.debitOcc1,
                                                            {
                                                                digits: 2,
                                                            }
                                                        )
                                                    }}
                                                </div>
                                                <div class="colBule">
                                                    {{
                                                        record.debitAdjust1 > 0
                                                            ? '+'
                                                            : ''
                                                    }}
                                                    {{
                                                        record.debitAdjust1
                                                            ? `${XEUtils.commafy(
                                                                  record.debitAdjust1,
                                                                  {
                                                                      digits: 2,
                                                                  }
                                                              )}`
                                                            : ''
                                                    }}
                                                </div>
                                                <div class="colLime">
                                                    {{
                                                        record.debitOccTotal
                                                            ? `${XEUtils.commafy(
                                                                  record.debitOccTotal,
                                                                  { digits: 2 }
                                                              )}`
                                                            : ''
                                                    }}
                                                </div>
                                            </div>
                                            <div
                                                slot="creditOcc1"
                                                class="ofh"
                                                :style="
                                                    accountCheckLinke(record)
                                                "
                                                slot-scope="text, record"
                                                @click.stop="
                                                    queryMerchantCheckVoucherDetail(
                                                        record
                                                    )
                                                "
                                            >
                                                <div>
                                                    {{
                                                        XEUtils.commafy(
                                                            record.creditOcc1,
                                                            {
                                                                digits: 2,
                                                            }
                                                        )
                                                    }}
                                                </div>
                                                <div class="colBule">
                                                    {{
                                                        record.creditAdjust1 > 0
                                                            ? '+'
                                                            : ''
                                                    }}
                                                    {{
                                                        record.creditAdjust1
                                                            ? `${XEUtils.commafy(
                                                                  record.creditAdjust1,
                                                                  {
                                                                      digits: 2,
                                                                  }
                                                              )}`
                                                            : ''
                                                    }}
                                                </div>
                                                <div class="colLime">
                                                    {{
                                                        record.creditOccTotal
                                                            ? `${XEUtils.commafy(
                                                                  record.creditOccTotal,
                                                                  { digits: 2 }
                                                              )}`
                                                            : ''
                                                    }}
                                                </div>
                                            </div>
                                            <div
                                                slot="balance1"
                                                class="ofh"
                                                style="text-align: right"
                                                slot-scope="text, record"
                                                :title="
                                                    XEUtils.commafy(
                                                        record.balance1,
                                                        {
                                                            digits: 2,
                                                        }
                                                    )
                                                "
                                            >
                                                <div
                                                    :style="{
                                                        color: record.balanceColor
                                                            ? 'red'
                                                            : '',
                                                    }"
                                                >
                                                    {{
                                                        XEUtils.commafy(
                                                            record.balance1,
                                                            {
                                                                digits: 2,
                                                            }
                                                        )
                                                    }}
                                                </div>
                                                <div class="colBule">
                                                    {{
                                                        record.balanceAdjust1 >
                                                        0
                                                            ? '+'
                                                            : ''
                                                    }}
                                                    {{
                                                        record.balanceAdjust1
                                                            ? `${XEUtils.commafy(
                                                                  record.balanceAdjust1,
                                                                  {
                                                                      digits: 2,
                                                                  }
                                                              )}`
                                                            : ''
                                                    }}
                                                </div>
                                                <div class="colLime">
                                                    {{
                                                        record.balanceTotal
                                                            ? `${XEUtils.commafy(
                                                                  record.balanceTotal,
                                                                  { digits: 2 }
                                                              )}`
                                                            : ''
                                                    }}
                                                </div>
                                            </div>
                                            <template
                                                #subjectName1="text, record"
                                            >
                                                <span
                                                    :style="{
                                                        color: record.tip
                                                            ? 'red'
                                                            : '',
                                                        fontSize: '12px',
                                                    }"
                                                >
                                                    <a-tooltip
                                                        placement="topLeft"
                                                    >
                                                        <template slot="title">
                                                            <span
                                                                >存在多个对方科目未分析完成的情况，请点击对方科目完成对应凭证分析</span
                                                            >
                                                        </template>
                                                        <a-icon
                                                            type="close-circle"
                                                            v-if="record.tip"
                                                        />
                                                    </a-tooltip>
                                                    <!-- record.autoId1?record.autoId1 + text:text -->
                                                    {{ text }}
                                                </span>
                                            </template>
                                        </a-table>
                                    </span>
                                </div>
                            </a-tab-pane>
                            <!-- 明细核对 -->
                            <a-tab-pane key="2" tab="明细核对">
                                <!-- 明细核对-左表 -->
                                <div class="box-left">
                                    <div
                                        style="
                                            margin-bottom: 5px;
                                            line-height: 32px;
                                        "
                                    >
                                       已关联:{{
                                            leftCountObj.relatedCount
                                        }}
                                        未关联:{{
                                            leftCountObj.unRelatedCount
                                        }}
                                        总件数:{{ leftCountObj.totalCount }}
                                        <span v-if="item.mergeCustomerId">
                                            <a-select
                                                v-model="sortOrder1.left"
                                                show-search
                                                style="width: 200px"
                                                allowClear
                                                placeholder="请选择科目"
                                                @change="
                                                    (val, option) => {
                                                        selectChange1(
                                                            val,
                                                            1,
                                                            option
                                                        );
                                                    }
                                                "
                                            >
                                                <a-select-option
                                                    v-for="item in data3"
                                                    :key="item.subjectId"
                                                    :value="item.subjectId"
                                                >
                                                    {{ item.subjectFullName }}
                                                </a-select-option>
                                            </a-select>
                                            <a-select
                                                v-model="sortOrder1.leftDate"
                                                show-search
                                                style="width: 100px"
                                                allowClear
                                                placeholder="月份"
                                                @change="
                                                    (val, option) => {
                                                        dateChange(1, val);
                                                    }
                                                "
                                            >
                                                <a-select-option
                                                    :value="mon.value"
                                                    v-for="(mon, idx) in month"
                                                    :key="idx"
                                                    >{{
                                                        mon.label
                                                    }}</a-select-option
                                                >
                                            </a-select>
                                        </span>
                                    </div>
                                    <div class="left_table">
                                        <table
                                          :components='components'
                                            data-dropflag="1"
                                            border="1"
                                            @dragleave="
                                                onDragleave($event, 'one', {})
                                            "
                                        >
                                            <thead>
                                                <tr
                                                    style="
                                                        background-color: #ebeef5;
                                                    "
                                                >
                                                    <th
                                                        v-for="(
                                                            item, idx
                                                        ) in tableData[0]
                                                            .columns"
                                                        :key="idx + 'q'"
                                                        :style="{
                                                            border: '1px solid #dcdfe6',
                                                            width: item.width,
                                                            position: 'relative'
                                                        }"
                                                        
                                                    >
                                                        {{ item.title }}
                                                        <span class="resize-handler"></span>
                                                    </th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr
                                                    data-dropflag="1"
                                                    @dragover.prevent="
                                                        onDragover(
                                                            $event,
                                                            'one',
                                                            items
                                                        )
                                                    "
                                                    @contextmenu.prevent="
                                                        onContextmenu(
                                                            $event,
                                                            'one',
                                                            items
                                                        )
                                                    "
                                                    @dragenter="
                                                        onDragenter(
