<script>
import Vue from 'vue'
import VueDraggableResizable from 'vue-draggable-resizable'
import GridMockTable, { datadrag } from '../GridMockTable/GridMockTable.vue';
import sessionUtils from '@data/session/sessionUtils.js';
import XEUtils from 'xe-utils';
import { formatter } from '../libs/utils/formatter';
import showDetailsModal from './showDetailsModal';
import clickEnterModel from './clickEnterModel';
import counter from '../components/counter';
import {
    columns,
    columns1,
    columns2,
    columns3,
    columns4,
} from '../model/transactionVoucher.js';
import { add } from 'xe-utils/methods';
import { uniqBy } from 'lodash';

const iconMap = {
    0: 'select',
    1: 'interaction',
    2: 'shake'
}

export default {
    name: 'App',
    directives: { datadrag },
    components: { showDetailsModal, clickEnterModel, counter },
    data() {
          this.components = {
            header: {
                cell: (h, props, children) => {
                    const { key, ...restProps } = props;
                    const col = this.columns.find((col) => {
                        const k = col.dataIndex || col.key;
                        return k === key;
                    });

                    if (!col || !col.width) {
                        return h('th', { ...restProps }, [...children]);
                    }

                    const dragProps = {
                        key: col.dataIndex || col.key,
                        class: 'table-draggable-handle',
                        attrs: {
                            w: 10,
                            x: col.width,
                            z: 1,
                            axis: 'x',
                            draggable: true,
                            resizable: false,
                        },
                        on: {
                            dragging: (x, y) => {
                                col.width = Math.max(x, 1);
                            },
                        },
                    };
                    const drag = h('vue-draggable-resizable', { ...dragProps });
                    return h('th', { ...restProps, class: 'resize-table-th' }, [
                        ...children,
                        drag,
                    ]);
                },
            },
        };
        this.formatter = formatter;
        this.XEUtils = XEUtils;
        return {
            iconMap,
            changeList:[
                { type: 'batch', value: 'all', label: '全部', filterLabel: '全部' },
                { type: 'batch', value: '1', label: '仅合并范围内', filterLabel: '仅合并范围内' },
                { type: 'batch', value: '0', label: '仅合并范围外且是关联方', filterLabel: '仅合并范围外且是关联方' },
                { type: 'batch', value: '2', label: "仅本公司关联方", filterLabel: "仅本公司关联方" },
            ],
            maxH: 'calc(100vh - 220px)',
            activeKey: '1',
            showIf: false,
            // 排序方式
            sortOrder: {
                //  1 已关联  2未关联  3全部
                left: '3',
                right: '3',
            },
            sortOrder1: {
                left: undefined,
                right: undefined,
                leftDate: undefined,
                rightDate: undefined,
            },
            // 表格数据
            tableData: [
                {
                    columns,
                    data: [],
                },
                {
                    columns,
                    data: [],
                },
            ],
            // 显示左边菜单
            showLeft: true,
            loading: false,
            // 左边菜单数据
            optionList: [],
            filteredOptionsList: [],
            // 左边菜单计数
            optionListCountObj: {
                complete: 0,
                unfinished: 0,
            },
            // 暂存数据
            item: {},
            entrySerail: null,
            dragType: '',
            entrySerailCount: '',
            classShow: null,
            userinfo: {},
