            const filteredOptionsList = type === 'batch' ? this.genBatchList(val) : this.optionList.filter(item => item.mergeCustomerId === val);
            this.filteredOptionsList = filteredOptionsList;
        },
        genBatchList(val) {
            if (val === 'all') {
                return this.optionList;
            }
            
            return this.optionList.filter(item => item.mergeScope === val);
        },
        // 查看方式
        selectChange(val, i) {
