        accountCheckLinke(row) {
            if (!row.isChildren) {
                return {
                    textAlign: 'right',
                };
            } else {
                return {
                    textAlign: 'right',
                    cursor: 'pointer',
                    color: '#1890ff',
                };
            }
        },
        test(standard, mapValues) {
            let sum = (...values) =>
                Number(
                    values.reduce((total, curr) => total + curr, 0).toFixed(5)
                );
            let leftVals = (start, max) => {
                let vals = [];
                for (let i = start; i <= max; i++) {
                    vals.push(i);
                }
                return vals;
            };
            let range = mapValues.length;
            let result = [],
                idxArgs = [],
                valArgs = [];
            for (let i = 0; i < range; i++) {
                // lauout
                if (mapValues[i] === standard) {
                    // f(i)
                    result.push({ key: i.toString(), value: mapValues[i] });
                }
                for (let j = i + 1; j < range; j++) {
                    // deep
                    for (let k = j; k < range; k++) {
                        // length
                        idxArgs = leftVals(j, k);
                        valArgs = idxArgs.map((item) => mapValues[item]);
                        if (sum(mapValues[i], ...valArgs) === standard) {
                            result.push({
                                key: [i, ...idxArgs].join(','),
                                value: [mapValues[i], ...valArgs],
                            });
                        }
                    }
                }
            }
            return result;
        },
        clickRecommend() {
            const that = this;
            this.$confirm({
                title: `提示`,
                content:
                    '使用小杏仁推荐，将会清空所有关联关系，并对时间相近，金额相等的数据进行关联，是否确认？',
                onOk() {
                    that.recommend();
                },
                onCancel() {},
            });
        },
        // 推荐
        recommend() {
            this.reset();
            let list1 = [];
            this.tableData[0].data.map((item) => {
                if (item.vchDate) {
                    list1.push(item);
                }
            });
            let list2 = [];
            this.tableData[1].data.map((item) => {
                if (item.vchDate) {
                    list2.push(item);
                }
            });
            let temporaryData = {};
            for (let i = 0; i < list1.length; i++) {
                let list = [];
                let moneyList = [];
                for (let t = 0; t < list2.length; t++) {
                    // 当时间相差不超过五天
                    if (
                        Math.abs(
                            new Date(list1[i].vchDate) -
                                new Date(list2[t].vchDate)
                        ) <= 432000000
                    ) {
                        list.push(list2[t]);
                        moneyList.push(list2[t].occurValue);
                    }
                }
                let item = {
                    time: list1[i].vchDate,
                    entrySerail: list1[i].entrySerail,
                    list: list,
                    occurValue: list1[i].occurValue,
                    moneyList: moneyList,
                };
                if (list.length > 0) {
                    // 有相近时间
                    temporaryData[list1[i].entrySerail] = item;
                }
            }
            for (let key in temporaryData) {
                this.okFlag = false;
                const { list, occurValue, entrySerail, moneyList } =
                    temporaryData[key];
                if (list.length === 1) {
                    if (list[0].occurValue === occurValue) {
                        this.assignment(list[0].entrySerail, entrySerail, true);
                        this.assignment(entrySerail, list[0].entrySerail, true);
                    }
                } else {
                    let accordList = this.test(occurValue, moneyList);
                    // 排序  优先最少的匹配
                    accordList.sort(function (a, b) {
                        return a.key.length < b.key.length ? -1 : 1;
                    });
                    if (accordList[0]) {
                        // 循环同相近日期内容 （前后五天
                        accordList.map((t) => {
                            // 已完成过  不在执行
                            if (this.okFlag) {
                                return;
                            }
                            let itemIndexList = t.key.split(',');
                            let checkEntrySerail = '';

                            let flag = this.flagFun(t, list);
                            if (!flag) {
                                return;
                            }

                            itemIndexList.map((item) => {
                                // 右边多条赋值
                                this.assignment(
                                    list[item].entrySerail,
                                    entrySerail,
                                    flag
                                );
                                checkEntrySerail =
                                    (checkEntrySerail
                                        ? checkEntrySerail + '<lx2>'
                                        : '') + list[item].entrySerail;
                            });
                            // 给左边单条赋值
                            this.assignment(
                                entrySerail,
                                checkEntrySerail,
                                flag
                            );
                            this.okFlag = true;
                        });
                    }
                }
            }
            let list3 = this.tableData[0].data.filter((item) => {
                if (!item.flag && item.vchDate) {
                    return item;
                }
            });
            let list4 = this.tableData[1].data.filter((item) => {
                if (!item.flag && item.vchDate) {
                    return item;
                }
            });
            temporaryData = {};
            for (let i = 0; i < list4.length; i++) {
                let list = [];
                let moneyList = [];
                for (let t = 0; t < list3.length; t++) {
                    // 当时间相差不超过五天
                    if (
                        Math.abs(
                            new Date(list4[i].vchDate) -
                                new Date(list3[t].vchDate)
                        ) <= 432000000
                    ) {
                        list.push(list3[t]);
                        moneyList.push(list3[t].occurValue);
                    }
                }
                let item = {
                    time: list4[i].vchDate,
                    entrySerail: list4[i].entrySerail,
                    list: list,
                    occurValue: list4[i].occurValue,
                    moneyList: moneyList,
                };

                if (list.length > 0) {
                    // 有相近时间
                    temporaryData[list4[i].entrySerail] = item;
                }
            }
            for (let key in temporaryData) {
                this.okFlag = false;
                const tempData = temporaryData[key];
                const list = tempData.list;
                if (list.length === 1) {
                    if (list[0].occurValue === tempData.occurValue) {
                        this.assignment(
                            list[0].entrySerail,
                            tempData.entrySerail,
                            true
                        );
                        this.assignment(
                            tempData.entrySerail,
                            list[0].entrySerail,
                            true
                        );
                    }
                } else {
                    let accordList = this.test(
                        tempData.occurValue,
                        tempData.moneyList
                    );
                    // 排序  优先最少的匹配
                    accordList.sort(function (a, b) {
                        return a.key.length < b.key.length ? -1 : 1;
                    });
                    if (accordList[0]) {
                        // 循环同相近日期内容 （前后五天
                        accordList.map((t) => {
                            // 已完成过  不在执行
                            if (this.okFlag) {
                                return;
                            }
                            let itemIndexList = t.key.split(',');
                            let checkEntrySerail = '';

                            let flag = this.flagFun(t, list);
                            if (!flag) {
                                return;
                            }

                            itemIndexList.map((item) => {
                                // 右边多条赋值
                                this.assignment(
                                    list[item].entrySerail,
                                    tempData.entrySerail,
                                    flag
                                );
                                checkEntrySerail =
                                    (checkEntrySerail
                                        ? checkEntrySerail + '<lx2>'
                                        : '') + list[item].entrySerail;
                            });
                            // 给左边单条赋值
                            this.assignment(
                                tempData.entrySerail,
                                checkEntrySerail,
                                flag
                            );
                            this.okFlag = true;
                        });
                    }
                }
            }
        },
        flagFun(accord, list) {
            let itemIndexList = accord.key.split(',');
            let flagList = [];
            let flag = true;
            itemIndexList.map((item) => {
                this.tableData[1].data.map((i) => {
                    if (list[item].entrySerail === i.entrySerail) {
                        if (i.flag) {
                            flag = false;
                        }
                        flagList.push(i);
                    }
                });
            });
            if (!flag) {
                return flag;
            }
            flagList.map((item) => {
                this.tableData[1].data.map((i) => {
                    if (item.entrySerail === i.entrySerail) {
                        i.flag = true;
                    }
                });
            });
            return flag;
        },
        // 赋值
        assignment(id, value, flag = true) {
            // console.log('赋值', id, value);
            if (!flag) {
                return;
            }
            this.splicing(this.tableData[0].data, id, value);
            this.splicing(this.tableData[1].data, id, value);
        },
        splicing(data = [], id, value) {
            data.map((item) => {
                if (item.entrySerail === id) {
                    item.checkEntrySerail =
                        (item.checkEntrySerail
                            ? item.checkEntrySerail + '<lx2>'
                            : '') + value;
                    item.checkValue = item.occurValue;
                    item.flag = true;
                }
            });
        },
        // 清空关联方法
        reset() {
            this.entrySerail = '';
            this.resetData(this.tableData[0].data);
            this.resetData(this.tableData[1].data);
        },
        resetData(data = []) {
            data.map((item) => {
                item.checkEntrySerail = '';
                item.flag = false;
                item.checkValue = '';
            });
        },
        // 清空关联按钮
        clickReset() {
            this.$confirm({
                title: `提示`,
                content: '是否清空所有关联？',
                onOk: () => {
                    this.reset();
                },
                onCancel: () => {},
            });
        },
        // 取消
        clickCancel() {
            this.$confirm({
                title: `提示`,
                content: '是否重新核对？',
                onOk: () => {
                    // 1是确认  0是取消
                    let par = {
                        param1: this.item.uscc,
                        param2: this.item.mergeCustomerId,
                        param3: this.item.mergeProjectId,
                        param4: 0,
                        lockCustomerId: this.userinfo.curCustomerId,
                        lockProjectId: this.userinfo.preProjectId,
                        customerId: this.userinfo.mergeCustomerId,
                        projectId: this.userinfo.mergeProjectId,
                        menuID: this.userinfo.menuID,
                        lockYyyy:this.userinfo.curAccYear,
                    };
                    this.loading = true;
                    this.$sacpApis
                        .saveMerchantCheckComplete(par)
                        .then((res) => {
                            this.loading = false;
                            if (res.success) {
                                this.getOptionList();
                                this.$message.success('重置状态成功');
                                let refreshStatus =
                                    this.$store.state.refreshStatus;
                                this.$store.commit(
                                    'changeRefreshStatus',
                                    !refreshStatus
                                );
                            } else {
                                this.$message.error(res.resultInfo.statusText);
                            }
                        })
                        .catch((err) => {
                            this.loading = false;
                            this.$message.error(err);
                        });
                },
                onCancel: () => {},
            });
        },

        // 右键点击解除关联关系
        onContextmenu(e, type, item) {
            if (type === 'two') {
                const etsList = item.checkEntrySerail.split('<lx2>');
                if (etsList.length) {
                    etsList.forEach((k) => {
                        this.tableData[0].data.forEach((x) => {
                            if (
                                x.entrySerail &&
                                x.entrySerail.indexOf(k) > -1
                            ) {
                                item.checkEntrySerail = item.checkEntrySerail
                                    .replace(x.entrySerail, '')
                                    .replaceAll('<lx2>', '  ')
                                    .trim()
                                    .replaceAll('  ', '<lx2>')
                                    .replaceAll('<lx2><lx2>', '<lx2>');
                                x.checkEntrySerail = x.checkEntrySerail
                                    .replace(item.entrySerail, '')
                                    .replaceAll('<lx2>', '  ')
                                    .trim()
                                    .replaceAll('  ', '<lx2>')
                                    .replaceAll('<lx2><lx2>', '<lx2>');
                                item.checkValue = '';
                                x.checkValue = '';
                            }
                        });
                    });
                }
            }
        },
        rowStyle(data, item) {
            const checkList = item.checkEntrySerail;
            checkList.split('<lx2>').forEach((k) => {
                data.forEach((x) => {
                    if (k === x.entrySerail) {
                        return {
                            background: 'rgb(255 234 194)',
                        };
                    }
                });
            });
        },
        onDragover(e, type, item) {
            if (this.item.checkComplete == 1) {
                return;
            }
            if (e.target.dataset.dropflag) {
                this.activeBlockIndex = Number(e.target.dataset.dropflag);
            }
            if (this.dragType == type) {
                return;
            } else {
                // this.classShow = item.entrySerail;
                return;
            }
        },
        onDragenter(e, type, item) {
            if (this.item.checkComplete == 1) {
                return;
            }
            if (e.target.dataset.dropflag) {
                this.activeBlockIndex = Number(e.target.dataset.dropflag);
            }
            if (!this.isDragRendering) {
                this.isDragRendering = true;
                this.classShow = item.entrySerail;
                setTimeout(() => {
                    this.isDragRendering = false;
                }, 80);
            }
        },
        onDragleave(e, type, item) {
            if (this.item.checkComplete == 1) {
                return;
            }
            if (!e.target.dataset.dropflag) {
                this.activeBlockIndex = null;
            }
            if (this.dragType == type) {
                this.classShow = null;

                return;
            } else {
                if (!e.target.dataset.dropflag) {
                    this.classShow = null;
                }
            }
        },
        clickTr(e, type, item) {
            // 切换关联主数据
            if (item.entrySerail == this.entrySerail) {
                this.entrySerail = null;
            } else {
                this.entrySerail = item.entrySerail;
            }
            if (type === 'one' && this.entrySerail !== null) {
                let list1 = [];
                let list2 = [];
                this.tableData[1].data.map((i) => {
                    if (i.checkEntrySerail.indexOf(this.entrySerail) !== -1) {
                        list1.push(i);
                    } else {
                        list2.push(i);
                    }
                });
                this.tableData[1].data = [...list1, ...list2];
            } else {
                this.tableData[1].data.sort(function (a, b) {
                    return a.vchDate < b.vchDate ? -1 : 1;
                });
            }
        },
        // 拖动开始
        dragstartFun(e, type, item) {
            this.money = item.occurValue;
            if (this.item.checkComplete == 1) {
                return;
            }
            this.dragType = type;
            this.entrySerailCount = item.entrySerail;
        },
        // 接收
        onDrop(e, type, item) {
            // console.log('接收', type, item);
            let flag = false;
            if (this.item.checkComplete == 1) {
                return;
            }
            this.classShow = null;
            // 判断是否是同表格内
            if (this.dragType === type) return;
            // 判断是否已关联
            if (
                item.checkEntrySerail &&
                item.checkEntrySerail.indexOf(this.entrySerailCount) !== -1
            ) {
                // 切换关联主数据
                this.entrySerail = item.entrySerail;
                this.$message.error('已与该数据关联');
                return;
            }
            if (item.occurValue === this.money) {
                flag = true;
            }

            // 给接收数据添加关联
            this.entrySerail = item.entrySerail;
            item.checkEntrySerail =
                (item.checkEntrySerail ? item.checkEntrySerail + '<lx2>' : '') +
                this.entrySerailCount;
            // 给拖拽数据添加关联
            let count = type === 'one' ? 1 : 0;
            this.tableData[count].data.map((i) => {
                if (i.entrySerail === this.entrySerailCount) {
                    i.checkEntrySerail =
                        (i.checkEntrySerail
                            ? i.checkEntrySerail + '<lx2>'
                            : '') + item.entrySerail;
                    if (flag) {
                        item.checkValue = this.money;
                        i.checkValue = this.money;
                    }
                }
            });
        },
        // 页面进入
        showTable() {
            this.tableData[0].data = [];
            this.tableData[1].data = [];
            this.form = {};
            this.getOptionList();
        },
        // 左侧菜单
        getOptionList() {
            let par = {
                lockCustomerId: this.userinfo.curCustomerId,
                lockProjectId: this.userinfo.preProjectId,
                customerId: this.userinfo.mergeCustomerId,
                projectId: this.userinfo.mergeProjectId,
                menuID: this.userinfo.menuID,
                lockYyyy:this.userinfo.curAccYear,
            };
            const p1 = this.$sacpApis
                .queryMergeCustomer(par)
                .then((res) => {
                    if (res.success) {
                        let count = 0;
                        res.data.map((item) => {
                            if (item.checkComplete == '0') {
                                count++;
                            }
                            if (
                                this.item.mergeCustomerName ===
                                item.mergeCustomerName
                            ) {
                                this.item = item;
                            }
                        });
                        this.optionListCountObj = {
                            complete: res.data.length - count,
                            unfinished: count,
                        };
                        res.data.forEach((x) => {
                            if (this.item && x.uscc === this.item.uscc) {
                                this.item = x;
                            }
                        });
                        return res;
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                });

            const p2 = this.$sacpApis
                .queryDictionary()
                .then((res) => {
                    return res;
                })
                .catch((err) => this.$message.error(err))
                .finally(() => (this.spinning = false));

            Promise.all([p1, p2]).then((res) => {
                if (res[0] && Array.isArray(res[0].data)) {
                    res[0].data.forEach((x) => {
                        x.mergeCustomerNameList = x.mergeCustomerName
                        x.mergeCustomerName =
                            x.mergeCustomerName +
                            `(${res[1]['合并范围'][x.mergeScope]})`;
                    });
                    const optionList = res[0].data;
                    this.optionList = optionList;
                    this.filteredOptionsList = optionList;
                    const singleFilterOptions = optionList.map(item => {
                        return {
                            ...item,
                            type: 'single',
                            label: item.mergeCustomerName,
                            value: item.mergeCustomerId,
                            title: item.mergeCustomerName,
                        }
                    })
                    this.changeList = uniqBy([].concat(this.changeList, singleFilterOptions), 'value')
                }
            });
        },
        getTableList(item) {
            if (item) {
                this.item = item;
                this.mergeCustomerName = item.mergeCustomerName;
            }
            this.entrySerail = null;
            this.sortOrder = {
                //  1 已关联  2未关联  3全部
                left: '3',
                right: '3',
            };
            this.sortOrder1 = {
                left: undefined,
                right: undefined,
                leftDate: undefined,
                rightDate: undefined,
            };
            this.current1 = 1;
            this.current2 = 1;
            this.showHiddenFlag = false;
            this.showHidden();
            this.queryMerchantCheckAccList();
            this.queryMerchantCheckList({ num: 1 });
            this.queryMerchantCheckList({ num: 2 });
        },
        queryMerchantCheckList({ type, num, subject, date, start }) {
            let par = {
                start: start || 0,
                limit: 500,
                param1: this.item.uscc,
                param2: this.item.mergeCustomerId,
                param3: this.item.mergeProjectId,
                param4: type, //是否关联 1是 0否 全部传空
                param5: num === 1 ? subject : '',
                param6: num === 2 ? subject : '',
                param7: num === 1 ? date : '',
                param8: num === 2 ? date : '',
                param9: num,
                lockCustomerId: this.userinfo.curCustomerId,
                lockProjectId: this.userinfo.preProjectId,
                customerId: this.userinfo.mergeCustomerId,
                projectId: this.userinfo.mergeProjectId,
                menuID: this.userinfo.menuID,
                lockYyyy:this.userinfo.curAccYear,
            };
            this.loading = true;
            this.$sacpApis
                .queryMerchantCheckList(par)
                .then((res) => {
                    this.loading = false;
                    if (res.success) {
                        if (num === 1) {
                            this.tableData[0].data = res.data[0][1];
                            this.tableData[0].data.map((item, index) => {
                                item.show = true;
                                item.show1 = true;
                                item.input = false;
                            });
                            this.data3 = res.data[0][3];
                            this.leftCountObj = res.data[0][5];
                            this.total1 = res.data[0][5].totalCount;
                        }
                        if (num === 2) {
                            this.tableData[1].data = res.data[0][2];
                            this.tableData[1].data.map((item) => {
                                item.show = true;
                                item.show1 = true;
                                item.input = false;
                            });
                            this.data4 = res.data[0][4];
                            this.rightCountObj = res.data[0][6];
                            this.total2 = res.data[0][6].totalCount;
                        }
                    } else {
                        this.tableData[1].data = [];
                        this.tableData[0].data = [];
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.loading = false;
                    this.$message.error(err);
                });
        },
        queryMerchantCheckAccList() {
            let par = {
                param1: this.item.uscc,
                param2: this.item.mergeCustomerId,
                param3: this.item.mergeProjectId,
                lockCustomerId: this.userinfo.curCustomerId,
                lockProjectId: this.userinfo.preProjectId,
                customerId: this.userinfo.mergeCustomerId,
                projectId: this.userinfo.mergeProjectId,
                menuID: this.userinfo.menuID,
                lockYyyy:this.userinfo.curAccYear,
            };
            this.loading = true;
            this.$sacpApis
                .queryMerchantCheckAccList(par)
                .then((res) => {
                    this.loading = false;
                    if (res.success) {
                        let subjectName = '借合-贷合';

                        // 借方
                        let debitOcc = 0;
                        // 贷方
                        let creditOcc = 0;
                        // 期末
                        let balance = 0;
                        // 期初
                        let remain = 0;
                        // 借方调整
                        let debitAdjust = 0;
                        // 贷方调整
                        let creditAdjust = 0;
                        // 骑摩调整
                        let balanceAdjust = 0;
                        // 期初调整
                        let remainAdjust = 0;
                        // direction    0- 借   1- 贷
                        let rawdata = res.data[0][1];
                        rawdata.map((item) => {
                            item.__uid = JSON.stringify(item);
                            if (item.children.length > 0) {
                                item.children.map((i) => {
                                    for (let key in i) {
                                        i[key] = i[key];
                                    }
                                    i.__uid = JSON.stringify(i);
                                });
                            } else {
                                item.children = null;
                            }
                            if (item.direction === 1) {
                                debitOcc = debitOcc + item.debitOcc;
                                creditOcc = creditOcc + item.creditOcc;
                                balance = balance + item.balance;
                                remain = remain + item.remain;
                                debitAdjust = debitAdjust + item.debitAdjust;
                                creditAdjust = creditAdjust + item.creditAdjust;
                                balanceAdjust =
                                    balanceAdjust + item.balanceAdjust;
                                remainAdjust = remainAdjust + item.remainAdjust;
                            } else {
                                debitOcc = debitOcc - item.debitOcc;
                                creditOcc = creditOcc - item.creditOcc;
                                balance = balance - item.balance;
                                remain = remain - item.remain;
                                debitAdjust = debitAdjust - item.debitAdjust;
                                creditAdjust = creditAdjust - item.creditAdjust;
                                balanceAdjust =
                                    balanceAdjust - item.balanceAdjust;
                                remainAdjust = remainAdjust - item.remainAdjust;
                            }
                        });
                        rawdata.push({
                            subjectName,
                            debitOcc,
                            creditOcc,
                            balance,
                            remain,
                            debitAdjust,
                            creditAdjust,
                            balanceAdjust,
                            remainAdjust,
                            debitOccTotal:
                                debitAdjust != 0
                                    ? debitOcc + debitAdjust
                                    : null,
                            creditOccTotal:
                                creditAdjust != 0
                                    ? creditOcc + creditAdjust
                                    : null,
                            balanceTotal:
                                balanceAdjust != 0
                                    ? balance + balanceAdjust
                                    : null,
                            remainTotal:
                                remainAdjust != 0
                                    ? remain + remainAdjust
                                    : null,
                            balanceColor: false,
                            remainColor: false,
                        });
                        this.data1 = rawdata;
                        this.addTip(rawdata);
                        this.accountCheck(rawdata);
                        subjectName = '贷合-借合';
                        let debitOcc1 = 0;
                        let creditOcc1 = 0;
                        let balance1 = 0;
                        let remain1 = 0;
                        let debitAdjust1 = 0;
                        let creditAdjust1 = 0;
                        let balanceAdjust1 = 0;
                        let remainAdjust1 = 0;
                        let list = [];
                        let rawdata2 = res.data[0][2];
                        rawdata2.map((item) => {
                            item.__uid = JSON.stringify(item);
                            let obj = {
                                __uid: item.__uid + '2',
                            };
                            for (let key in item) {
                                obj[key + '1'] = item[key];
                            }
                            list.push(obj);
                        });
                        rawdata2 = list;
                        rawdata2.map((item) => {
                            item.children = item.children1;
                            if (item.children.length > 0) {
                                item.children.map((i) => {
                                    for (let key in i) {
                                        i[key + '1'] = i[key];
                                    }
                                    i.__uid = JSON.stringify(i);
                                });
                            } else {
                                item.children = null;
                            }
                            if (item.direction1 != 1) {
                                debitOcc1 = debitOcc1 + item.debitOcc1;
                                creditOcc1 = creditOcc1 + item.creditOcc1;
                                balance1 = balance1 + item.balance1;
                                remain1 = remain1 + item.remain1;
                                debitAdjust1 = debitAdjust1 + item.debitAdjust1;
                                creditAdjust1 =
                                    creditAdjust1 + item.creditAdjust1;
                                balanceAdjust1 =
                                    balanceAdjust1 + item.balanceAdjust1;
                                remainAdjust1 =
                                    remainAdjust1 + item.remainAdjust1;
                            } else {
                                debitOcc1 = debitOcc1 - item.debitOcc1;
                                creditOcc1 = creditOcc1 - item.creditOcc1;
                                balance1 = balance1 - item.balance1;
                                remain1 = remain1 - item.remain1;
                                debitAdjust1 = debitAdjust1 - item.debitAdjust1;
                                creditAdjust1 =
                                    creditAdjust1 - item.creditAdjust1;
                                balanceAdjust1 =
                                    balanceAdjust1 - item.balanceAdjust1;
                                remainAdjust1 =
                                    remainAdjust1 - item.remainAdjust1;
                            }
                        });
                        rawdata2.push({
                            subjectName1: subjectName,
                            debitOcc1,
                            creditOcc1,
                            balance1,
                            remain1,
                            debitAdjust1,
                            creditAdjust1,
                            balanceAdjust1,
                            remainAdjust1,
                            debitOccTotal:
                                debitAdjust1 != 0
                                    ? debitOcc1 + debitAdjust1
                                    : null,
                            creditOccTotal:
                                creditAdjust1 != 0
                                    ? creditOcc1 + creditAdjust1
                                    : null,
                            balanceTotal:
                                balanceAdjust1 != 0
                                    ? balance1 + balanceAdjust1
                                    : null,
                            remainTotal:
                                remainAdjust1 != 0
                                    ? remain1 + remainAdjust1
                                    : null,
                            balanceColor: false,
                            remainColor: false,
                        });
                        this.addTip(rawdata2);
                        this.accountCheck(rawdata2);
                        this.data2 = rawdata2;
                        let leftObj = this.data1[this.data1.length - 1];
                        let rightObj = this.data2[this.data2.length - 1];
                        if (leftObj.remain !== rightObj.remain1) {
                            leftObj.remainColor = true;
                            rightObj.remainColor = true;
                        }
                        if (leftObj.balance !== rightObj.balance1) {
                            leftObj.balanceColor = true;
                            rightObj.balanceColor = true;
                        }
                        this.relatedList = res.data[0][3];
                        this.confirmCompletionBut = res.data[0][4];
                        // console.log(
                        //     this.data1[this.data1.length - 1],
                        //     this.data2[this.data2.length - 1]
                        // );
                        this.$forceUpdate();
                    } else {
                        this.data2 = [];
                        this.data1 = [];
                    }
                })
                .catch((err) => {
                    this.loading = false;
                    this.$message.error(err);
                });
        },
        init() {
            if (!this.item.mergeCustomerId) {
                return this.$message.error('请选择关联方');
            }
            if (this.activeKey == '1') {
                let par = {
                    param1: this.item.uscc,
                    param2: this.item.mergeCustomerId,
                    param3: this.item.mergeProjectId,
                    lockCustomerId: this.userinfo.curCustomerId,
                    lockProjectId: this.userinfo.preProjectId,
                    customerId: this.userinfo.mergeCustomerId,
                    projectId: this.userinfo.mergeProjectId,
                    menuID: this.userinfo.menuID,
                    lockYyyy:this.userinfo.curAccYear,
                };
                const that = this;
                this.$confirm({
                    title: '提示',
                    content: '是否要初始化？',
                    onOk() {
                        that.loading = true;
                        that.$sacpApis
                            .initMerchantCheckAcc(par)
                            .then((res) => {
                                that.loading = false;
                                if (res.success) {
                                    that.getTableList(that.item);
                                    that.$message.success('初始化完成');
                                } else {
                                    that.$message.error(
                                        res.resultInfo.statusText
                                    );
                                }
                            })
                            .catch((err) => {
                                that.$message.error(err);
                                that.loading = false;
                            });
                    },
                    onCancel() {},
                    class: 'test',
                });
            } else if (this.activeKey == '2') {
                let par = {
                    param1: this.item.uscc,
                    param2: this.item.mergeCustomerId,
                    param3: this.item.mergeProjectId,
                    lockCustomerId: this.userinfo.curCustomerId,
                    lockProjectId: this.userinfo.preProjectId,
                    customerId: this.userinfo.mergeCustomerId,
                    projectId: this.userinfo.mergeProjectId,
                    menuID: this.userinfo.menuID,
                    lockYyyy:this.userinfo.curAccYear,
                };
                const that = this;
                this.$confirm({
                    title: '提示',
                    content:
                        '初始化会删除当前数据并重新获取,但保留关联关系,是否确认?',
                    onOk() {
                        that.loading = true;
                        that.$sacpApis
                            .initMerchantCheck(par)
                            .then((res) => {
                                that.loading = false;
                                if (res.success) {
                                    that.getTableList(that.item);
                                    that.$message.success('初始化完成');
                                } else {
                                    that.$message.error(
                                        res.resultInfo.statusText
                                    );
                                }
                            })
                            .catch((err) => {
                                that.$message.error(err);
                                that.loading = false;
                            });
                    },
                    onCancel() {},
                    class: 'test',
                });
            }
        },
        save() {
            const { preProjectId, curCustomerId } = this.userinfo;
            if (this.activeKey === '1') {
                if (this.data1.length < 1 || this.data2.length < 1) {
                    return this.$message.error('数据为空，不能保存');
                }
                let data1 = [];
                let data2 = [];
                this.data1.map((item) => {
                    if (!item.children) {
                        item.children = [];
                    }
                    data1.push(item);
                });
                this.data2.map((item) => {
                    if (!item.children) {
                        item.children = [];
                    }
                    data2.push(item);
                });
                let par = {
                    param1: JSON.stringify(data1),
                    param2: JSON.stringify(data2),
                    lockCustomerId: curCustomerId,
                    lockProjectId: preProjectId,
                    customerId: this.userinfo.mergeCustomerId,
                    projectId: this.userinfo.mergeProjectId,
                    menuID: this.userinfo.menuID,
                    lockYyyy:this.userinfo.curAccYear,
                };
                this.$sacpApis
                    .saveMerchantCheckAccList(par)
                    .then((res) => {
                        if (res.success) {
                            this.$message.success('保存成功');
                            this.getTableList();
                        } else {
                            this.$message.error(res.resultInfo.statusText);
                        }
                    })
                    .catch((err) => {
                        this.$message.error(err);
                    });
            } else if (this.activeKey === '2') {
                const data0 = this.tableData[0].data;
                const data1 = this.tableData[1].data;
                if (data0.length < 1 || data1.length < 1) {
                    return this.$message.error('关联方对账数据为空，不能保存');
                }
                let par = {
                    param1: JSON.stringify(data0),
                    param2: JSON.stringify(data1),
                    lockCustomerId: curCustomerId,
                    lockProjectId: preProjectId,
                    customerId: this.userinfo.mergeCustomerId,
                    projectId: this.userinfo.mergeProjectId,
                    menuID: this.userinfo.menuID,
                    lockYyyy:this.userinfo.curAccYear,
                };
                this.$sacpApis
                    .saveMerchantCheckList(par)
                    .then((res) => {
                        if (res.success) {
                            this.$message.success('保存成功');
                            this.getTableList();
                        } else {
                            this.$message.error(res.resultInfo.statusText);
                        }
                    })
                    .catch((err) => {
                        this.$message.error(err);
                    });
            }
        },
        downloadMerchantCheck() {
            const { uscc, mergeCustomerId, mergeProjectId } = this.item;
            const { left, right, leftDate, rightDate } = this.sortOrder1;
            var relation;
            switch (this.sortOrder.left) {
                case '1':
                    relation = 1;
                    break;
                case '2':
                    relation = 0;
                    break;
                default:
                    relation = '';
                    break;
            }

            const param = {
                param1: uscc,
                param2: mergeCustomerId,
                param3: mergeProjectId,
                param4: relation,
                param5: left,
                param6: right,
                param7: leftDate,
                param8: rightDate,
                lockCustomerId: this.userinfo.curCustomerId,
                lockProjectId: this.userinfo.preProjectId,
                menuID: this.userinfo.menuID,
                lockYyyy:this.userinfo.curAccYear,
            };
            this.$sacpApis
                .downloadMerchantCheck(param, '关联方对账明细.xlsx')
                .then((res) => {
                    if (res.success) {
                        this.$message.success('导出成功!');
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                });
        },
        clear(val) {
            this.number = val;
        },
        openWind(val, record) {
            let insideTransactionParam = {
                subjectId: record.subjectId,
                relatedVoucher: record.relatedVoucher,
                oppositeSubjectId: record.oppositeSubjectId,
                relatedList: this.relatedList,
                showType: '1',
            };
            if (val && val.indexOf(',') > -1) {
                insideTransactionParam.showType = '1';
            } else {
                insideTransactionParam.showType = '0';
            }
            localStorage.setItem(
                'insideTransactionParam',
                JSON.stringify(insideTransactionParam)
            );
            window.open('/Faith/bdologin.do?m=gotoDesktop&menuId=50010017');
        },
        linkSty(val, record) {
            // console.log(record)
            if (val) {
                return 'aLink';
            } else {
                return '';
            }
        },
        unequal(text, record) {
            // console.log('record', record);
            let total = 0;
            if (record.children && record.children.length) {
                for (var i = record.children.length - 1; i >= 0; i--) {
                    total += XEUtils.add(record.children[i].debitOcc1, 0);
                }

                if (total != text) {
                    this.debit = true;
                    return;
                }
            }
        },
        queryObject(arr, type) {
            // console.log('arr', objectMessage);
            if (arr && arr.length) {
                arr.forEach((x) => {
                    if (
                        x.oppositeSubjectName &&
                        x.oppositeSubjectName.indexOf(',') > -1
                    ) {
                        if (type === 1) {
                            this.objectMessage.push(x.subjectName);
                        } else {
                            this.objectAnalysisMessage.push(x.subjectName);
                        }
                    }
                    this.queryObject(x.children);
                });
            }
        },
        rowClassName(record, index) {
            if (
                typeof record.oppositeSubjectName1 == 'string' &&
                record.oppositeSubjectName1.indexOf(',') > -1
            ) {
                return 'alertColor';
            } else {
                return '';
            }
        },
        addTip(data, parent) {
            if (data && data.length) {
                data.forEach((x) => {
                    if (
                        x.oppositeSubjectName &&
                        x.oppositeSubjectName.indexOf(',') > -1
                    ) {
                        parent.tip = true;
                    } else {
                        this.addTip(x.children, x);
                    }
                });
            }
            // this.data1 = data;
        },
        accountCheck(data) {
            if (data && data.length) {
                data.forEach((x) => {
                    if (x.children) {
                        x.children.forEach((s) => {
                            s.isChildren = true;
                        });
                    } else {
                        this.accountCheck(x.children, x);
                    }
                });
            }
        },
        confirmCompletionButFun(type) {
            if (this.isOneselfJump) {
                if (this.item.checkComplete == '0') {
                    return type == 1 ? false : true;
                } else {
                    return type == 0 ? false : true;
                }
            } else {
                if (this.confirmCompletionBut == 1) {
                    if (this.item.checkComplete == '0') {
                        return type == 1 ? false : true;
                    } else {
                        return type == 0 ? false : true;
                    }
                } else {
                    return false;
                }
            }
        },
        sendCheckMessageConfirm() {
            this.$confirm({
                title: `提示`,
                content: '请确认是否要发送消息提醒对方完成关联方对账？',
                onOk: () => {
                    this.sendCheckMessage();
                },
                onCancel: () => {},
            });
        },
        // 企业微信提醒
        sendCheckMessage() {
            const param = {
                param1: this.item.mergeCustomerId,
                param2: this.item.mergeProjectId,
                param3: '请去往来关联方对账核对数据',
            };
            this.submitLoading = true;
            this.$sacpApis
                .sendCheckMessage(param)
                .then((res) => {
                    this.submitLoading = false;
                    if (res.success) {
                        this.$message.success('发送成功!');
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                });
        },
    },
    mounted() {
        if (this.$route.query.type === 'true') {
            this.showIf = false;
            this.isOneselfJump = false;
            this.userinfo = this.$route.query;
            sessionStorage.setItem(
                'userinfo_obj',
                JSON.stringify(this.userinfo)
            );
            this.$router.replace({
                name: 'associatedTransactionVoucherTable',
                query: {},
            });
        } else if (this.$route.query.type === 'false') {
            this.showIf = true;
            this.isOneselfJump = true;
            // 赋值
            const info = sessionUtils.findUserInfo();
            const nameArr = info.curProjectName.split('-');
            this.userinfo = {
                ...info,
                preProjectId: info.curProjectId,
                curProjectName: info.curProjectName,
                lockYyyy:  info.curAccYear,
            };
        } else {
            this.showIf = false;
            this.isOneselfJump = true;
            this.userinfo = JSON.parse(sessionStorage.getItem('userinfo_obj'));
        }
        this.showTable();
        window.onclick = this.hidInput;
        window.onresize = this.onReSize;
        // 计算器
        window.addEventListener('click', (e) => {
            if (this.$refs.counter && this.$refs.counter.icon) {
                if (!e.target.innerText) return;
                const icon = this.$refs.counter.icon;
                const text = e.target.innerText.trim().replaceAll(',', '');
                const star = text.indexOf('.');
                const end = text.lastIndexOf('.');
                if (
                    !isNaN(text * 1) &&
                    star === end &&
                    star > -1 &&
                    end > -1 &&
                    text.length - star === 3 &&
                    icon
                ) {
                    this.number = text * 1;
                }
            }
        });
        this.initTableResize();
    },
};
</script>
<style lang='scss'>
