}
.content {
    height: 100%;
    width: 100%;
    position: relative;
    .shrink-button {
        height: 30px;
        width: 22px;
        padding: 0 3px;
        position: absolute;
        top: 50%;
        transform: translate(-100%, -50%);
    }
    .ant-tabs {
        width: 100%;
        height: 100%;
        >>> .ant-tabs-content {
            width: 100%;
            height: calc(100% - 50px);
            .ant-tabs-tabpane {
                width: 100%;
                height: 100%;
                .table_1 {
                    width: 100%;
                    height: 100%;
                    display: flex;
                    // .ant-table-wrapper {
                    //     float: left;
                    //     width: 49%;
                    // }
                    .tab1 {
                        margin-right: 2%;
                    }
                }
            }
            .ant-tabs-tabpane-inactive {
                display: none;
            }
        }
    }
    .left {
        width: 14%;
        height: 100%;
        float: left;
        background: #fff;
        margin-right: 1%;
        box-shadow: 0 0 5px 2px #dcdfe6;
        padding: 10px;
        overflow-y: auto;
    }
    .right {
        width: 85%;
        height: 100%;
        position: relative;
        float: left;
        .ucheckManName {
            position: absolute;
            left: 200px;
            width: 100%;
            max-width: calc(100% - 500px);
            top: 12px;
            z-index: 90;
        }
        .bdo__gridmocktable {
            width: 50% !important;
            height: 100% !important;
            overflow: auto;
            display: inline-block;
            .bdo__gridmocktable--block {
                width: 100%;
                .bdo__gridmocktable--block-table {
                    width: 100%;
                }
            }
        }
        .box-right {
            width: 49%;
            height: 100%;
            float: right;
            position: relative;
        }
        .box-left {
            height: 100%;
            width: 49%;
            float: left;
        }
        .right_table {
            height: calc(100% - 84px);
            width: 100%;
            overflow-y: auto;
        }
        .left_table {
            height: calc(100% - 84px);
            width: 100%;
            overflow-y: auto;
        }
        table {
            width: 100%;
            table-layout: fixed;
            word-break: break-all;
            .trClass {
                background: #ffedef !important;
            }
            th {
                padding: 10px;
                text-align: center;
            }
            td {
                padding: 6px;
                overflow: hidden; /*超出部分隐藏*/
                white-space: nowrap; /*不换行*/
                text-overflow: ellipsis; /*超出部分省略号显示*/
                font-size: 10px;
                border: 1px solid #dcdfe6;
            }
            .relationClass {
                background: #e6f7dc;
            }
        }
    }
}

.bdo__gridmocktable--column.border {
    border-right: 1px solid #dcdfe6;
    padding: 7px;
}
.drag-item {
    list-style: none;
    line-height: 24px;
    padding: 0 16px;
    cursor: move;
    &:hover {
        background-color: #f2f6fc;
    }
}
.dataPool {
    width: 30%;
    overflow-x: auto;
    margin-left: 30px;
    table {
        width: max-content;
    }
}
.ofh {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.colBule {
    color: blue;
}
.colLime {
    color: green;
}

.alertColor {
    background-color: chocolate;
}
.resize-table-th {
  position: relative;
}
.table-draggable-handle {
    position: absolute;
  transform: none !important;
  position: absolute;
  height: 100% !important;
  left: auto !important;
  right: -5px;
  cursor: col-resize;
  touch-action: none;
  border: none;
}
</style>



