<template>
    <div class="page">
        <a-pagination
            show-size-changer
            v-model="current"
            :page-size-options="pageSizeOptions"
            :default-current="1"
            :page-size="pageSize"
            :total="currentData.length"
            @showSizeChange="onShowSizeChange"
            @change="pageChang"
        />
    </div>
</template>

<script>
export default {
    props: {
        data: Array,
        idx: Number,
        currentData: Array,
        dataListCopy: Array,
    },
    data() {
        return {
            dataList: this.data,
            pageSize: 10,
            current: 1,
            pageSizeOptions: ['10', '20', '30', '40', '50'],
        };
    },
    watch: {
        pageSize(val, oldVal) {
            if (val) {
                this.current = 1;
            }
        },
    },
    methods: {
        onShowSizeChange(current, pageSize) {
            // console.log(current, pageSize);
            this.pageSize = pageSize;
            const pageData = this.dataListCopy[this.idx].slice(0, 1 * pageSize);
            this.$emit('pageData', { pageData, idx: this.idx });
        },
        pageChang(page, pageSize) {
            // console.log(page, pageSize);
            const pageData = this.dataListCopy[this.idx].slice(
                (page - 1) * pageSize,
                page * pageSize
            );
            this.$emit('pageData', { pageData, idx: this.idx });
        },
    },
    mounted() {
        this.dataList.forEach((x) => {
            x.splice(this.pageSize);
        });
        this.$emit('initData', this.dataList);
    },
};
</script>

<style>
</style>


