<template>
    <div>
        <a-modal
            title="自定义交易事项明细列表"
            :visible="visible"
            :confirm-loading="confirmLoading"
            width="55%"
            @ok="handleOk"
            @cancel="handleCancel"
        >
            <a-form-model
                :model="form"
                :rules="{
                    classifyCode: [
                        {
                            required: true,
                            message: '请选择自定义交易事项',
                            trigger: 'blur',
                        },
                    ],
                }"
                ref="form"
                :label-col="{ span: 4 }"
                :wrapper-col="{ span: 20 }"
            >
                <a-form-model-item label="自定义交易事项" prop="classifyCode">
                    <a-select v-model="form.classifyCode" mode="multiple">
                        <a-select-option
                            v-for="item in optionList"
                            :key="item.classifyCode"
                        >
                            {{ item.classifyName }}
                        </a-select-option>
                    </a-select>
                </a-form-model-item>
            </a-form-model>
        </a-modal>
    </div>
</template>

<script>
import sessionUtils from '@data/session/sessionUtils.js';
// import  {mixin} from '@pages/insideTransactionSummary/mixins/drageTable.js';
import { mixin } from './mixins/drageTable';
export default {
    name: 'setUpDialog',
    mixins: [mixin],
    inject: ['updateTreeData'],
    data() {
        return {
            treeData: [],
            visible: false,
            confirmLoading: false,
            form: {
                classifyCode: [],
            },
            userinfo: sessionUtils.findUserInfo(),
            optionList: [],
        };
    },
    methods: {
        show() {
            this.form.classifyCode = [];
            this.visible = true;
            let par = {
                sqlId: 'DG40039',
                param1: this.userinfo.curCustomerId,
                param2: this.userinfo.curProjectId,
                param4: 4,
            };
            this.$sacpApis
                .generalQuery(par)
                .then((res) => {
                    if (res.success) {
                        this.optionList = res.data;
                        this.optionList.map((item) => {
                            if (item.isSelect === '1') {
                                this.form.classifyCode.push(item.classifyCode);
                            }
                        });
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                });
        },
        handleOk() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    this.submitForm();
                }
            });
        },
        submitForm() {
            let par = { param1: this.form.classifyCode.join(','), param3: 1 };
            const that = this;
            this.$confirm({
                title: '提示',
                content:
                    '修改选择的自定义事项会清空已保存的自定义数据，确认修改？',
                okText: '确 认',
                cancelText: '取 消',
                onOk() {
                    that.$sacpApis
                        .setMerchantUserClassifyCustomize(par)
                        .then((res) => {
                            if (res.success) {
                                that.$message.success('保存成功');
                                that.$emit('getTableList');
                                that.queryTradeTree1();
                                that.handleCancel();
                                // let m = this;
                                // console.log(m);
                                // that.queryTradeTree();
                                //   let refreshTreeStatus = this.$store.state.refreshTreeStatus;
                                // this.$store.commit(
                                //'changeRerefreshTreeStatus',
                                // !refreshTreeStatus
                                // );
                            } else {
                                that.$message.error(res.resultInfo.statusText);
                            }
                        })
                        .catch((err) => {
                            that.$message.error(err);
                        });   
                },
                onCancel() {},
            });
        },
        handleCancel(e) {
            this.visible = false;
        },
        queryTradeTree1(){
            // console.log("sdfdsfdsf");
            this.queryTradeTree();
        },
        onTreeDataUpdate(data) {
            if (typeof this.updateTreeData === 'function') {
                this.updateTreeData(data);
            }
        }
    }
   
};
</script>

<style lang="scss" scoped>
.button {
    position: absolute;
    right: 25px;
    top: 82px;
}
</style>


