<template>
    <div>
        <a-modal
            :footer="null"
            :width="1400"
            v-model="visible"
            :title="`凭证详细  ${vchDate} ${oldVoucherId}`"
            @ok="handleOk"
        >
            <a-table
                :scroll="{ y: 300 }"
                :pagination="paginations"
                :columns="columns"
                :rowClassName="rowClassName"
                :data-source="data"
                :loading="loading"
                :components='components'
            >
                <div slot="summary" slot-scope="text, record">
                    <a-tag v-if="record.type === '1'" color="green">辅</a-tag>
                    {{ record.summary }}
                </div>
                <div
                    slot="debitOcc"
                    style="text-align: right"
                    slot-scope="text, record"
                >
                    {{ XEUtils.commafy(record.debitOcc, { digits: 2 }) }}
                </div>
                <div
                    slot="creditOcc"
                    style="text-align: right"
                    slot-scope="text, record"
                >
                    {{ XEUtils.commafy(record.creditOcc, { digits: 2 }) }}
                </div>
            </a-table>
        </a-modal>
    </div>
</template>
<script>
import XEUtils from 'xe-utils';
export default {
    data() {
        this.components = {
        header: {
        cell: (h, props, children) => {
          const { key, ...restProps } = props
        //   console.log('ResizeableTitle', key)
        console.log(this.columns);
          const col = this.columns.find(col => {
            const k = col.dataIndex || col.key
            return k === key
          })
 
          if (!col || !col.width) {
            return h('th', { ...restProps }, [...children])
          }
 
          const dragProps = {
            key: col.dataIndex || col.key,
            class: 'table-draggable-handle',
            attrs: {
              w: 10,
              x: col.width,
              z: 1,
              axis: 'x',
              draggable: true,
              resizable: false
            },
            on: {
              dragging: (x, y) => {
                col.width = Math.max(x, 1)
              }
            }
          }
          const drag = h('vue-draggable-resizable', { ...dragProps })
          return h('th', { ...restProps, class: 'resize-table-th' }, [...children, drag])
        }
      }
    };
        this.XEUtils = XEUtils;
        return {
            visible: false,
            columns: [
                {
                    title: '摘要',
                    dataIndex: 'summary',
                    rowKey: 'summary',
                    scopedSlots: { customRender: 'summary' },
                },
                {
                    title: '科目编号',
                    dataIndex: 'subjectId',
                    rowKey: 'subjectId',
                },
                {
                    title: '科目名称',
                    dataIndex: 'subjectFullName',
                    rowKey: 'subjectFullName',
                },
                // {
                //     title: '凭证日期',
                //     dataIndex: 'vchDate',
                //     rowKey: 'vchDate',
                // },
                // {
                //     title: '凭证号',
                //     dataIndex: 'oldVoucherId',
                //     rowKey: 'oldVoucherId',
                // },
                {
                    title: '借方金额 ',
                    dataIndex: 'debitOcc',
                    rowKey: 'debitOcc',
                    width: 200,
                    scopedSlots: { customRender: 'debitOcc' },
                },
                {
                    title: '贷方金额',
                    dataIndex: 'creditOcc',
                    width: 200,
                    rowKey: 'creditOcc',
                    scopedSlots: { customRender: 'creditOcc' },
                },
            ],
            data: [],
            oldVoucherId: '',
            vchDate: '',
            serail: '',
            flag: false,
            curRow: {},
            userinfo: {},
            loading: {
                size: 'large',
                spinning: false,
                tip: '加载中...',
            },
            start: 0,
            limit: 10,
            paginations: false,
            pagination: {
                current: 1,
                pageSize: 10,
                showSizeChanger: true,
                pageSizeOptions: ['10', '20', '30', '40', '50'],
                showQuickJumper: false,
                size: 'small',
                total: 0,
                showTotal: () => `共${this.pagination.total}条`,
                change: true,
                onShowSizeChange: (current, pageSize) => {
                    this.start = 0;
                    this.limit = pageSize;
                    this.pagination.pageSize = pageSize;
                    this.pagination.current = 1;
                    this.queryMerchantCheckVoucherDetail();
                },
                onChange: (current, size) => {
                    //start 0 -10 -20 -30
                    this.start = (current - 1) * size;
                    this.limit = this.pagination.pageSize;
                    this.pagination.current = current;
                    this.queryMerchantCheckVoucherDetail();
                },
            },
        };
    },
    methods: {
        rowClassName(record, index) {
            if (this.flag) {
                if (record.serail == this.serail) {
                    return 'bgr';
                } else if (record.type === '1') {
                    return 'bgr2';
                }
            } else {
                if (record.autoId == this.serail) {
                    return 'bgr';
                } else if (record.type === '1') {
                    return 'bgr2';
                }
            }
        },
        showModal(row) {
            this.visible = true;
            this.loading.spinning = true;
            this.paginations = false;
            this.serail = row.serail;
            if (row.vchDate) {
                this.oldVoucherId = `凭证号:${row.typeId + row.oldVoucherId}`;
                this.vchDate = `凭证日期:${row.vchDate}`;
                this.flag = true;
            } else {
                this.oldVoucherId = `索引号:${row.oldVoucherId}`;
                this.vchDate = `调整日期:${row.adjustDate}`;
                this.flag = false;
            }

            let par = {
                sqlId: 'FINWL000009',
                lockCustomerId: row.customerId,
                lockYyyy: row.curAccYear,
                lockProjectId: row.projectId,
                param1: row.vchDate,
                param2: row.oldVoucherId,
                param3: row.typeId,
            };
            this.$sacpApis
                .generalQueryJson(par)
                .then((res) => {
                    if (res.success) {
                        this.loading.spinning = false;
                        this.data = res.data;
                    } else {
                        this.data=[];
                        // this.$router.go(0);
                        this.$message.error('财务数据已删除，请重新导入后再查看');
                        
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.loading.spinning = false;
                });
        },
        queryMerchantCheckVoucherDetail(row, userinfo) {
            this.visible = true;
            this.loading.spinning = true;
            this.paginations = this.pagination;
            if (row) {
                this.curRow = row;
            }
            if (userinfo) {
                this.userinfo = userinfo;
            }
            this.serail = this.curRow.serail;
            if (this.curRow.vchDate) {
                this.flag = true;
            } else {
                this.flag = false;
            }
            this.vchDate = `科目:${this.curRow.subjectName};   `;
            this.oldVoucherId = `对方科目:${this.curRow.oppositeSubjectName};`;

            let par = {
                lockCustomerId: this.curRow.customerId,
                lockYyyy: this.userinfo.curAccYear,
                param1: this.curRow.relatedVoucher,
                limit: this.limit,
                start: this.start,
            };
            this.$sacpApis
                .queryMerchantCheckVoucherDetail(par)
                .then((res) => {
                    if (res.success) {
                        this.loading.spinning = false;
                        this.data = res.data;
                        this.pagination.total = res.totalCount;
                    } else {
                        this.data=[];
                        // this.$router.go(0);
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.loading.spinning = false;
                });
        },
        handleOk(e) {
            console.log(e);
            this.visible = false;
        },
    },
};
</script>
<style  lang='scss'>
.bgr > td {
    background: #fdf5e6;
}
.bgr2 > td {
    background: #f6ffed;
}
.resize-table-th {
    position: relative;
}
.table-draggable-handle {
    position: absolute;
    transform: none !important;
    bottom: 0;
    height: 100% !important;
    left: auto !important;
    right: -5px;
    cursor: col-resize;
    touch-action: none;
    border: none;
}
</style>


