<template>
    <div>
        <div class="reset">
            <a-popconfirm
                :title="ModalText"
                ok-text="重置"
                cancel-text="取消"
                @confirm="handleOk"
                @cancel="handleCancel"
            >
                <!-- <a-button type="primary"> 重置 </a-button> -->
            </a-popconfirm>
        </div>
        <div>
            <a-spin :spinning="spinning" :tip="tip">
                <grid-mock-table
                    border
                    v-model="tableData"
                    @data-drop="onDataDrop"
                    @deleteData="deleteData"
                    :rowDragable="false"
                    :colDragable="false"
                    :rowDragableState="false"
                >
                    <template
                        :slot="`col-${edit.prop}`"
                        slot-scope="{
                            row,
                            col,
                            curCol,
                            colIndex,
                            rowIndex,
                            record,
                            recordCol,
                            cellIndex,
                            columns,
                        }"
                        v-for="(edit, tx) in editCell"
                    >
                        <div :key="tx" class="slot-div">
                            <!-- 金额 -->
                            <div
                                v-if="edit.pageFieldType === '1'"
                                class="slot-div"
                                @click="
                                    switchState(
                                        $event,
                                        row,
                                        curCol,
                                        cellIndex,
                                        recordCol
                                    )
                                "
                            >
                                <a-input-number
                                    v-if="
                                        row[
                                            `${row.classifyCode}${curCol}Isedit`
                                        ] && row.uscc
                                    "
                                    v-model="row[curCol]"
                                    :default-value="0"
                                    :precision="2"
                                    :max="99999999999999999999.99"
                                    :formatter="formatter"
                                    :parser="parser"
                                    @change="onChange($event, row, col, curCol)"
                                />
                                <div
                                    class="slot-div"
                                    style="text-align: right"
                                    v-else
                                >
                                    {{
                                        row[curCol]
                                            | capitalize(columns.pageFieldType)
                                    }}
                                    <a-button
                                        v-if="
                                            interestRateButStatus()[
                                                cellIndex
                                            ] &&
                                            !disabled(
                                                record,
                                                row,
                                                recordCol,
                                                cellIndex
                                            )
                                        "
                                        type="primary"
                                        icon="sync"
                                        @click.stop="
                                            interestRateBut(row[curCol], curCol)
                                        "
                                    />
                                </div>
                            </div>
                            <!-- 文本 -->
                            <div
                                v-if="edit.pageFieldType === '2'"
                                class="slot-div"
                                @click="
                                    switchState(
                                        $event,
                                        row,
                                        curCol,
                                        cellIndex,
                                        recordCol
                                    )
                                "
                            >
                                <a-input
                                    v-if="
                                        row[
                                            `${row.classifyCode}${curCol}Isedit`
                                        ] && row.uscc
                                    "
                                    v-model="row[curCol]"
                                    @change="onChange($event, row, col, curCol)"
                                />
                                <div
                                    class="slot-div"
                                    :title="row[curCol]"
                                    v-else
                                >
                                    {{ row[curCol] }}
                                    <a-button
                                        v-if="
                                            interestRateButStatus()[
                                                cellIndex
                                            ] &&
                                            !disabled(
                                                record,
                                                row,
                                                recordCol,
                                                cellIndex
                                            )
                                        "
                                        type="primary"
                                        icon="sync"
                                        @click.stop="
                                            interestRateBut(row[curCol], curCol)
                                        "
                                    />
                                </div>
                            </div>
                            <a-select
                                v-else-if="
                                    edit.pageFieldType === '3' && row.uscc
                                "
                                v-model="row[curCol]"
                                show-search
                                :default-value="col"
                                placeholder="请选择"
                                option-filter-prop="children"
                                style="width: 200px"
                                :filter-option="filterOption"
                                :disabled="
                                    disabled(record, row, recordCol, cellIndex)
                                "
                                @focus="handleFocus"
                                @blur="handleBlur"
                                @change="
                                    handleChange(
                                        $event,
                                        row,
                                        curCol,
                                        cellIndex,
                                        rowIndex
                                    )
                                "
                            >
                                <a-select-option
                                    :value="item.value"
                                    v-for="(item, idx) in selectData(columns)"
                                    :key="idx"
                                    >{{ item.label }}</a-select-option
                                >
                            </a-select>

                            <a-date-picker
                                style="width: 130px"
                                v-else-if="
                                    edit.pageFieldType === '4' && row.uscc
                                "
                                v-model="row[curCol]"
                                value-format="YYYY-MM-DD"
                                :disabled="
                                    disabled(record, row, recordCol, cellIndex)
                                "
                                :default-value="
                                    col ? moment(col, 'YYYY-MM-DD') : ''
                                "
                                @change="onChangeTime"
                            />

                            <a-month-picker
                                style="width: 130px"
                                v-else-if="
                                    edit.pageFieldType === '5' && row.uscc
                                "
                                v-model="row[curCol]"
                                value-format="YYYY-MM-01"
                                :disabled="
                                    disabled(record, row, recordCol, cellIndex)
                                "
                                :default-value="
                                    col ? moment(col, 'YYYY-MM-DD') : ''
                                "
                                @change="onChangeTime"
                            />

                            <a-tree-select
                                v-else-if="
                                    edit.pageFieldType === '6' && row.uscc
                                "
                                v-model="row[curCol]"
                                show-search
                                style="width: 100%"
                                :disabled="
                                    disabled(record, row, recordCol, cellIndex)
                                "
                                :dropdown-style="{
                                    maxHeight: '400px',
                                    overflow: 'auto',
                                }"
                                placeholder="请选择"
                                allow-clear
                                tree-default-expand-all
                                :tree-data="selectTreeData(curCol)"
                                key="key"
                                :replaceFields="{ value: 'subjectId' }"
                                :filterTreeNode="fillterFUN"
                                @change="
                                    (selectedKeys, e) =>
                                        selected(selectedKeys, e, row, curCol)
                                "
                            >
                            </a-tree-select>
                            <!-- 非金额数字类型 -->
                            <div
                                v-if="edit.pageFieldType === '8'"
                                class="slot-div"
                                @click="
                                    switchState(
                                        $event,
                                        row,
                                        curCol,
                                        cellIndex,
                                        recordCol
                                    )
                                "
                            >
                                <a-input-number
                                    v-if="
                                        row[
                                            `${row.classifyCode}${curCol}Isedit`
                                        ] && row.uscc
                                    "
                                    v-model="row[curCol]"
                                    :default-value="0"
                                    :precision="2"
                                    :max="99999999999999999999.99"
                                    :formatter="formatter"
                                    :parser="parser"
                                    @change="onChange($event, row, col, curCol)"
                                />
                                <div
                                    class="slot-div"
                                    style="text-align: right"
                                    v-else
                                >
                                    {{ row[curCol] }}
                                    <a-button
                                        v-if="
                                            interestRateButStatus()[
                                                cellIndex
                                            ] &&
                                            !disabled(
                                                record,
                                                row,
                                                recordCol,
                                                cellIndex
                                            )
                                        "
                                        type="primary"
                                        icon="sync"
                                        @click.stop="
                                            interestRateBut(row[curCol], curCol)
                                        "
                                    />
                                </div>
                            </div>
                        </div>
                    </template>
                    <template
                        slot="col-action"
                        slot-scope="{
                            row,
                            col,
                            curCol,
                            colIndex,
                            rowIndex,
                            record,
                            recordCol,
                            cellIndex,
                        }"
                    >
                        <div style="text-align: center">
                            <a-button
                                :disabled="row.isDelect === false"
                                type="danger"
                                @click="deleteInsideFromEntry(rowIndex)"
                                >删除</a-button
                            >
                        </div>
                    </template>
                </grid-mock-table>
            </a-spin>
        </div>
    </div>
</template>

<script>
/**
 * 说明：需要安装插件库 npm i -S vue-slicksort
 */
import GridMockTable, { datadrag } from '../../GridMockTable/GridMockTable.vue';
import sessionUtils from '@data/session/sessionUtils.js';
import moment from 'moment';
import { mixin } from './mixins/drageTable.js';
import { formatter } from '../../libs/utils/formatter';

export default {
    name: 'App',
    directives: { datadrag },
    components: { GridMockTable },
    props: {
        treeData: Object,
        dialogTitle: String,
        publicParam: Object,
        userinfo: Object,
    },
    mixins: [mixin],
    data() {
        return {
            tableData: [],
            dataPoolTableData: [],
            header: [],
            editCell: [],
            field: [],
            // userinfo: sessionUtils.findUserInfo(),
            transactionType: '0',
            treeExpandedKeys: [],
            ModalText: '重置数据池数据会清空当前所有保存的数据，是否重置？',
            visible: false,
            confirmLoading: false,
            storeTableData: {},
            uscc: '',
            spinning: false,
            tip: '加载中...',
        };
    },
    watch: {
        table: {
            handler(val, oldVal) {
                if (val && Object.keys(val).length) {
                    this.storeTableData = val;
                    this.tableDataHandler();
                }
            },
            deep: true,
            immediate: true,
        },
        tableData: {
            handler(val, oldVal) {
                if (val && Object.keys(val).length) {
                    this.tableSum(val);
                }
            },
            deep: true,
            immediate: true,
        },
    },
    filters: {
        capitalize: (value, type) => {
            if (type === '1') {
                if (value) {
                    return formatter.commafy(value * 1);
                } else {
                    return '';
                }
            } else if (value) {
                return value;
            } else {
                return '';
            }
        },
    },
    computed: {
        table() {
            return JSON.parse(JSON.stringify(this.$store.state.table));
        },
    },
    methods: {
        moment,
        tableInit(type, uscc) {
            this.transactionType = type;
            this.uscc = uscc;
        },
        tableDataHandler() {
            this.reset();
            let data = this.storeTableData;
            this.tableData = data.originalTableData;
            // this.tableData.shift()
            this.header = data.header;
            this.field = data.field;
            this.editCell = data.editCell;
            this.$nextTick(() => {
                this.generalQuery();
            });
        },
        /** 目前是用index传递的，最好可以的话换成唯一的字段 */
        // trgIndex目标列索引,trgRowIndex目标行索引，srcIndex数据池行索引
        onDataDrop(trgIndex, srcIndex, trgRowIndex) {
            // console.log('trgIndex', trgIndex, srcIndex);
            // debugger
            // 上下行拖拽时不执行下面操作,拖拽到表头区域也不执行下面操作
            if (srcIndex === '' || trgRowIndex === null || trgIndex === 0)
                return;
            const table = this.tableData[trgIndex];
            const dataPool = this.dataPoolTableData[srcIndex];
            const mergeCustomerNameData = this.tableData[0].data;
            const customerUscc = mergeCustomerNameData[trgRowIndex].uscc;
            const insideAmount = table.data[trgRowIndex].insideAmount;
            // 获取展示字段并进行赋值
            const children = table.columns[0].children;
            if (children) {
                children.forEach((x) => {
                    if (x.dataField) {
                        dataPool[x.prop] = dataPool[x.dataField];
                    }
                });
            }
            let items = this.dataPoolTableData.splice(srcIndex, 1);
            const { mergeCustomerName, mergeProjectId, uscc } = items[0];
            // 通过金额判断该行是否被拖拽过
            if (
                (!customerUscc && !insideAmount) ||
                (customerUscc && insideAmount)
            ) {
                mergeCustomerNameData.splice(trgRowIndex, 0, {
                    mergeCustomerName,
                    mergeProjectId,
                    uscc,
                });
                table.data.splice(
                    trgRowIndex,
                    0,
                    Object.assign(...items, this.field[trgIndex])
                );
            } else if (customerUscc) {
                Object.assign(
                    table.data[trgRowIndex],
                    Object.assign(...items, this.field[trgIndex])
                );
            }

            // 根据关联方列数据条数填充其他列数据
            this.tableData.forEach((x, idx) => {
                if (
                    idx != trgIndex &&
                    idx != 0 &&
                    x.data.length < mergeCustomerNameData.length
                ) {
                    // 判断是否是第一次拖拽
                    if (mergeCustomerNameData.length - x.data.length > 0) {
                        let num = x.data.length;
                        // x.data[num] = { ...this.field[idx] };
                        x.data.unshift({ ...this.field[idx] });
                    } else {
                        x.data = new Array(mergeCustomerNameData.length)
                            .fill(null)
                            .map((k) => ({ ...this.field[idx] }));
                    }
                }
            });
        },
        moveTotable(trgIndex, srcIndex, content) {
            const length = this.tableData[0].data.length - 1;
            this.onDataDrop(trgIndex + 1, srcIndex, length);
            this.hide();
        },
        deleteData(val, idx) {
            this.tableData[idx].columns[0].children.forEach((x) => {
                if (x.prop && x.dataField === 'insideAmount') {
                    val.insideAmount = val[x.prop];
                }
            });
            this.dataPoolTableData.push(val);
        },
        // 初始化数据池
        initTradeAccount(uscc) {
            this.$sacpApis
                .initTradeAccount({ param3: uscc.join(',') })
                .then((res) => {
                    if (res.success) {
                        this.$message.success('重置成功！');
                        this.reset();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                });
        },
        // 查询数据池
        generalQuery() {
            // debugger
            if (!this.userinfo) return;
            this.spinning = true;
            this.tip = '加载中...';
            const { curCustomerId, curProjectId } = this.userinfo;
            const table = this.storeTableData.originalTableData;
            // 重复点击财务数据获取交易表数据会重复,所以做一次深拷贝
            const initialTable = JSON.parse(JSON.stringify(table));
            this.dataPoolTableData = [];
            const param = {
                sqlId: 'DG40034',
                param1: curCustomerId,
                param2: curProjectId,
                param4: this.uscc,
                ...this.publicParam,
            };
            this.$sacpApis
                .generalQuery(param)
                .then((res) => {
                    if (res.success) {
                        // console.log('generalQuery', res);
                        if (!res.data) res.data = [];
                        this.dataPoolTableData = res.data;
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                    this.queryMerchantInsideList();
                })
                .catch((err) => {
                    this.$message.error(err);
                });
        },
        // 保存内部交易明细
        saveMerchantInsideDeatil() {
            this.tip = '保存中...';
            this.spinning = true;
            let saveData = [];
            let tableData = JSON.parse(JSON.stringify(this.tableData));
            const columnsChildren = tableData[1].columns[0].isCur;
            tableData[1].data.forEach((x) => {
                if (x.uscc) {
                    x.insideType = this.storeTableData.insideType;
                    x.classifyName = columnsChildren.title;
                    x.isCur = columnsChildren.isCur;
                    // 向后端传递节点名称并用'|'分隔id和name
                    this.editCell.map((l) => {
                        if (l.pageFieldType === '6') {
                            x[l.prop] = x[l.prop]
                                ? x[l.prop].replace('-', '|')
                                : '';
                        }
                    });
                    saveData.push(x);
                }
            });
            const param = {
                param1: JSON.stringify(saveData),
                param2: this.uscc,
                param3: this.storeTableData.insideType,
                ...this.publicParam,
            };
            this.$sacpApis
                .saveMerchantInsideDeatil(param)
                .then((res) => {
                    if (res.success) {
                        this.$message.success('保存成功！');
                        this.$emit('commitState', true);
                        this.queryTradeTree();
                        this.$store.state.table.queryMerchantInsideShowList();
                        this.$store.state.table.queryMerchantEffect();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                        this.$emit('commitState', false);
                    }
                })
                .catch((err) => {
                    return this.$message.error(err);
                })
                .finally(() => {
                    this.spinning = false;
                });
        },
        // 查询内部交易数据
        queryMerchantInsideList() {
            const param = {
                param3: this.uscc,
                param5: this.storeTableData.classifyCode,
                param4: this.storeTableData.insideType,
                ...this.publicParam,
            };
            this.$sacpApis
                .queryMerchantInsideList(param)
                .then((res) => {
                    if (res.success) {
                        if (res.data && res.data.length) {
                            const data = Object.values(res.data[0])[0];
                            const list = [];
                            let prop = '';
                            this.tableData[1].columns[0].children.forEach(
                                (x) => {
                                    if (x.dataField === 'insideAmount')
                                        prop = x.prop;
                                }
                            );
                            this.dataPoolTableData.forEach((x) => {
                                if (
                                    x.classifyCode ===
                                    Object.keys(res.data[0])[0]
                                ) {
                                    // x.bgc = true;
                                    x[prop] = x.insideAmount;
                                    x.isDelect = false;
                                    list.push(x);
                                }
                            });
                            const data1 = [...list, ...data];
                            const data0 = data1.map((x) => ({
                                mergeCustomerName: x.mergeCustomerName,
                                uscc: x.uscc,
                                mergeProjectId: x.mergeProjectId,
                            }));
                            // 向后端传递节点名称并用'|'分隔id和name(转换成id,回显)
                            if (data1) {
                                data1.forEach((x) => {
                                    this.editCell.forEach((l) => {
                                        if (
                                            l.pageFieldType === '6' &&
                                            x[l.prop]
                                        ) {
                                            x[l.prop] = x[l.prop]
                                                ? x[l.prop].replace('|', '-')
                                                : '';
                                        }
                                    });
                                });
                            }
                            this.tableData[1].data = data1;
                            this.tableData[0].data = data0;
                            this.tableData[2] = {
                                columns: [
                                    {
                                        title: '操作',
                                        prop: 'action',
                                        isEdit: '0',
                                        pageFieldType: '8',
                                        code: 'action',
                                    },
                                ],
                                data: new Array(data1.length)
                                    .fill({
                                        action: '',
                                    })
                                    .map((x, idx) => ({ ...x, ...data1[idx] })),
                            };
                        }
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.spinning = false;
                });
        },
        hide() {
            // console.log(111);
            this.visible = false;
        },
        onChange(val, row, col, curCol) {
            // console.log(val, row, col, curCol);
            // row[curCol] = val;
        },
        onChangeTime(date, dateString) {
            // console.log(date, dateString);
        },
        handleChange(option, row, col, colIndex, rowIndex) {
            // console.log(`selected`,option,row,col,colIndex,rowIndex);
            // this.tableData[colIndex].data[rowIndex][col]=option.key
        },
        handleBlur() {
            console.log('blur');
        },
        handleFocus() {
            console.log('focus');
        },
        filterOption(input, option) {
            return (
                option.componentOptions.children[0].text
                    .toLowerCase()
                    .indexOf(input.toLowerCase()) >= 0
            );
        },
        // 树选择
        selected(selectedKeys, e, row, curCol) {
            row[curCol] = e[0];
        },
        // 树选择搜索
        fillterFUN(searchVal, treeNode) {
            return treeNode.data.props.title.includes(searchVal);
        },
        //树选择数据
        selectTreeData(col) {
            for (let i = 0; i < this.editCell.length; i++) {
                const x = this.editCell[i];
                if (x.pageFieldType === '6' && col === x.prop) {
                    return this.treeData.purpose;
                }
            }
        },
        //选择数据
        selectData(columns) {
            switch (columns.dicCode) {
                case '固定资产购入方式':
                    return this.treeData.purchaseMethod;
                case '固定资产折旧方法':
                    return this.treeData.depreciation;
            }
        },
        handleOk(e) {
            setTimeout(() => {
                this.initTradeAccount(this.uscc);
            }, 0);
        },
        handleCancel(e) {},
        reset() {
            this.tableData = [];
            this.dataPoolTableData = [];
            this.header = [];
            this.editCell = [];
            this.field = [];
            // console.log('this.tableData',this.tableData)
        },
        switchState(e, row, curCol, cellIndex, recordCol) {
            // console.log(e, row, curCol);
            // 拼接字段区分每一个单元格状态
            if (recordCol[0].children[cellIndex].isEdit == '0') return;
            row[`${row.classifyCode}${curCol}Isedit`] = true;
            this.$forceUpdate();
            this.$nextTick(() => {
                if (e.target.tagName == 'DIV') {
                    const input = e.currentTarget.querySelector('input');
                    input.blur();
                    input.focus();
                    input.addEventListener('blur', () => {
                        row[`${row.classifyCode}${curCol}Isedit`] = false;
                        this.$forceUpdate();
                    });
                }
            });
        },
        formatter(value) {
            return `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',');
        },
        parser(value) {
            return value.replace(/\$\s?|(,*)/g, '');
        },
        disabled(record, row, recordCol, cellIndex) {
            // return false;
            if (
                record[record.length - 1] == row ||
                recordCol[0].children[cellIndex].isEdit == '0'
            ) {
                return true;
            } else {
                return false;
            }
        },
        deleteInsideFromEntry(rowIndex) {
            const {
                curProjectId,
                curCustomerId,
                curAccYear,
                mergeCustomerId,
                mergeProjectId,
            } = this.userinfo;
            const param = {
                param1: JSON.stringify([this.tableData[1].data[rowIndex]]),
                lockCustomerId: curCustomerId,
                lockProjectId: curProjectId,
                lockYyyy:curAccYear,
                
            };
            this.$confirm({
                title: '提示:',
                content: '是否删除该条数据',
                okText: '删除',
                okType: 'danger',
                cancelText: '取消',
                onOk: () => {
                    this.$sacpApis
                        .deleteInsideFromEntry(param)
                        .then((res) => {
                            if (res.success) {
                                this.$message.success('删除成功！');
                                this.generalQuery();
                                // this.$emit('commitState', true);
                                this.queryTradeTree();
                                this.$store.state.table.queryMerchantInsideShowList();
                                this.$store.state.table.queryMerchantEffect();
                            } else {
                                this.$message.error(res.resultInfo.statusText);
                            }
                        })
                        .catch((err) => {
                            this.$message.error(err);
                        });
                },
                onCancel: () => {
                    // console.log('Cancel');
                },
            });
        },
        interestRateButStatus() {
            const children = this.tableData[1].columns[0].children;
            return children.map((x, idx) => {
                if (x.title.search('毛利率') > -1) {
                    return true;
                } else {
                    return false;
                }
            });
        },
        interestRateBut(value, prop) {
            this.tableData[1].data.forEach((x) => {
                if (x.uscc) {
                    this.$set(x, prop, value);
                }
            });
        },
    },
    mounted() {
        // this.generalQuery();
    },
};
</script>

<style lang="scss" scoped>
/deep/.bdo__gridmocktable {
    .bdo__gridmocktable--container {
        & > div {
            &:first-child {
                display: none;
            }
            // tbody {
            //     tr {
            //         &:last-child {
            //             background-color: whitesmoke;
            //         }
            //     }
            // }
        }
    }
}

/deep/.ant-input-number {
    .ant-input-number-handler-wrap {
        display: none;
    }
}

.slot-div {
    height: 100%;
    .ant-input-number {
        width: 100%;
    }
}

.reset {
    text-align: right;
    margin-bottom: 10px;
    margin-top: -15px;
}

.drag-item {
    list-style: none;
    line-height: 24px;
    padding: 0 16px;
    cursor: move;
    &:hover {
        background-color: #f2f6fc;
    }
}

.bdo__gridmocktable {
    // width: 70% !important;
    overflow-x: auto;
    height: calc(100vh - 200px) !important;
}
</style>



