<template>
    <div>
        <!-- 弹窗 -->
        <a-modal
            :title="title"
            :visible="visible"
            v-if="visible"
            :confirm-loading="confirmLoading"
            :destroyOnClose="true"
            :closable="false"
            width="75%"
            okText="保存"
            @ok="handleOk"
            @cancel="handleCancel"
        >
            <!-- <template slot="footer"> -->
            <!-- <a-button type="primary" @click="handleCancel"> 取消 </a-button> -->
            <!-- <a-button type="primary" @click="saveMerchantInside">
                    财务数据获取
                </a-button> -->
            <!-- <a-button type="primary" @click="handleOk"> 保存 </a-button> -->
            <!-- </template> -->
            <test
                ref="test"
                :treeData="treeData"
                :title="dialogTitle"
                :userinfo="userinfo"
                :publicParam="publicParam"
                @commitState="commitState"
            ></test>
        </a-modal>
        <a-modal
            title="新增"
            :visible="visibleAdd"
            v-if="visibleAdd"
            :confirm-loading="confirmLoading"
            width="75%"
            :footer="null"
            @ok="handleOkAdd"
            @cancel="handleCancelAdd"
        >
            <a-spin tip="初始化中..." size="large" :spinning="resetLoading">
                <div class="button">
                    <div>
                        提示:内部交易科目对照，将获取的关联方财务数据根据内部交易科目分类对照
                    </div>
                    <div>
                        <a-popconfirm
                            :title="'该操作会初始化所有关联方并清空全部已保存数据，是否初始化？'"
                            ok-text="确定"
                            cancel-text="取消"
                            @confirm="reset"
                            @cancel="cancel"
                        >
                            <!-- <a-button type="primary">初始化</a-button> -->
                        </a-popconfirm>
                        <a-button
                            type="primary"
                            :disabled="objectSetBut"
                            @click="queryMerchantSubjectSet"
                            >交易分类汇总设置</a-button
                        >
                        <!-- <a-button
                            type="primary"
                            @click="queryMerchantInsideMid()"
                            >对应科目分析</a-button
                        > -->
                    </div>
                </div>
                <a-table
                    :columns="columns"
                    :data-source="data"
                    bordered
                    :scroll="{ x: false, y: 300 }"
                    :row-key="(row, idx) => `${idx}`"
                    :components='components'
                >
                    <template slot="operation" slot-scope="text, record">
                        <a-button
                            type="primary"
                            :disabled="editButState(record)"
                            @click="edit(record.uscc, record.mergeCustomerName)"
                        >
                            编辑
                        </a-button>
                        <!-- <a-button
                            type="primary"
                            @click="initTradeAccount(record.uscc)"
                        >
                            初始化</a-button
                        > -->
                        <!-- <a-button
                            type="primary"
                            @click="queryMerchantSubjectSet"
                        >
                            科目设置
                        </a-button> -->
                        <!-- <a-button
                            type="primary"
                            @click="queryMerchantInsideMid(record.uscc)"
                        >
                            对应科目分析
                        </a-button> -->
                        <a-button
                            type="primary"
                            :disabled="
                                butState ? butState : record.tradeComplete == 1
                            "
                            @click="dataDefend(record)"
                            >交易数据维护</a-button
                        >
                        <a-button
                            type="primary"
                            :disabled="
                                butState ? butState : record.tradeComplete == 1
                            "
                            @click="adjustEntryDialog(record)"
                        >
                            调整数据维护
                        </a-button>
                        <a-button
                            type="primary"
                            @click="saveMerchantTradeComplete(record)"
                            :disabled="buttonState(record)"
                            v-if="record.tradeComplete == 0"
                        >
                            确认完成
                        </a-button>
                        <a-button
                            v-else
                            type="primary"
                            :disabled="buttonState(record)"
                            @click="clickCancel(record)"
                        >
                            取消
                        </a-button>
                    </template>
                    <template slot="tradeTime" slot-scope="text, record">
                        <div>
                            {{ text ? moment(text).format('YYYY-MM-DD') : '' }}
                        </div>
                    </template>
                </a-table>
            </a-spin>
        </a-modal>
        <!-- 科目设置 -->
        <a-modal
            :visible="subjectVisible"
            v-if="subjectVisible"
            width="50%"
            :confirm-loading="subjectConfirmLoading"
            @ok="saveMerchantSubjectSetList"
            okText="保存"
            @cancel="subjectHandleCancel"
        >
            <template #title>
                <div
                    style="
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        margin-right: 30px;
                    "
                >
                    <span>科目设置</span>
                    <a-button @click="automaticComparison">
                        自动对照
                    </a-button>
                </div>
            </template>
            <a-table
                :columns="subjectSetColumns"
                :data-source="subjectSetData"
                row-key="autoId"
                :scroll="{ y: 300 }"
                bordered
                 :components='components'
            >
                <template slot="summaryLevel" slot-scope="text, record">
                    <a-input-number
                        style="width: 100%"
                        allowClear
                        :maxLength="1"
                        :max="9"
                        :min="1"
                        v-model="record.summaryLevel"
                        :parser="(value) => (/^\d+$/.test(value) ? value : '')"
                    ></a-input-number>
                </template>
                <template slot="classifyCode" slot-scope="text, record">
                    <a-select
                        v-model="record.classifyCode"
                        style="width: 100%"
                        size="large"
                        allowClear
                    >
                        <a-select-option
                            v-for="item in optionList"
                            :key="item.classifyCode"
                        >
                            {{ item.classifyName }}
                        </a-select-option>
                    </a-select>
                </template>
            </a-table>
        </a-modal>
        <!-- 对应科目分析 -->
        <a-modal
            title="对应科目分析"
            :visible="dataSplitVisible"
            :confirm-loading="dataSplitConfirmLoading"
            width="90%"
            @ok="saveMerchantInsideMid"
            okText="保存"
            @cancel="dataSplitHandleCancel"
            :footer="null"
        >
            <a-select
                default-value="拆分"
                style="width: 120px; margin-bottom: 20px"
                @change="IsSplit"
            >
                <a-select-option value="1"> 拆分 </a-select-option>
                <a-select-option value=""> 不拆分 </a-select-option>
            </a-select>
            <a-table
                :columns="dataSplitSetColumns"
                :data-source="dataSplitSetData"
                bordered
                :pagination="false"
                :scroll="{ y: 300 }"
                row-key="autoId"
                @change="page"
                 :components='components'
            >
                <template slot="insideAmount" slot-scope="text, record">
                    <editable-cell
                        :text="String(text)"
                        :splitState="String(splitState)"
                        :formatter="formatter"
                        @change="
                            onCellChange(record.autoId, 'insideAmount', $event)
                        "
                    />
                </template>
            </a-table>
        </a-modal>

        <!-- 交易数据维护 -->
        <a-modal
            :visible="dataDefendVisible"
            v-if="dataDefendVisible"
            :confirm-loading="dataDefendConfirmLoading"
            :destroyOnClose="true"
            width="95%"
            @ok=""
            okText="保存"
            @cancel="DataDefendCancel"
            :footer="null"
        >
            <template slot="title">
                交易数据维护 (关联方:{{ currRow.mergeCustomerName }};
                财务数据获取时间:{{
                    currRow.tradeTime
                        ? moment(currRow.tradeTime).format('YYYY-MM-DD')
                        : ''
                }}; 账套年份:{{ formInline.lockYyyy }};)
                <div style="float: right; margin-right: 30px; margin-top: -6px">
                    <a-button @click="queryEntryForTrade(true)">
                        <a-icon type="search" />
                        查询</a-button
                    >
                    <a-button
                        :disabled="
                            selectedRows && selectedRows.length ? false : true
                        "
                        @click="batchAddRow"
                    >
                        添加</a-button
                    >
                    <a-button
                        :disabled="
                            selectedRows && selectedRows.length ? false : true
                        "
                        @click="deleteInsideFromEntry"
                    >
                        删除</a-button
                    >
                </div>
            </template>
            <a-spin size="large" :spinning="dataDefendSpinning" tip="加载中...">
                <a-form-model
                    layout="inline"
                    :model="formInline"
                    ref="dataDefendForm"
                    style="margin-bottom: 20px"
                >
                    <!-- <a-form-model-item label="账套年份">
                    <a-date-picker
                        style="width: 100px"
                        v-model="formInline.lockYyyy"
                        :open="yearShow"
                        mode="year"
                        format="YYYY"
                        placeholder="账套年份"
                        disabled
                        @openChange="openChangeOne($event)"
                        @panelChange="panelChangeOne($event)"
                    />
                </a-form-model-item> -->
                    <a-form-model-item label="凭证日期">
                        <a-range-picker
                            :defaultValue="[
                                moment('2019-01-01'),
                                moment('2019-12-31'),
                            ]"
                            :defaultPickerValue="[
                                moment('2019-01-01'),
                                moment('2019-12-31'),
                            ]"
                            format="MM-DD"
                            style="width: 150px"
                            @change="timeChange"
                        />
                    </a-form-model-item>
                    <a-form-model-item label="科目" prop="subjectId">
                        <a-tree-select
                            v-model="formInline.subjectId"
                            show-search
                            style="width: 200px"
                            :dropdown-style="{
                                maxHeight: '400px',
                                overflow: 'auto',
                            }"
                            placeholder="选择科目"
                            allow-clear
                            tree-default-expand-all
                            option-filter-prop="children"
                            :filterTreeNode="fillterFUN"
                            :tree-data="treeData.purpose"
                            key="key"
                            :replaceFields="{ value: 'subjectId' }"
                            @change="selected"
                        >
                        </a-tree-select>
                    </a-form-model-item>
                    <a-form-model-item label="核算类型" prop="assItemId">
                        <a-tree-select
                            v-model="formInline.assItemId"
                            show-search
                            style="width: 200px"
                            :dropdown-style="{
                                maxHeight: '400px',
                                overflow: 'auto',
                            }"
                            placeholder="核算类型"
                            allow-clear
                            option-filter-prop="children"
                            :filterTreeNode="fillterFUN"
                            :tree-data="assitemIsBankTree"
                            key="key"
                            :replaceFields="{ value: 'assItemId' }"
                            :disabled="assItemDisabled"
                            :load-data="onLoadData"
                            @change="selectedSubject"
                            @treeExpand="treeExpand"
                        >
                        </a-tree-select>
                    </a-form-model-item>
                    <a-form-model-item label="字" prop="typeId"
                        ><a-input placeholder="字" v-model="formInline.typeId"
                    /></a-form-model-item>
                    <a-form-model-item label="号" prop="voucherId"
                        ><a-input
                            placeholder="号"
                            v-model="formInline.voucherId"
                    /></a-form-model-item>
                    <!-- <a-form-model-item label="摘要匹配">
                    <a-select
                        default-value="1"
                        style="width: 80px"
                        @change="abstractChange"
                    >
                        <a-select-option value="1"> 且 </a-select-option>
                        <a-select-option value="2"> 或 </a-select-option>
                        <a-select-option value="3">不含</a-select-option>
                    </a-select>
                </a-form-model-item> -->
                    <a-form-model-item label="摘要关键字" prop="summary">
                        <a-input placeholder="" v-model="formInline.summary" />
                    </a-form-model-item>
                    <a-form-model-item label="显示已添加数据" prop="isAdd">
                        <a-select
                            default-value="0"
                            style="width: 80px"
                            @change="isAddChange"
                        >
                            <a-select-option value="1"> 是 </a-select-option>
                            <a-select-option value="0"> 否 </a-select-option>
                            <!-- <a-select-option value=""> 全部 </a-select-option> -->
                        </a-select>
                    </a-form-model-item>
                    <a-form-model-item label="借贷方向" prop="direction">
                        <a-select
                            default-value=""
                            style="width: 80px"
                            allowClear
                            @change="directionChange"
                        >
                            <a-select-option value="1"> 借 </a-select-option>
                            <a-select-option value="-1"> 贷</a-select-option>
                            <!-- <a-select-option value="">全部 </a-select-option> -->
                        </a-select>
                    </a-form-model-item>
                </a-form-model>
                <a-table
                    :columns="dataDefendColumns"
                    :data-source="dataDefendData"
                    bordered
                    :scroll="{ y: 'calc(100vh - 340px)', x: 'scollXWidth' }"
                    :row-key="
                        (row, idx) => {
                            `${idx}`;
                        }
                    "
                     :components='components'
                    :footer="null"
                    :pagination="pagination"
                    :customRow="click"
                    :row-selection="{
                        selectedRowKeys,
                        onChange: onSelectChange,
                        getCheckboxProps,
                    }"
                    id="tableList"
                    ref="tableList"
                >
                    <!-- <template slot="direction" slot-scope="text, record">
                        <div>{{ record.direction === 1 ? '借' : '贷' }}</div>
                    </template> -->
                    <template slot="operation" slot-scope="text, record">
                        <!-- <a-button
                        type="primary"
                        :disabled="record.isAdd == 1"
                        @click="addRow(record)"
                        >添加</a-button
                    > -->

                        <a-popconfirm
                            title="确定要删除该条数据吗？"
                            ok-text="确 定"
                            cancel-text="取 消"
                            :disabled="!(record.isAdd == 1)"
                            @confirm="deleteTradeFromEntry(record)"
                        >
                            <a-button
                                type="primary"
                                :disabled="!(record.isAdd == 1)"
                                >删除</a-button
                            >
                        </a-popconfirm>
                        <a-button
                            style="margin-left: 10px"
                            type="primary"
                            @click="queryMerchantTradeVoucherDetail(record)"
                            >查看凭证</a-button
                        >
                    </template>
                    <template slot="footer" slot-scope="currentPageData">
                        <a-table
                            v-show="footerTotalData && footerTotalData.length"
                            id="totalTable"
                            ref="totalTable"
                            :bordered="false"
                            :pagination="false"
                            :columns="dataDefendColumns"
                            :dataSource="footerTotalData"
                            :row-selection="rowSelection"
                            :showHeader="false"
                            :scroll="{
                                y: 'calc(100vh - 340px)',
                                x: 'scollXWidth',
                            }"
                             :components='components'
                            row-key="id"
                        >
                            <template
                                slot="operation"
                                slot-scope="text, record"
                            >
                                <!-- <a-button
                        type="primary"
                        :disabled="record.isAdd == 1"
                        @click="addRow(record)"
                        >添加</a-button
                    > -->

                                <a-popconfirm
                                    title="确定要删除该条数据吗？"
                                    ok-text="确 定"
                                    cancel-text="取 消"
                                    :disabled="true"
                                    @confirm="deleteTradeFromEntry(record)"
                                >
                                    <a-button type="primary" :disabled="true"
                                        >删除</a-button
                                    >
                                </a-popconfirm>
                                <a-button
                                    style="margin-left: 10px"
                                    type="primary"
                                    disabled
                                    @click="
                                        queryMerchantTradeVoucherDetail(record)
                                    "
                                    >查看凭证</a-button
                                >
                            </template>
                        </a-table>
                    </template>
                </a-table>
            </a-spin>
        </a-modal>
        <!-- 交易数据维护-添加窗口 -->
        <a-modal
            title="添加"
            :visible="addVisible"
            v-if="addVisible"
            :confirm-loading="addConfirmLoading"
            okText="保存"
            @ok="saveTransactionType"
            @cancel="addCancel"
        >
            <div style="text-align: center">
                <a-select
                    v-model="classifyCode"
                    style="width: 70%"
                    size="large"
                    allowClear
                >
                    <a-select-option
                        v-for="item in optionList"
                        :key="item.classifyCode"
                    >
                        {{ item.classifyName }}
                    </a-select-option>
                </a-select>
            </div>
        </a-modal>
        <!-- 凭证  -->
        <a-modal
            class="drageDialog"
            :title="`凭证 ${
                voucherRow.typeId && voucherRow.oldVoucherId
                    ? `( 字:${voucherRow.typeId} ; 号:${voucherRow.oldVoucherId};)`
                    : ''
            }`"
            :visible="voucherVisible"
            v-if="voucherVisible"
            :confirm-loading="voucherConfirmLoading"
            :destroyOnClose="true"
            width="95%"
            @ok=""
            okText="保存"
            @cancel="voucherCancel"
            :footer="null"
        >
            <a-spin size="large" :spinning="lookSpinning" tip="加载中...">
                <a-table
                    :columns="voucherColumns"
                    :data-source="voucherData"
                    bordered
                    :scroll="{ y: 'calc(100vh - 300px)', x: 'scollXWidth' }"
                    :row-key="(row, idx) => `${idx}`"
                    :footer="null"
                    :pagination="false"
                    :customRow="click"
                     :components='components'
                >
                    <!-- :pagination="voucherPagination" -->
                    <div
                        slot="summary"
                        class="summary"
                        slot-scope="text, record"
                    >
                        <a-tag v-if="record.type === '1'" color="green"
                            >辅</a-tag
                        >
                        {{ record.summary }}
                    </div>
                    <template slot="operation" slot-scope="text, record">
                        <a-button
                            v-if="record.type != '1'"
                            type="primary"
                            :disabled="record.isAdd == 1"
                            @click="addRow(record)"
                            >添加</a-button
                        >
                        <a-button v-else type="link" style="color: #f5deb300"
                            >加</a-button
                        >
                    </template>
                </a-table>
            </a-spin>
        </a-modal>
        <!-- 调整数据维护 -->
        <a-modal
            :visible="adjustDataVisible"
            v-if="adjustDataVisible"
            :confirm-loading="adjustDataConfirmLoading"
            :destroyOnClose="true"
            width="95%"
            @ok=""
            okText="保存"
            @cancel="adjustDataCancel"
            :footer="null"
        >
            <template slot="title">
                调整数据维护 (关联方:{{ newMergeCustomerName }};
                财务数据获取时间:{{
                    newtradeTime
                        ? moment(newtradeTime).format('YYYY-MM-DD')
                        : ''
                }}; 账套年份:{{ formInline.lockYyyy }};)
                <div style="float: right; margin-right: 30px; margin-top: -6px">
                    <a-button @click="queryAdjustEntryForTrade(true)">
                        <a-icon type="search" />
                        查询</a-button
                    >
                    <!-- <a-button
                        :disabled="selectedRowKeys && selectedRowKeys.length ? false : true"
                        @click="batchAddRow"
                    >
                        添加</a-button
                    > -->
                </div>
            </template>
            <a-spin size="large" :spinning="dataDefendSpinning" tip="加载中...">
                <a-form-model
                    layout="inline"
                    :model="formInline"
                    ref="dataDefendForm"
                    style="margin-bottom: 20px"
                >
                    <!-- <a-form-model-item label="凭证日期">
                        <a-range-picker
                            :defaultValue="[
                                moment('2019-01-01'),
                                moment('2019-12-31'),
                            ]"
                            :defaultPickerValue="[
                                moment('2019-01-01'),
                                moment('2019-12-31'),
                            ]"
                            format="MM-DD"
                            style="width: 150px"
                            @change="timeChange"
                        />
                    </a-form-model-item> -->
                    <a-form-model-item label="科目" prop="subjectId">
                        <a-tree-select
                            v-model="formInline.subjectId"
                            show-search
                            style="width: 200px"
                            :dropdown-style="{
                                maxHeight: '400px',
                                overflow: 'auto',
                            }"
                            placeholder="选择科目"
                            allow-clear
                            tree-default-expand-all
                            option-filter-prop="children"
                            :filterTreeNode="fillterFUN"
                            :tree-data="treeData.purpose"
                            key="key"
                            :replaceFields="{ value: 'subjectId' }"
                            @change="selected"
                        >
                        </a-tree-select>
                    </a-form-model-item>
                    <a-form-model-item label="核算类型" prop="assItemId">
                        <a-tree-select
                            v-model="formInline.assItemId"
                            show-search
                            style="width: 200px"
                            :dropdown-style="{
                                maxHeight: '400px',
                                overflow: 'auto',
                            }"
                            placeholder="核算类型"
                            allow-clear
                            option-filter-prop="children"
                            :filterTreeNode="fillterFUN"
                            :tree-data="assitemIsBankTree"
                            key="key"
                            :replaceFields="{ value: 'assItemId' }"
                            :disabled="assItemDisabled"
                            :load-data="onLoadData"
                            @change="selectedSubject"
                            @treeExpand="treeExpand"
                        >
                        </a-tree-select>
                    </a-form-model-item>

                    <a-form-model-item label="显示已添加数据" prop="isAdd">
                        <a-select
                            default-value="0"
                            style="width: 80px"
                            @change="isAddChange"
                        >
                            <a-select-option value="1"> 是 </a-select-option>
                            <a-select-option value="0"> 否 </a-select-option>
                            <!-- <a-select-option value=""> 全部 </a-select-option> -->
                        </a-select>
                    </a-form-model-item>
                </a-form-model>
                <a-table
                    :columns="adjustDataColumns"
                    :data-source="adjustDataData"
                    bordered
                    :scroll="{ y: 'calc(100vh - 340px)', x: 'scollXWidth' }"
                    :row-key="
                        (row, idx) => {
                            `${idx}`;
                        }
                    "
                     :components='components'
                    :footer="null"
                    :pagination="pagination"
                    :customRow="click"
                    :row-selection="{
                        selectedRowKeys,
                        onChange: onSelectChange,
                        getCheckboxProps,
                    }"
                    id="tableList"
                    ref="tableList"
                >
                    <!-- <template slot="direction" slot-scope="text, record">
                        <div>{{ record.direction === 1 ? '借' : '贷' }}</div>
                    </template> -->
                    <template slot="operation" slot-scope="text, record">
                        <!-- <a-button
                        type="primary"
                        :disabled="record.isAdd == 1"
                        @click="addRow(record)"
                        >添加</a-button
                    > -->

                        <a-popconfirm
                            title="确定要删除该条数据吗？"
                            ok-text="确 定"
                            cancel-text="取 消"
                            :disabled="!(record.isAdd == 1)"
                            @confirm="deleteAdjustTradeFromEntry(record)"
                        >
                            <a-button
                                type="primary"
                                :disabled="!(record.isAdd == 1)"
                                >删除</a-button
                            >
                        </a-popconfirm>
                        <a-button
                            style="margin-left: 10px"
                            type="primary"
                            @click="queryAdjustEntryForVoucher(record)"
                            >查看凭证</a-button
                        >
                    </template>
                    <template slot="footer" slot-scope="currentPageData">
                        <a-table
                            v-show="footerTotalData && footerTotalData.length"
                            id="totalTable"
                            ref="totalTable"
                            :bordered="false"
                            :pagination="false"
                            :columns="adjustDataColumns"
                            :dataSource="footerTotalData"
                            :row-selection="rowSelection"
                            :showHeader="false"
                            :scroll="{
