                    // );
                    this.row = selectedRows;
                },
                getCheckboxProps: (record) => ({
                    props: {
                        disabled: record.subjectFullName === '合计', // Column configuration not to be checked
                        name: record.subjectFullName,
                    },
                }),
            };
        },
        objectSetBut() {
            if (this.butState) {
                return true;
            } else {
                return this.data.some((x) => x.tradeComplete == 1);
            }
        },
    },
    methods: {
        moment,
        add(type, treeData) {
            this.visibleAdd = true;
            this.treeData = treeData;
            this.type = type;
        },
        // isShowComplete 从合并跳转过来控制 '确认完成和取消'按钮的disabled状态的字段(不是从合并跳转不使用这个字段控制)
        // tradeComplete控制完成状态,tradeComplete==0表示未完成,isShowComplete==0表示禁用
        editButState(record) {
            if (this.butState) {
                if (record.isShowComplete == 0) {
                    return true;
                } else {
                    return record.tradeComplete == 1 ? true : false;
                }
            } else {
                return record.tradeComplete == 1 ? true : false;
            }
        },
        handleOk(e) {
            this.confirmLoading = true;
            window.removeEventListener('click', () => {});
            console.log("sfsfsfsdfds")
            this.$refs.test.saveMerchantInsideDeatil();
        },
        handleCancel(e) {
            // debugger
            this.visible = false;
            window.removeEventListener('click', () => {});
            //this.$refs.test.reset();
        },
        getCheckboxProps: (record) => ({
            props: {
                disabled: record.subjectFullName === '合计', // Column configuration not to be checked
                name: record.subjectFullName,
            },
        }),
        saveMerchantSubjectSetList() {
            this.$confirm({
                title: '提示:',
                content:
                    '修改科目设置会删除旧数据并根据最新设置对照交易数据 是否确认修改？',
                onOk: () => {
                    this.objectClassCode(this.subjectSetData);
                    const param = {
                        param1: JSON.stringify(this.subjectSetData),
                    };
                    this.$sacpApis
                        .saveMerchantSubjectSetList(param)
                        .then((res) => {
                            if (res.success) {
                                this.subjectConfirmLoading = false;
                                this.subjectVisible = false;
                                this.$message.success('保存成功！');
                                this.$store.state.table.queryMerchantInsideShowList();
                                this.$store.state.table.queryMerchantEffect();
                            } 
                            else {
                                this.$message.error(res.resultInfo.statusText);
                            }
                            this.resetLoading = false;
                        })
                        .catch((err) => {
                            this.$message.error(err);
                        });
                },
                onCancel() {
                    console.log('Cancel');
                },
            });
        },
        subjectHandleCancel(e) {
            this.subjectVisible = false;
            this.subjectConfirmLoading = false;
        },
        queryMerchantSubjectSet() {
            this.generalQuery();
            this.subjectVisible = true;
            this.$sacpApis
                .queryMerchantSubjectSet()
                .then((res) => {
                    if (res.success) {
                        this.subjectSetData = res.data;
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                    this.resetLoading = false;
                })
                .catch((err) => {
                    this.$message.error(err);
                });
        },
        // 查询数据池拆分
        queryMerchantInsideMid(uscc) {
            this.dataSplitVisible = true;
            if (uscc) {
                this.uscc = uscc;
            }
            const param = {
                param3: this.splitState,
                param4: this.uscc ? this.uscc : '',
            };
            this.$sacpApis
                .queryMerchantInsideMid(param)
                .then((res) => {
                    if (res.success) {
                        // console.log(res.data);
                        this.dataSplitSetData = res.data;
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                    // this.resetLoading = false;
                })
                .catch((err) => {
                    this.$message.error(err);
                });
        },
        dataDefend(record) {
            if (record.uscc) {
                this.uscc = record.uscc;
                this.currRow = record;
