        //交易数据维护
        queryEntryForTrade(val) {
            this.selectedRowKeys = [];
            this.selectedRows = [];
            const { lockYyyy, startDate, endDate, subjectId } = this.formInline;
            if (
                startDate == 'Invalid date' ||
                startDate == null ||
                endDate == 'Invalid date' ||
                endDate == null
            ) {
                return this.$message.error('请选择凭证日期!');
            } else if (!subjectId) {
                return this.$message.error('请选择科目或核算类型!');
            }
            this.dataDefendConfirmLoading = true;
            this.dataDefendSpinning = true;
            if (val) {
                this.pagination.start = 0;
                this.pagination.limit = 30;
                this.pagination.current = 1;
            }
            this.pagination.total = 0;
            const { start, limit } = this.pagination;
            this.formInline.uscc = this.uscc;
            const formInline = JSON.parse(JSON.stringify(this.formInline));
            delete formInline.lockYyyy;
            const param = {
                lockCustomerId: this.userinfo.curCustomerId,
                lockYyyy: this.userinfo.curAccYear,
                start,
                limit,
                jsonData: JSON.stringify(formInline),
            };console.log('=====>',this.userinfo)
            this.$sacpApis
                .queryEntryForTrade(param)
                .then((res) => {
                    if (res.success) {
                        this.dataDefendSpinning = false;
                        // console.log(res.data);
                        this.dataDefendData = res.data;
                        this.pagination.total = res.totalCount;
                        this.dataDefendConfirmLoading = false;
                        res.data && res.data.length
                            ? this.queryEntryForTradeSum(param)
                            : (this.footerTotalData = []);
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                    // this.resetLoading = false;
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.dataDefendConfirmLoading = false;
                    this.dataDefendSpinning = false;
                });
        },
