                .then((res) => {
                    if (res.success) {
                        this.subjectSetData = res.data;
                        this.$message.success('对照成功!');
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.lookSpinning = false;
                });
        },
    },
    mounted() {
        // this.generalQuery();
    },
};
</script>

<style lang="scss" scoped>
.button {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
    margin-top: -8px;
}

.editable-cell {
    position: relative;
    /deep/.editable-cell-input-wrapper {
        .ant-input {
            input::-webkit-outer-spin-button,
            input::-webkit-inner-spin-button {
                -webkit-appearance: none;
            }
        }
    }
}

.editable-cell-input-wrapper,
.editable-cell-text-wrapper {
    padding-right: 24px;
}

.editable-cell-text-wrapper {
    padding: 5px 24px 5px 5px;
}

.editable-cell-icon,
.editable-cell-icon-check {
    position: absolute;
    right: 0;
    width: 20px;
    cursor: pointer;
}

.editable-cell-icon {
    line-height: 18px;
    display: none;
}

.editable-cell-icon-check {
    line-height: 28px;
}

.editable-cell:hover .editable-cell-icon {
    display: inline-block;
}

.editable-cell-icon:hover,
.editable-cell-icon-check:hover {
    color: #108ee9;
}

.editable-add-btn {
    margin-bottom: 8px;
}

.ant-modal-root {
    & >>> .ant-modal-wrap {
        overflow: hidden;
        .ant-modal {
            top: 10px;
        }
    }
}

.drageDialog {
    & >>> .ant-modal-wrap {
        .ant-modal {
            .ant-modal-body {
                max-height: calc(100vh - 150px);
                overflow-y: auto;
            }
        }
    }
    & >>> .ant-table-body-inner {
        overflow-y: clip !important;
        padding-right: 4px;
    }
    .summary {
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
    }
}

#tableList,
#totalTable {
    & >>> .ant-table-body-inner {
        overflow-y: clip !important;
        padding-right: 4px;
    }
}

/deep/#tableList {
    .ant-table-body {
        overflow-x: hidden !important;
        max-height: calc(100vh - 360px) !important;
    }
    .ant-table-content {
        table {
            border-collapse: collapse !important;
            .ant-table-thead {
                [key='autoMatch'] {
                    border-right: 0px;
                }
                [key='merchantId'] {
                    border-left: 1px solid #e8e8e8;
                }
            }
        }
    }
    .ant-table-footer {
        padding: 0 !important;
        #totalTable {
            .ant-table-body {
                overflow: auto !important;
                .ant-table-fixed {
                    .ant-table-row-cell-break-word {
                        //ant-table-fixed-columns-in-body
                        // border: 0;
                        color: black;
                    }
                }
            }
            .ant-table-content {
                .ant-table-fixed-left {
                    display: none;
                }
            }
        }
    }
}

.edit {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    .edit-left-table {
        width: 100%;
        margin-right: 20px;
    }
}
.resize-table-th {
    position: relative;
}
.table-draggable-handle {
    position: absolute;
    transform: none !important;
    bottom: 0;
    height: 100% !important;
    left: auto !important;
    right: -5px;
    cursor: col-resize;
    touch-action: none;
    border: none;
}
</style>


