<template>
    <div>
        <a-modal
            title="合并项目"
            :visible="visible"
            width="70%"
            :confirm-loading="confirmLoading"
            :footer="false"
            @ok="handleOk"
            @cancel="handleCancel"
        >
            <a-table
                :columns="columns"
                :data-source="data"
                :pagination="pagination"
                :row-key="(row, idx) => `${idx}`"
                :loading="loading"
                :scroll="{ y: 'calc(100vh - 200px)', x: 'scollXWidth' }"
            >
                <template #projectSource="text, scoped">{{
                    projectSource[text]
                }}</template>
                <template #mergeScope="text, scoped">{{
                    mergeScope[text]
                }}</template>
            </a-table>
        </a-modal>
    </div>
</template>
<script>
export default {
    data() {
        return {
            columns: [
                {
                    title: '序号',
                    width: 30,
                    customRender: function (t, r, index) {
                        return index + 1;
                    },
                },
                {
                    title: '客户名称',
                    dataIndex: 'customerName',
                    key: 'customerName',
                    width: 150,
                    scopedSlots: { customRender: 'customerName' },
                },
                {
                    title: '项目名称',
                    dataIndex: 'projectName',
                    key: 'projectName',
                    width: 200,
                },
                {
                    title: '项目来源',
                    dataIndex: 'projectSource',
                    key: 'projectSource',
                    width: 80,
                    ellipsis: true,
                    scopedSlots: { customRender: 'projectSource' },
                },
                {
                    title: '项目负责人',
                    dataIndex: 'projectManager',
                    key: 'projectManager',
                    width: 60,
                    ellipsis: true,
                },
                // {
                //     title: '是否合并项目',
                //     dataIndex: 'isMergeProject',
                //     key: 'isMergeProject',
                //     width: 70,
                //     ellipsis: true,
                //     customRender: function (t, r, index) {
                //         return t == 1 ? '是' : '否';
                //     },
                // },
                {
                    title: '合并范围',
                    dataIndex: 'mergeScope',
                    key: 'mergeScope',
                    width: 80,
                    ellipsis: true,
                    scopedSlots: { customRender: 'mergeScope' },
                },
            ],
            data: [],
            visible: false,
            confirmLoading: false,
            start: 0,
            limit: 10,
            pagination: {
                current: 1,
                pageSize: 10,
                showSizeChanger: true,
                pageSizeOptions: ['10', '20', '30', '40', '50'],
                showQuickJumper: false,
                size: 'small',
                total: 0,
                showTotal: () => `共${this.pagination.total}条`,
                change: true,
                onShowSizeChange: (current, pageSize) => {
                    this.start = current - 1;
                    this.limit = pageSize;
                    this.pagination.pageSize = pageSize;
                    this.queryMergeProjectList();
                },
                onChange: (current, size) => {
                    //start 0 -10 -20 -30
                    this.start = (current - 1) * size;
                    this.limit = this.pagination.pageSize;
                    this.pagination.current = current;
                    this.queryMergeProjectList();
                },
            },
            projectSource: {},
            mergeScope: {},
            loading: {
                tip: '加载中...',
                spinning: false,
                size: 'large',
            },
        };
    },
    methods: {
        handleOk(e) {
            this.confirmLoading = true;
            setTimeout(() => {
                this.visible = false;
                this.confirmLoading = false;
            }, 2000);
        },
        handleCancel(e) {
            this.visible = false;
        },
        queryMergeProjectType(param) {
            this.param = param;
            const p1 = this.$sacpApis
                .queryMergeProjectType(param)
                .then((res) => {
                    if (res.success) {
                        return res;
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.spinning = false;
                });

            const p2 = this.$sacpApis
                .queryDictionary()
                .then((res) => {
                    return res;
                })
                .catch((err) => this.$message.error(err))
                .finally(() => (this.spinning = false));

            return Promise.all([p1, p2]).then((res) => {
                this.projectSource = res[1]['合并报表项目来源'];
                this.mergeScope = res[1]['合并范围'];
                if (res[0].data && res[0].data[0]) {
                    return res[1]['合并范围'][res[0].data[0].mergeScope];
                } else {
                    return '';
                }
            });
        },
        queryMergeProjectList() {
            this.visible = true;
            this.param.start = this.start;
            this.param.limit = this.limit;
            this.loading.spinning = true;
            return this.$sacpApis
                .queryMergeProjectList(this.param)
                .then((res) => {
                    if (res.success) {
                        if (res.data && res.data.length) {
                            this.data = res.data;
                            this.pagination.total = res.totalCount;
                        } else {
                            this.data = [];
                        }
                        return res;
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.loading.spinning = false;
                });
        },
    },
};
</script>



