<template>
    <div>
        <a-modal
            :width="600"
            v-model="visible"
            :title="`是否确认${item.mergeCustomerName}客户下的关联方对账已完成？`"
            @ok="handleOk"
        >
            <a-form-model
                ref="ruleForm"
                :model="form"
                :rules="rules"
                :label-col="labelCol"
                :wrapper-col="wrapperCol"
            >
                <a-form-model-item label="核对完成理由" prop="desc">
                    <a-input
                        :maxLength="100"
                        v-model="form.desc"
                        type="textarea"
                    />
                </a-form-model-item>
            </a-form-model>
        </a-modal>
    </div>
</template>
<script>
import XEUtils from 'xe-utils';
export default {
    props: {
        objectMessage: Array,
        objectAnalysisMessage: Array,
        debit: Boolean,
    },
    data() {
        this.XEUtils = XEUtils;
        return {
            visible: false,
            item: {},
            userinfo: {},
            form: {},
            rules: {
                desc: [
                    {
                        required: true,
                        message: '请填写核对完成理由！',
                        trigger: 'blur',
                    },
                ],
            },
            labelCol: { span: 5 },
            wrapperCol: { span: 19 },
        };
    },
    methods: {
        // 确认
        clickEnter() {
            // 1是确认  0是取消
            if (this.debit) {
                return this.$message.error(
                    '发生额和展开的明细不相等，请到对应凭证分析菜单做多借多贷分析!'
                );
            }
            let par = {
                param1: this.item.uscc,
                param2: this.item.mergeCustomerId,
                param3: this.item.mergeProjectId,
                param4: 1,
                param5: this.form.desc,
                lockCustomerId: this.userinfo.curCustomerId,
                lockProjectId: this.userinfo.preProjectId,
                lockYyyy:this.userinfo.curAccYear,
            };
            this.$emit('loadingChange', true);

            this.$sacpApis
                .saveMerchantCheckComplete(par)
                .then((res) => {
                    this.$emit('loadingChange', false);
                    this.visible = false;
                    if (res.success) {
                        this.$emit('getOptionList');
                        this.$message.success('核对完成');
                          let refreshStatus = this.$store.state.refreshStatus;
                          this.$store.commit(
                            'changeRefreshStatus',
                              !refreshStatus
                        );
                    } else {
                        this.$message.error(
                            `${res.resultInfo.statusText}(${
                                this.objectMessage
                                    ? '未拆分科目:' +
                                      this.objectMessage.join(',')
                                    : ''
                            }${
                                this.objectAnalysisMessage
                                    ? '需分析科目:' +
                                      this.objectAnalysisMessage.join(',')
                                    : ''
                            })`
                        );
                    }
                })
                .catch((err) => {
                    this.loading = false;
                    this.$emit('loadingChange', false);
                });
        },
        showModal(par) {
            this.userinfo = par.userinfo;
            this.item = par.item;
            this.form = {};
            this.visible = true;
        },
        handleOk(e) {
            const that = this;
            this.$refs.ruleForm.validate((valid) => {
                if (valid) {
                    that.clickEnter();
                } else {
                    return false;
                }
            });
        },
    },
};
</script>
<style  lang='scss'>
.bgr > td {
    background: #fdf5e6;
}
</style>


