<template>
    <div>
        <a-modal
            title="自定义交易事项明细列表"
            :visible="visible"
            :confirm-loading="confirmLoading"
            width="55%"
            @ok="handleOk"
            @cancel="handleCancel"
        >
            <a-form-model
                :model="form"
                :rules="{
                    classifyName: [
                        {
                            required: true,
                            message: '请填写自定义交易事项名称',
                            trigger: 'blur',
                        },
                    ],
                    tradeType: [
                        {
                            required: true,
                            message: '请选择交易类型',
                            trigger: 'blur',
                        },
                    ],
                }"
                ref="form"
                :label-col="{ span: 6 }"
                :wrapper-col="{ span: 14 }"
            >
                <a-form-model-item
                    prop="classifyName"
                    label="自定义交易事项名称"
                >
                    <a-input v-model="form.classifyName" />
                </a-form-model-item>
                <a-form-model-item prop="tradeType" label="请选择交易类型">
                    <a-select v-model="form.tradeType" style="width: 200px">
                        <a-select-option :value="1">
                            采购类（借）
                        </a-select-option>
                        <a-select-option :value="-1">
                            销售类（贷）
                        </a-select-option>
                    </a-select>
                </a-form-model-item>
            </a-form-model>

            <div class="button">
                <a-button type="primary" @click="addItem">添加</a-button>
            </div>
            <a-table :pagination="false" :columns="columns" :data-source="data">
                <template slot="fieldName" slot-scope="text, record, index">
                    <a-input v-model="record.fieldName" />
                </template>
                <template slot="pageFieldType" slot-scope="text, record, index">
                    <a-select
                        style="width: 120px"
                        v-model="record.pageFieldType"
                    >
                        <a-select-option
                            v-for="item in optionList"
                            :key="item.value"
                            :value="item.value"
                        >
                            {{ item.label }}
                        </a-select-option>
                    </a-select>
                </template>
                <template
                    slot="subjectShowName"
                    slot-scope="text, record, index"
                >
                    <a-checkbox
                        @change="
                            (e) => {
                                itemChange(record, 'subjectShowName', index);
                            }
                        "
                        v-model="record.subjectShowName"
                    >
                    </a-checkbox>
                </template>
                <template slot="insideAmount" slot-scope="text, record, index">
                    <a-checkbox
                        @change="
                            (e) => {
                                itemChange(record, 'insideAmount', index);
                            }
                        "
                        v-model="record.insideAmount"
                    >
                    </a-checkbox>
                </template>
                <template slot="action" slot-scope="text, record, index">
                    <div class="editable-row-operations">
                        <a @click="delItem(index)">删除</a>
                    </div>
                </template>
            </a-table>
        </a-modal>
    </div>
</template>

<script>
import sessionUtils from '@data/session/sessionUtils.js';
import { mixin } from './mixins/drageTable';

export default {
 mixins: [mixin],
    data() {
        return {
            visible: false,
            confirmLoading: false,
            form: {
                classifyName: '',
                tradeType:''
            },
            columns: [
                {
                    title: '自定义列名',
                    dataIndex: 'fieldName',
                    scopedSlots: { customRender: 'fieldName' },
                },
                {
                    title: '自定义类型',
                    dataIndex: 'pageFieldType',
                    scopedSlots: { customRender: 'pageFieldType' },
                },
                {
                    title: '是否科目项',
                    dataIndex: 'subjectShowName',
                    scopedSlots: { customRender: 'subjectShowName' },
                },
                {
                    title: '是否金额项',
                    dataIndex: 'insideAmount',
                    scopedSlots: { customRender: 'insideAmount' },
                },
                {
                    title: '操作',
                    key: 'action',
                    scopedSlots: { customRender: 'action' },
                },
            ],
            data: [],
            userinfo: sessionUtils.findUserInfo(),
            optionList: [],
        };
    },
    methods: {
        itemChange(record, type, index) {
            if (record[type]) {
                this.data.map((item, i) => {
                    if (i == index) {
                        item.insideAmount = false;
                        item.subjectShowName = false;
                        item[type] = true;
                    } else {
                        item[type] = false;
                    }
                });
            }
        },
        queryDictionary() {
            this.$sacpApis
                .queryDictionary()
                .then((res) => {
                    this.optionList = [];
                    for (let item in res['内部交易自定义类型']) {
                        this.optionList.push({
                            label: res['内部交易自定义类型'][item],
                            value: item,
                        });
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => (this.spinning = false));
        },
        delItem(index) {
            this.data.splice(index, 1);
        },
        getTableList(classifyCode) {
            let par = {
                param1: classifyCode,
            };
            this.$sacpApis
                .queryMerchantClassifyCustomize(par)
                .then((res) => {
                    if (res.success) {
                        let list = [];
                        res.data.map((item) => {
                            list.push({
                                ...item,
                                insideAmount:
                                    item.insideAmount === '1' ? true : false,
                                subjectShowName:
                                    item.subjectShowName === '1' ? true : false,
                            });
                        });
                        this.data = list;
                        this.form.classifyName = this.data[0].classifyName;
                        this.form.tradeType = this.data[0].tradeType;
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                });
        },
        customShow(type, classifyCode) {
            this.visible = true;
            this.queryDictionary();
            if (type === 'change') {
                this.classifyCode = classifyCode;
                this.getTableList(classifyCode);
            } else {
                this.form = { classifyName: '' };
                this.data = [];
            }
        },
        addItem() {
            this.data.push({
                insideAmount: false,
                subjectShowName: false,
            });
        },
        handleOk() {
            if (this.data.length < 1) {
                return this.$message.error('没有添加自定义行,不能保存');
            }
            this.$refs.form.validate((valid) => {
                if (valid) {
                    for (var i = 0; i < this.data.length; i++) {
                        if (!this.data[i].fieldName) {
                            this.$message.error(
                                `请填写第${i + 1}行的自定义列名`
                            );
                            return;
                        } else if (!this.data[i].pageFieldType) {
                            this.$message.error(
                                `请填写第${i + 1}行的自定义类型`
                            );
                            return;
                        }
                    }
                    this.submitForm();
                }
            });
        },
        submitForm() {
            let param1 = [];
            this.data.map((item) => {
                param1.push({
                    ...item,
                    insideAmount: item.insideAmount ? '1' : '0',
                    subjectShowName: item.subjectShowName ? '1' : '0',
                    classifyName: this.form.classifyName,
                    tradeType:this.form.tradeType
                });
            });
            let par = { param1: JSON.stringify(param1) };
            this.$sacpApis
                .saveMerchantClassifyCustomize(par)
                .then((res) => {
                    if (res.success) {
                        this.$message.success('保存成功');
                        this.$emit('getTableList');
                        this.queryTradeTree();
                        this.visible = false;
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => (this.spinning = false));
        },
        handleCancel(e) {
            this.visible = false;
        },
    },
};
</script>

<style lang="scss" scoped>
.button {
    position: absolute;
    right: 25px;
    top: 82px;
}
</style>


