<template>
    <div>
        <a-modal
            title="上年交易影响"
            width="95%"
            :visible="visible"
            :confirm-loading="confirmLoading"
            okText="保存"
            @ok="handleOk"
            @cancel="handleCancel"
        >
            <grid-mock-table
                border
                v-model="sourseData"
                :rowDragable="false"
                :colDragable="false"
                :rowDragableState="false"
            >
                <template
                    :slot="`col-${edit.prop}`"
                    slot-scope="{
                        row,
                        col,
                        curCol,
                        colIndex,
                        rowIndex,
                        record,
                        recordCol,
                        cellIndex,
                    }"
                    v-for="(edit, tx) in editCell"
                >
                    <div :key="tx" class="slot-div">
                        <div class="slot-div" v-if="edit.pageFieldType === '1'">
                            <!-- 工作量 -->
                            <template v-if="curCol === 'colN3'">
                                <div
                                    class="slot-div"
                                    @click="
                                        switchState(
                                            $event,
                                            row,
                                            curCol,
                                            cellIndex,
                                            recordCol
                                        )
                                    "
                                >
                                    <a-input-number
                                        v-if="
                                            row[
                                                `${row.classifyCode}${curCol}Isedit`
                                            ]
                                        "
                                        v-model="row[curCol]"
                                        :default-value="0"
                                        :precision="0"
                                        a-input-number="0"
                                        :max="999999999"
                                        :disabled="
                                            disabled(
                                                record,
                                                row,
                                                recordCol,
                                                cellIndex
                                            )
                                        "
                                    />
                                    <div
                                        class="slot-div"
                                        :title="row[curCol]"
                                        v-else
                                    >
                                        {{ row[curCol] }}
                                    </div>
                                </div>
                            </template>
                            <!-- 年限 -->
                            <template v-else-if="curCol === 'colN2'">
                                <div
                                    class="slot-div"
                                    @click="
                                        switchState(
                                            $event,
                                            row,
                                            curCol,
                                            cellIndex,
                                            recordCol
                                        )
                                    "
                                >
                                    <a-input-number
                                        v-if="
                                            row[
                                                `${row.classifyCode}${curCol}Isedit`
                                            ]
                                        "
                                        v-model="row[curCol]"
                                        :default-value="0"
                                        :precision="2"
                                        :max="99999999999999999999.99"
                                        :disabled="
                                            disabled(
                                                record,
                                                row,
                                                recordCol,
                                                cellIndex
                                            )
                                        "
                                    />
                                    <div
                                        class="slot-div"
                                        :title="row[curCol]"
                                        v-else
                                    >
                                        {{ row[curCol] }}
                                    </div>
                                </div>
                            </template>
                            <!-- 金额 -->
                            <template v-else>
                                <div
                                    class="slot-div"
                                    @click="
                                        switchState(
                                            $event,
                                            row,
                                            curCol,
                                            cellIndex,
                                            recordCol
                                        )
                                    "
                                >
                                    <a-input-number
                                        v-if="
                                            row[
                                                `${row.classifyCode}${curCol}Isedit`
                                            ]
                                        "
                                        v-model="row[curCol]"
                                        :default-value="0"
                                        :precision="2"
                                        :max="99999999999999999999.99"
                                        :formatter="formatter"
                                        :parser="parser"
                                        :disabled="
                                            disabled(
                                                record,
                                                row,
                                                recordCol,
                                                cellIndex
                                            )
                                        "
                                        @change="
                                            onChange($event, row, col, curCol)
                                        "
                                    />
                                    <div
                                        class="slot-div"
                                        style="text-align: right"
                                        :title="row[curCol] | capitalize"
                                        v-else
                                    >
                                        {{ row[curCol] | capitalize }}
                                    </div>
                                </div>
                            </template>
                        </div>
                        <a-select
                            v-else-if="edit.pageFieldType == 3"
                            v-model="row[curCol]"
                            show-search
                            :default-value="col"
                            placeholder="请选择"
                            option-filter-prop="children"
                            style="width: 200px"
                            :filter-option="filterOption"
                            :disabled="
                                disabled(record, row, recordCol, cellIndex)
                            "
                            @focus="handleFocus"
                            @blur="handleBlur"
                            @change="
                                handleChange(
                                    $event,
                                    row,
                                    curCol,
                                    cellIndex,
                                    rowIndex
                                )
                            "
                        >
                            <a-select-option
                                :value="item.value"
                                v-for="(item, idx) in selectData(curCol)"
                                :key="idx"
                                >{{ item.label }}</a-select-option
                            >
                        </a-select>

                        <a-date-picker
                            style="width: 130px"
                            v-else-if="edit.pageFieldType == 4"
                            v-model="row[curCol]"
                            value-format="YYYY-MM-DD"
                            :disabled="
                                disabled(record, row, recordCol, cellIndex)
                            "
                            :default-value="
                                col ? moment(col, 'YYYY-MM-DD') : ''
                            "
                            @change="onChangeTime"
                        />

                        <a-month-picker
                            style="width: 130px"
                            v-else-if="edit.pageFieldType == 5"
                            v-model="row[curCol]"
                            value-format="YYYY-MM-01"
                            :disabled="
                                disabled(record, row, recordCol, cellIndex)
                            "
                            :default-value="
                                col ? moment(col, 'YYYY-MM-DD') : ''
                            "
                            @change="onChangeTime"
                        />

                        <a-tree-select
                            v-else-if="edit.pageFieldType == 6"
                            v-model="row[curCol]"
                            show-search
                            style="width: 100%"
                            :disabled="
                                disabled(record, row, recordCol, cellIndex)
                            "
                            :dropdown-style="{
                                maxHeight: '400px',
                                overflow: 'auto',
                            }"
                            placeholder="请选择"
                            allow-clear
                            tree-default-expand-all
                            :tree-data="selectTreeData(curCol)"
                            key="key"
                            :replaceFields="{ value: 'subjectId' }"
                            :filterTreeNode="fillterFUN"
                            @change="selected"
                        >
                        </a-tree-select>
                    </div>
                </template>
            </grid-mock-table>
        </a-modal>
    </div>
</template>

<script>
import GridMockTable, { datadrag } from '../../GridMockTable/GridMockTable.vue';
import { formatter } from '../../libs/utils/formatter';

export default {
    data() {
        return {
            visible: false,
            confirmLoading: false,
            transactionType: '0',
            sourseData: [],
            editCell: [],
            treeData: {},
        };
    },
    components: {
        GridMockTable,
    },
    watch: {
        table: {
            handler(val, oldVal) {
                if (val) {
                    this.sourseData = val.influenceTable;
                    this.editCell = val.editCell;
                }
            },
            deep: true,
            immediate: true,
        },
    },
    computed: {
        table() {
            return this.$store.state.table;
        },
    },
    filters: {
        capitalize: (value) => {
            if (value === 0) {
                return '0.00';
            } else if (!value) {
                return '';
            } else if (isNaN(value * 1)) {
                return value;
            } else {
                return formatter.commafy(value * 1);
            }
        },
    },
    methods: {
        showModal(type, treeData) {
            this.transactionType = type;
            this.treeData = treeData;
            this.visible = true;
        },
        handleOk(e) {
            this.confirmLoading = true;
            setTimeout(() => {
                this.visible = false;
                this.confirmLoading = false;
                this.saveMerchantInsideDeatil();
            }, 0);
        },
        // 保存内部交易明细
        saveMerchantInsideDeatil() {
            this.sourseData.forEach((x, idx) => {
                this.sourseData[idx] = [x];
            });
            const padram = { param1: JSON.stringify(this.sourseData) };
            this.$sacpApis
                .saveMerchantInsideDeatil(padram)
                .then((res) => {
                    if (res.success) {
                        this.$message.success('保存成功！');
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    return this.$message.error(err);
                });
        },
        handleCancel(e) {
            this.visible = false;
        },
        hide() {
            console.log(111);
            this.visible = false;
        },
        onChange(val, row, col) {
            // console.log('changed', value);
        },
        onChangeTime(date, dateString) {
            console.log(date, dateString);
        },
        handleChange(option, row) {
            // console.log(`selected`,option,row);
        },
        handleBlur() {
            console.log('blur');
        },
        handleFocus() {
            console.log('focus');
        },
        filterOption(input, option) {
            return (
                option.componentOptions.children[0].text
                    .toLowerCase()
                    .indexOf(input.toLowerCase()) >= 0
            );
        },
        // 树选择
        selected(value, label, extra) {
            // console.log(value, label, extra);
            this.$emit('change', { subjectName: label[0], subjectId: value });
        },
        // 树选择搜索
        fillterFUN(searchVal, treeNode) {
            return treeNode.data.props.title.includes(searchVal);
        },
        //树选择数据
        selectTreeData(col) {
            if (
                col == 'colS1' ||
                col == 'colS2' ||
                col == 'colS3' ||
                col == 'colS5'
            ) {
                return this.treeData.purpose;
            } else {
            }
        },
        //树选择数据
        selectData(col) {
            if (col == 'colS1') {
                return this.treeData.purchaseMethod;
            } else if (col == 'colS4') {
                return this.treeData.depreciation;
            }
        },
        switchState(e, row, curCol, cellIndex, recordCol) {
            // console.log(e, row, curCol);
            // 拼接字段区分每一个单元格状态
            if (recordCol[0].children[cellIndex].isEdit == '0') return;
            row[`${row.classifyCode}${curCol}Isedit`] = true;
            this.$forceUpdate();
            this.$nextTick(() => {
                if (e.target.tagName == 'DIV') {
                    const input = e.currentTarget.querySelector('input');
                    input.blur();
                    input.focus();
                    input.addEventListener('blur', () => {
                        row[`${row.classifyCode}${curCol}Isedit`] = false;
                        this.$forceUpdate();
                    });
                }
            });
        },
        formatter(value) {
            return `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',');
        },
        parser(value) {
            return value.replace(/\$\s?|(,*)/g, '');
        },
        disabled(record, row, recordCol, cellIndex) {
            if (
                record[record.length - 1] == row ||
                recordCol[0].children[cellIndex].isEdit == '0'
            ) {
                return true;
            } else {
                return false;
            }
        },
    },
};
</script>

<style lang="scss" scoped>
.slot-div {
    height: 100%;
}
</style>


