<script>
import { setCookie } from '../libs/utils/cookieUtils';
import session from '../data/session/sessionUtils';
import XEUtils from 'xe-utils';
import merchantRangeSelection from './merchantRangeSelection';
import merchantList from './merchantList';
import matchBusinessData from './matchBusinessData';
import relatedPartyMatch from './relatedPartyMatch';
import relatedPartyDetailForm from './relatedPartyDetailForm';
import associatedTransactionVoucherTable from './associatedTransactionVoucherTable';
import insideTransactionSummary from './insideTransactionSummary/index';
// import viewTransactionVoucherTable from './viewTransactionVoucherTable';
import counter from '../components/counter';
import consolidationScope from '../components/consolidationScope.vue';
import add from './add';
import { mixin } from './insideTransactionSummary/mixins/drageTable';



export default {
    name: 'index',
    components: {
        associatedTransactionVoucherTable,
        merchantRangeSelection,
        merchantList,
        matchBusinessData,
        relatedPartyMatch,
        relatedPartyDetailForm,
        insideTransactionSummary,
        // viewTransactionVoucherTable,
        // DownloadExcel: JsonExcel,
        counter,
        add,
        consolidationScope,
    },
    computed: {
        nextStep() {
            return this.$store.state.nextStep;
        },
        customTransactionBut() {
            return this.$store.state.customTransactionBut;
        },
        refreshStatus() {
            return this.$store.state.refreshStatus;
        },
    },
    watch: {
        //   refreshTreeStatus() {
        //         this.queryTradeTree();
        //     },
        nextStep: {
            handler(val) {
                if (val) {
                    this.onChange(1);
                }
            },
            immediate: true,
        },
        refreshStatus() {
            this.queryInsideCheck();
        },
    },
    mixins: [mixin],
    data() {
        return {
            confirmLoading: false,
            viewTransactionVoucherVisible: false,
            spinning: false,
            current: -1,
            stepData: [
                {
                    title: '客商范围选择',
                    key: '0',
                    content: 'merchantRangeSelection',
                    description: '',
                    status: '',
                },
                {
                    title: '客商列表',
                    key: '1',
                    content: 'merchantList',
                    description: '',
                    status: '',
                },
                {
                    title: '匹配工商数据',
                    key: '2',
                    content: 'matchBusinessData',
                    description: '',
                    status: '',
                },
                {
                    title: '关联方匹配',
                    key: '3',
                    content: 'relatedPartyMatch',
                    description: '',
                    status: '',
                },
                {
                    title: '关联方明细表',
                    key: '4',
                    content: 'relatedPartyDetailForm',
                    description: '',
                    status: '',
                },
                {
                    title: '关联方交易汇总',
                    key: '5',
                    content: 'insideTransactionSummary',
                    description: '',
                    status: '',
                },
            ],
            activeTabKey: '0',
            panes: [],
            newTabIndex: 0,
            customerName: null,
            projectName: null,
            isZoom: false,
            customerData: {},
            num: 0,
            number: 0,
            stepState: false,
            consolidationScopeBut: false,
            butContent: '',
            fileData:[],
            fileName:"",
        };
    },
    methods: {
        updataBusiness() {
            this.$refs.matchBusinessData.updataBusiness('');
        },
        downloadBusiness() {
            this.$sacpApis
                .downloadBusinessData({})
                .then((res) => {
                    // console.log('成功======》');
                })
                .catch((err) => {
                    // console.log('失败', err);
                })
                .finally(() => {});
        },
        
        clickredo(){
            const customerId = this.customerData.curCustomerId
            const projectId = this.customerData.curProjectId
            this.queryTradeTree(customerId,projectId);
           
        },
        clickCustom() {
            this.$refs.insideTransactionSummary.customShow();
        },
        associatedTransactionVoucher() {
            window.open(`#/associatedTransactionVoucherTable?type=false`);
        },
        // viewTransactionVoucher(){
        //     this.viewTransactionVoucherVisible = true
        // },
        handleOk() {
            this.$refs.associatedTransactionVoucherTable.save();
        },
        handleCancel() {
            this.viewTransactionVoucherVisible = false;
            this.associatedTransactionVoucherVisible = false;
        },
        zoomOut() {
            this.isZoom = !this.isZoom;
            this.$store.dispatch('zoomState', this.isZoom);
        },
        //添加科目
        addSubject() {
            this.$refs.merchantRangeSelection.openSubjectTreeDialog();
        },
        resetMerchant() {
            //重置
            this.$refs.merchantRangeSelection.resetMerchant();
        },
        saveMerchatnRangeSelect() {
            //保存客商范围选择
            this.$refs.merchantRangeSelection.submitMerchantRangeData();
        },
        getSujectTree() {
            let params = {
                lockCustomerId: this.customerData.curCustomerId,
                lockYyyy: this.customerData.curAccYear,
            };
            this.$sacpApis
                .findAccSubjectType(params)
                .then((res) => {
                    if (res.success) {
                        session.saveMerchantRangeTree(res.data);
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                });
        },
        tabClick(current) {
            this.current = Number(current);
            let selectedTab = this.panes.filter((item) => item.key == current);
        },
        stepClick(item) {
            let hasExist = this.panes.some((item) => item.key == item.key);
            if (!hasExist) this.panes.push(item);
            this.activeTabKey = item.key;
        },
        onChange(current) {
            //步骤条点击
            this.current = current;
            const panes = this.panes;
            let hasExist = this.panes.some(
                (item) => item.key == this.stepData[current].key
            );
            if (!hasExist) this.panes.push(this.stepData[current]);
            this.activeTabKey = this.stepData[current].key;
            if (this.$store.state.nextStep)
                this.$store.commit('setNextStep', false);
        },
        onEdit(targetKey, action) {
            this[action](targetKey);
            relatedPartyDetailForm;
        },
        remove(targetKey) {
            let activeKey = this.activeTabKey;
            let lastIndex;
            this.panes.forEach((pane, i) => {
                if (pane.key === targetKey) {
                    lastIndex = i - 1;
                }
            });
            const panes = this.panes.filter((pane) => pane.key !== targetKey);
            if (!panes.length) activeKey = '-1'; //删除最后一个tab时清除子组件
            if (panes.length && activeKey === targetKey) {
                if (lastIndex >= 0) {
                    activeKey = panes[lastIndex].key;
                } else {
                    activeKey = panes[0].key;
                }
            }
            this.panes = panes;
            this.activeTabKey = activeKey;
            this.current = Number(activeKey);
        },
        getCustomerInfomation() {
            this.spinning = true;
            this.$sacpApis
                .queryCustomerInformation()
                .then((res) => {
                    if (res.success) {
                        this.customerData = res.data[0];
                        session.saveUserInfo(this.customerData);
                        const { curCustomerId, curProjectId, curAccYear } =
                            res.data[0];
                        if (!this.stepState) {
                            this.param = {
                                lockCustomerId: curCustomerId,
                                lockProjectId: curProjectId,
                                lockYyyy: curAccYear,
                            };
                            this.customerName = `${this.customerData.curCustomerId}-${this.customerData.curCustomerName}`;
                            this.projectName = this.customerData.curProjectName;
                        }
                        this.$refs.consolidationScope
                            .queryMergeProjectType(this.param)
                            .then((res) => {
                                if (!res) {
                                    this.consolidationScopeBut = false;
                                    this.butContent = '';
                                } else {
                                    this.consolidationScopeBut = true;
                                    this.butContent = res;
                                }
                            });
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    // this.$message.error(err);
                })
                .finally(() => {
                    this.getSujectTree();
                    this.spinning = false;
                });
        },
        relateSampling() {
            //关联抽凭 handleRelateSampling
            this.$refs.merchantList.handleRelateSampling();
        },
        uploadGetMatchtList() {
            this.$refs.merchantList.uploadGetMatchtList();
        },
        searchSampling() {
            if (this.current === 1) {
                this.$refs.merchantList.openSearchDialog();
            } else if (this.current === 2) {
                this.$refs.matchBusinessData.openSearchDialog();
            } else if (this.current === 3) {
                this.$refs.relatedPartyMatch.openSearchDialog();
            } else if (this.current === 4) {
                this.$refs.relatedPartyDetailForm.openSearchDialog();
            }
        },
        handleMatchBusinessData() {
            if (this.current == 2) {
                this.$refs.matchBusinessData.queryMatchCompanyList();
            } else {
                this.handleMatchContactsCustomer();
            }
        },
        handleMatchContactsCustomer() {
            //匹配往来客商
            this.spinning = true;
            this.$sacpApis
                .matchContactsCustomer()
                .then((res) => {
                    if (res.success) {
                        this.spinning = false;
                        this.$message.success('匹配成功!');
                        this.$store.commit('refreshRelatedPartMatch', true);
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    // this.spinning = false;
                });
        },
        // 导出接口
        exportMatchBusinessData() {
            this.title = '模板导出(仅限excel)';
            let parmas = {
                lockCustomerId: this.customerData.curCustomerId,
                lockProjectId: this.customerData.curProjectId,
                lockYyyy: this.customerData.curAccYear,
                param10: 1,
            };
            this.$message.success('正在下载 请稍等!');
            this.$sacpApis
                .exportMatchBusinessData(parmas)
                .then((res) => {
            this.$message.success('下载完成!');
                })
                .catch((err) => {
                })
                .finally(() => {});
        },
        handleMatchBusinessDataChange() {
            this.$refs.matchBusinessData.matchCompanyChange();
        },
        handleUpload() {
            this.$refs.relatedPartyMatch.openUploadDialog();
        },
        handleQueryRelateList() {
            this.$refs.relatedPartyMatch.openRelateListDialog(
                this.customerData,
                'sync'
            );
        },
        handleInitRelatedPartyForm() {
            this.$refs.relatedPartyDetailForm.initRelatedPartyForm();
        },
        handleMatchBaiduAddress() {
            this.$refs.matchBusinessData.matchBaiduAddress();
        },
        routerListen() {
            // let routeParam = window.location.hash.split('/');
            // routeParam = routeParam[routeParam.length - 1];
            // routeParam != 'index'
            //     ? (this.current = Number(
            //           this.stepData.filter(
            //               (item) => item.content == routeParam
            //           )[0].key
            //       ))
            //     : (this.current = this.current);
            const routerQuery = JSON.parse(
                window.sessionStorage.getItem('mergeQuery')
            );
            if (routerQuery && routerQuery.type) {
                const {
                    active,
                    curCustomerId,
                    curCustomerName,
                    curProjectId,
                    curProjectName,
                    mergeProjectId,
                    mergeCustomerId,
                    mergeCurAccYear,
                    mergeCustomerName,
                    mergeProjectName,
                } = routerQuery;
                this.stepState = true;
                this.current = active;
                this.customerName = mergeCustomerName;
                this.projectName = mergeProjectName;
                this.param = {
                    lockCustomerId: mergeCustomerId,
                    lockProjectId: mergeProjectId,
                    lockYyyy: mergeCurAccYear,
                };
            } else {
                this.stepState = false;
            }
        },
        queryMergeProjectList() {
            this.$refs.consolidationScope.queryMergeProjectList();
        },
        downLoadImportFile() {
            //  fileId写死,不可更改
            let _fileId = { fileId: 11 };
            this.$sacpApis.downLoad(_fileId, '关联方导入模板.xlsx');
        },
        dynamicDialog() {
            this.$refs.insideTransactionSummary.visible = true;
        },
        showDeleteConfirm() {
            const h = this.$createElement;
            this.$confirm({
                title: '确定要重置吗？',
                content: h('div', {}, [
                    h('p', '该操作会重置以下数据'),
                    h('p', '1.客商范围'),
                    h('p', '2.客商列表'),
                    h('p', '3.匹配工商数据'),
                    h('p', '4.关联方匹配'),
                    h('p', '5.关联方明细'),
                    h('p', '6.内部交易数据'),
                    h('p', '7.关联方对账数据'),
                    h('p', '确认是否重置？'),
                ]),
                okText: '重置',
                okType: 'danger',
                cancelText: '取消',
                onOk: () => {
                    this.resetMerchant();
                },
                onCancel() {
                    // console.log('Cancel');
                },
            });
        },
        clear(val) {
            this.number = val;
        },
        //添加科目
        add() {
            this.$refs.add.showModal();
        },
        refresh() {
            this.$refs.merchantList.queryMerchantListData();
        },
        queryInsideCheck() {
            this.subjectVisible = true;
            this.$sacpApis
                .queryInsideCheck()
                .then((res) => {
                    if (res.success) {
                        const { checkNum, completeNum, relatedNum, checkData } =
                            res.data[0];
                        this.stepData[4].description = `${completeNum}/${relatedNum}`;
                        this.stepData[5].description = `${checkNum}/${relatedNum}`;
                        if (completeNum === relatedNum) {
                            this.stepData[4].status = 'finish';
                        } else {
                            this.stepData[4].status = 'error';
                        }
                        if (checkNum === relatedNum) {
                            this.stepData[5].status = 'finish';
                        } else {
                            this.stepData[5].status = 'error';
                        }
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                    this.resetLoading = false;
                })
                .catch((err) => {
                    this.$message.error(err);
                });
        },
    },
    mounted() {
        this.queryInsideCheck();
        // 计算器
        window.addEventListener('click', (e) => {
            if (this.$refs.counter && this.$refs.counter.icon) {
                if (!e.target.innerText) return;
                const icon = this.$refs.counter.icon;
                const text = e.target.innerText.trim().replaceAll(',', '');
                const star = text.indexOf('.');
                const end = text.lastIndexOf('.');
                if (
                    !isNaN(text * 1) &&
                    star === end &&
                    star > -1 &&
                    end > -1 &&
                    text.length - star === 3 &&
                    icon
                ) {
                    this.number = text * 1;
                }
            }
        });
    },
    created() {
        this.getCustomerInfomation();
        this.routerListen();
        setCookie();
        // 'SESSION',
        // 'SESSION=N2EwNDY1ODQtYTI4Ny00MzY0LWI2ZGEtYmQwN2QwOGYzNjc4',
    },
};
</script>
<style lang="scss">
.my-custom-step {
    &.status-finish {
        .ant-steps-item-title,
        .ant-steps-item-description,
        .ant-steps-item-icon,
        .ant-steps-icon,
        .ant-steps-item-finish,
        .ant-steps-item-icon {
            color: rgb(147, 225, 147) !important;
            border-color: rgb(147, 225, 147) !important;
        }
    }
}
</style>


