<template>
    <div>
        <a-modal
            :width="600"
            v-model="visible"
            :title="`是否确认操作已完成？`"
            @ok="handleOk"
        >
            <a-form-model
                ref="ruleForm"
                :model="form"
                :rules="rules"
                :label-col="labelCol"
                :wrapper-col="wrapperCol"
            >
                <a-form-model-item label="核对完成理由" prop="desc">
                    <a-input
                        :maxLength="100"
                        v-model="form.desc"
                        type="textarea"
                    />
                </a-form-model-item>
            </a-form-model>
        </a-modal>
    </div>
</template>
<script>
import XEUtils from 'xe-utils';
export default {
    data() {
        return {
            XEUtils,
            visible: false,
            param: {},
            form: {},
            rules: {
                desc: [
                    {
                        required: true,
                        message: '请填写核对完成理由！',
                        trigger: 'blur',
                    },
                ],
            },
            labelCol: { span: 5 },
            wrapperCol: { span: 19 },
        };
    },
    methods: {
        // 确认
        saveMerchantTradeComplete() {
            this.param.param4 = 1;
            this.param.param5 = this.form.desc;
            this.$sacpApis
                .saveMerchantTradeComplete(this.param)
                .then((res) => {
                    if (res.success) {
                        this.visible = false;
                        this.$message.success('核对成功！');
                        this.$store.state.table.queryMergeCustomer();
                        let refreshStatus = this.$store.state.refreshStatus;
                        this.$store.commit(
                            'changeRefreshStatus',
                            !refreshStatus
                        );
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.lookSpinning = false;
                });
        },
        showModal(par) {
            this.param = par;
            this.form = {};
            this.visible = true;
        },
        handleOk() {
            this.$refs.ruleForm.validate((valid) => {
                if (valid) {
                    this.saveMerchantTradeComplete();
                } else {
                    return false;
                }
            });
        },
    },
};
</script>
<style  lang='scss'>
.bgr > td {
    background: #fdf5e6;
}
</style>


