<template>
    <a-spin
        tip="加载中..."
        :spinning="spinning"
        size="large"
        class="merchant-range-spin"
    >
        <div class="merchantRangeSelection">
            <div style="textalign: center; margin-bottom: 8px">
                <a-icon type="tag" style="margin-right: 8px" />
                {{ `共查询到${dataList.length || 0}条数据` }}
                <span
                    style="color: #409eff; margin-left: 10px; cursor: pointer"
                    @click="manualRefesh"
                >
                    <a-icon type="sync" style="margin-right: 5px" />刷新
                </span>
            </div>
            <a-table
                :columns="columns"
                :data-source="dataList"
                bordered
                :pagination="false"
                :rowKey="(row) => row.key"
                :scroll="{ y: 'calc(100% - 72px)' }"
                class="merchant-range-table"
                :components='components'
            >
                <template slot="subjectIsMerchant" slot-scope="text, record">
                    <a-checkbox
                        @change="onChange('subjectName', record)"
                        :checked="record.subjectIsMerchant == '1'"
                    >
                        科目
                    </a-checkbox>
                </template>
                <template slot="assitemIsMerchant" slot-scope="text, record">
                    <a-checkbox
                        @change="onChange('account', record)"
                        :checked="record.assitemIsMerchant == '1'"
                    >
                        辅助核算
                    </a-checkbox>
                </template>
                <template slot="assItemName" slot-scope="text, record">
                    <span
                        @click="openSelectDialog(record)"
                        class="select-assItemName"
                    >
                        {{ record.assItemName }}
                    </span>
                </template>
                <template slot="action" slot-scope="text, record">
                    <a-popconfirm
                        title="确定要删除这条数据吗?"
                        ok-text="确定"
                        cancel-text="取消"
                        @confirm="deleteRow(record)"
                    >
                        <a>删除</a>
                    </a-popconfirm>
                </template>
            </a-table>
            <!-- 添加对话框 -->
            <a-modal
                v-model="addDataDialogVisible"
                class="assSubjectDialog"
                title="添加科目"
                ok-text="确认"
                cancel-text="取消"
                width="50%"
                @ok="submitAddData"
                @cancel="hideModal"
            >
                <a-tree
                    v-model="checkedKeys"
                    checkable
                    :expanded-keys="expandedKeys"
                    :selected-keys="selectedKeys"
                    :tree-data="subjectTree"
                    :checkStrictly="checkStrictly"
                    :replace-fields="{}"
                    @expand="onExpand"
                />
            </a-modal>
            <!-- 更改辅助核算树 -->
            <a-modal
                v-model="selectDialogVisible"
                class="assSubjectDialog"
                title="辅助核算选择"
                ok-text="确认"
                cancel-text="取消"
                width="50%"
                @ok="submitAssitemIsMerchantData"
                @cancel="hideAssitemIsMerchantModal"
            >
                <a-tree
                    v-model="assitemIsMerchantCheckedKeys"
                    checkable
                    :tree-data="assitemIsMerchantTree"
                    :expanded-keys="assitemIsMerchantExpandedKeys"
                    :selected-keys="assitemIsMerchantSelectedKeys"
                    :checkStrictly="checkStrictly"
                    @expand="assitemIsMerchantOnExpand"
                />
                <!--  :load-data="onLoadData" -->
                <a-empty
                    :image="simpleImage"
                    v-if="
                        !this.assitemIsMerchantTree ||
                        !this.assitemIsMerchantTree.length
                    "
                />
            </a-modal>
        </div>
    </a-spin>
</template>
<script>
import session from '../data/session/sessionUtils';
import XEUtils from 'xe-utils';
import { Empty } from 'ant-design-vue';

import Vue from 'vue';
import VueDraggableResizable from 'vue-draggable-resizable';
Vue.component('vue-draggable-resizable', VueDraggableResizable);

const renderContent = (value, row, index) => {
    const obj = {
        children: value,
        attrs: {},
    };
    return obj;
};

export default {
    name: 'merchantRangeSelection',
    data() {
        this.components = {
            header: {
                cell: (h, props, children) => {
                    const { key, ...restProps } = props;
                    // console.log('ResizeableTitle', key);
                    const col = this.columns.find((col) => {
                        const k = col.dataIndex || col.key;
                        return k === key;
                    });

                    if (!col || !col.width) {
                        return h('th', { ...restProps }, [...children]);
                    }

                    const dragProps = {
                        key: col.dataIndex || col.key,
                        class: 'table-draggable-handle',
                        attrs: {
                            w: 10,
                            x: col.width,
                            z: 1,
                            axis: 'x',
                            draggable: true,
                            resizable: false,
                        },
                        on: {
                            dragging: (x, y) => {
                                col.width = Math.max(x, 1);
                            },
                        },
                    };
                    const drag = h('vue-draggable-resizable', { ...dragProps });
                    return h('th', { ...restProps, class: 'resize-table-th' }, [
                        ...children,
                        drag,
                    ]);
                },
            },
        };
        const columns = [
            {
                title: '科目',
                dataIndex: 'subjectName',
                width: 170,
                customRender: renderContent,
            },
            {
                title: '客商勾选',
                colSpan: 2,
                width: 170,
                key: 'subjectIsMerchant',
                scopedSlots: { customRender: 'subjectIsMerchant' },
            },
            {
                title: '辅助核算',
                colSpan: 0,
                width: 200,
                dataIndex: 'assitemIsMerchant',
                key: 'assitemIsMerchant',
                scopedSlots: { customRender: 'assitemIsMerchant' },
            },
            {
                title: '辅助核算选择',
                dataIndex: 'assItemName',
                key: 'assItemName',
                scopedSlots: { customRender: 'assItemName' },
            },
            {
                title: '操作',
                key: 'action',
                width: 170,
                scopedSlots: { customRender: 'action' },
            },
        ];
        return {
            columns,
            dataList: [],
            disabledSelect: false,
            key: -1,
            customerInformation: session.findUserInfo(),
            addDataDialogVisible: false,
            subjectTree: session.findMerchantRangeTree(),
            checkedKeys: [],
            expandedKeys: [],
            selectedKeys: [],
            spinning: false,
            assitemTypeTree: [],
            selectDialogVisible: false,
            assitemIsMerchantExpandedKeys: [],
            assitemIsMerchantSelectedKeys: [],
            assitemIsMerchantTree: [],
            assitemIsMerchantCheckedKeys: [],
            currentRow: {},
            checkStrictly: true,
            saveSuccess: false,
        };
    },
    methods: {
        openSubjectTreeDialog() {
            this.addDataDialogVisible = true;
        },
        submitAddData() {
            //提交添加科目树按钮
            if (this.checkedKeys.length == 0)
                return this.$message.warning('请选择需要添加的科目');
            let toTreeArray = XEUtils.toTreeArray(this.subjectTree);
            let arr = [];
            this.checkedKeys.checked.map((val) => {
                let selectedTree = toTreeArray.filter(
                    (item) => item.subjectId == val
                )[0];
                delete selectedTree['children'];
                this.dataList.push(selectedTree);
            });
            this.checkedKeys = [];
            this.hideModal();
        },
        onExpand(expandedKeys) {
            this.expandedKeys = expandedKeys;
        },
        onCheck(checkedKeys) {
            this.checkedKeys = checkedKeys;
        },
        hideModal() {
            //关闭添加科目树对话框
            this.addDataDialogVisible = false;
        },
        openSelectDialog(row) {
            //单击辅助核算树 - 父节点
            if (
                !row.assitemIsMerchant ||
                (row.assitemIsMerchant && row.assitemIsMerchant == '0')
            )
                return this.$message.warning('请先选中辅助核算!');
            this.selectDialogVisible = true;
            this.currentRow = row;
            let params = {
                lockCustomerId: this.customerInformation.curCustomerId,
                lockYyyy: this.customerInformation.curAccYear,
                param11: row.subjectId,
                param20: 1,
            };
            this.queryMerchantTree(params);
        },
        onLoadData(treeNode) {
            //子节点
            return new Promise((resolve) => {
                let params = {
                    lockCustomerId: this.customerInformation.curCustomerId,
                    lockYyyy: this.customerInformation.curAccYear,
                    param11: this.currentRow.subjectId,
                    param21: treeNode.dataRef.assItemId,
                };
                this.queryMerchantTree(params);
                resolve();
            });
        },
        queryMerchantTree(params) {
            this.$sacpApis
                .findAssitemTypeTree(params)
                .then((res) => {
                    if (res.success) {
                        res.data.map((item) => {
                            item.isLeaf =
                                (item.parentId === '' && !item.isLeaf) ||
                                !item.isLeaf
                                    ? false
                                    : true;
                        });
                        if (params.param21) {
                            let splitAssitemIsMerchantTree =
                                this.assitemIsMerchantTree.filter(
                                    (item) => item.id === params.param21
                                );
                            splitAssitemIsMerchantTree.push(...res.data);
                            XEUtils.toArrayTree(splitAssitemIsMerchantTree, {
                                sortKey: 'id',
                            }); //返回根据id排序的树结构
                            splitAssitemIsMerchantTree =
                                splitAssitemIsMerchantTree.filter(
                                    (item) => item.parentId === ''
                                ); //过滤子节点
                            this.assitemIsMerchantTree =
                                this.assitemIsMerchantTree.filter(
                                    (item) =>
                                        item.id !=
                                        splitAssitemIsMerchantTree[0].id
                                );

                            this.assitemIsMerchantTree.push(
                                splitAssitemIsMerchantTree[0]
                            );
                            this.assitemIsMerchantTree = XEUtils.sortBy(
                                this.assitemIsMerchantTree,
                                'id'
                            );
                        } else {
                            this.assitemIsMerchantTree = res.data;
                        }
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => {
                    this.spinning = false;
                    // this.assitemIsMerchantExpandedKeys=[];
                });
        },
        submitAssitemIsMerchantData() {
            //修改辅助核算选择
            if (
                this.assitemIsMerchantTree.length == 0 ||
                !this.assitemIsMerchantCheckedKeys ||
                this.assitemIsMerchantCheckedKeys.length == 0
            )
                return this.hideAssitemIsMerchantModal();
            let toTreeArray = XEUtils.toTreeArray(this.assitemIsMerchantTree);
            let selectedArr = [];
            this.assitemIsMerchantCheckedKeys.checked.map((val) => {
                selectedArr.push(
                    toTreeArray.filter((item) => item.assItemId == val)[0]
                );
            });
            let assItemId, assItemName;
            selectedArr &&
                selectedArr.length &&
                selectedArr.map((item) => {
                    if (item) {
                        assItemId += `,${item.assItemId}`;
                        assItemName += `,${item.assItemName}`;
                    }
                });
            assItemId = assItemId
                .split(',')
                .filter((item) => item != 'undefined')
                .join();
            assItemName = assItemName
                .split(',')
                .filter((item) => item != 'undefined')
                .join();
            this.currentRow.assItemId = assItemId;
            this.currentRow.assItemName = assItemName;
            this.hideAssitemIsMerchantModal();
        },
        hideAssitemIsMerchantModal() {
            this.selectDialogVisible = false;
            this.assitemIsMerchantCheckedKeys = [];
            this.assitemIsMerchantTree = [];
            this.assitemIsMerchantExpandedKeys = [];
        },
        assitemIsMerchantOnExpand(expandedKeys, { expanded: bool, node }) {
            this.assitemIsMerchantExpandedKeys = expandedKeys;
            let params = {
                lockCustomerId: this.customerInformation.curCustomerId,
                lockYyyy: this.customerInformation.curAccYear,
                param11: this.currentRow.subjectId,
                param21:
                    this.assitemIsMerchantExpandedKeys[
                        this.assitemIsMerchantExpandedKeys.length - 1
                    ],
            };
            if (expandedKeys && expandedKeys.length) {
                this.queryMerchantTree(params);
            }
        },
        onChange(value, row) {
            //checkbox操作
            this.key = row.key;
            if (value == 'subjectName') {
                if (!row.subjectIsMerchant || row.subjectIsMerchant == '0') {
                    row.subjectIsMerchant = '1';
                    // row.assitemIsMerchant="0";
                } else {
                    row.subjectIsMerchant = '0';
                    // row.assitemIsMerchant="1";
                }
            } else {
                if (!row.assitemIsMerchant || row.assitemIsMerchant == '0') {
                    row.assitemIsMerchant = '1';
                    // row.subjectIsMerchant="0";
                } else {
                    row.assitemIsMerchant = '0';
                    // row.subjectIsMerchant="1";
                }
            }
            //key不变的话是不会刷新的
            row.key = `${row.key}+a`;
        },
        submitMerchantRangeData() {
            //保存接口  1.客商勾选必须选一; 2.辅助核算选中,必须要有辅助核算选择
            let selectMerchantFlag = this.dataList.some(
                (item) =>
                    item.subjectIsMerchant == '0' &&
                    item.assitemIsMerchant == '0'
            );
            if (selectMerchantFlag)
                return this.$message.error('请勾选科目或辅助核算');
            let assitemIsMeerchantFlag = this.dataList.some(
                (item) => item.assitemIsMerchant == '1' && !item.assItemName
            );
            if (assitemIsMeerchantFlag)
                return this.$message.error('勾选辅助核算后必须有辅助核算选择');
            this.spinning = true;
            this.$sacpApis
                .saveMerchantRange(this.dataList)
                .then((res) => {
                    if (res.success) {
                        this.$store.commit('setNextStep', true);
                        this.$message.success('保存成功!');
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.spinning = false;
                    this.hideAssitemIsMerchantModal();
                    this.queryMerchantRangeData();
                    // this.$store.commit('setSaveMerchantRange',false);
                });
        },
        deleteRow(row) {
            //删除
            this.dataList = this.dataList.filter((item) => item.key != row.key);
        },
        queryMerchantRangeData() {
            this.$sacpApis
                .queryMerchantRange()
                .then((res) => {
                    if (res.success) {
                        if (res.data && res.data.length) {
                            res.data.forEach((item, index) => {
                                XEUtils.set(item, 'key', index);
                            });
                        }
                        this.dataList = res.data;
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    // this.queryAddSubjectTree();
                });
        },
        resetMerchant() {
            this.spinning = true;
            this.$sacpApis
                .resetMerchant()
                .then((res) => {
                    res.success
                        ? this.queryMerchantRangeData() &&
                          this.$message.success('重置成功!')
                        : this.$message.error(res.resultInfo.statusText);
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.spinning = false;
                });
        },
        manualRefesh() {
            this.queryMerchantRangeData();
        },
    },
    created() {
        this.queryMerchantRangeData();
    },
    beforeCreate() {
        this.simpleImage = Empty.PRESENTED_IMAGE_SIMPLE;
    },
};
</script>
<style>
.resize-table-th {
    position: relative;
}
.table-draggable-handle {
    transform: none;
    position: absolute;
    height: 100% !important;
    left: auto !important;
    right: -5px;
    cursor: col-resize;
    touch-action: none;
    border: none;
}
</style>


