<template>
    <div>
        <a-modal
            title="自定义交易事项列表"
            :visible="visible"
            :confirm-loading="confirmLoading"
            width="55%"
            :footer="null"
            @ok="handleOk"
            @cancel="handleCancel"
        >
            <div class="button">
                <a-button type="primary" @click="setUpShow">设置</a-button>
                <a-button type="primary" @click="addItem">添加</a-button>
            </div>
            <a-table :columns="columns" :data-source="data" :rowKey="(record,index)=>{return index}">
                <template slot="action" slot-scope="text, record, index">
                    <div class="editable-row-operations">
                        <a @click="changItem(record)">编辑</a>
                        &nbsp; &nbsp; &nbsp;
                        <a-popconfirm
                            title="是否删除这一项"
                            ok-text="确 认"
                            cancel-text="取 消"
                            @confirm="delItem(record)"
                        >
                            <a>删除</a>
                        </a-popconfirm>
                    </div>
                </template>
            </a-table>
        </a-modal>
        <custom-add-change-dialog
            @getTableList="getTableList"
            ref="customAddChangeDialog"
        />
        <set-up-dialog @getTableList="getTableList" ref="setUpDialog" />
    </div>
</template>

<script>
import sessionUtils from '@data/session/sessionUtils.js';
import customAddChangeDialog from './customAddChangeDialog';
import setUpDialog from './setUpDialog';
import { mixin } from './mixins/drageTable';
export default {
    components: {
        customAddChangeDialog,
        setUpDialog,
    },
    mixins: [mixin],
    data() {
        return {
            visible: false,
            confirmLoading: false,
            columns: [
                {
                    title: '自定义交易事项名称',
                    key: 'classifyName',
                    dataIndex: 'classifyName',
                    scopedSlots: { customRender: 'classifyName' },
                },
                {
                    title: '操作',
                    key: 'action',
                    scopedSlots: { customRender: 'action' },
                },
            ],
            data: [],
            userinfo: sessionUtils.findUserInfo(),
        };
    },
    methods: {
        setUpShow() {
            this.$refs.setUpDialog.show();
        },
        delItem(record) {
            let par = {
                param1: record.classifyCode,
            };
            this.$sacpApis
                .deleteMerchantClassifyCustomize(par)
                .then((res) => {
                    if (res.success) {
                        this.$message.success('操作成功');
                        this.getTableList();
                        this.$refs.setUpDialog.queryTradeTree1();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                });
        },
        changItem(record) {
            let classifyCode = record.classifyCode;
            this.$refs.customAddChangeDialog.customShow('change', classifyCode);
        },
        getTableList() {
            let par = {
                sqlId: 'DG40039',
                param1: this.userinfo.curCustomerId,
                param2: this.userinfo.curProjectId,
                param3: 1,
                param4: 4,
            };
            this.$sacpApis
                .generalQuery(par)
                .then((res) => {
                    if (res.success) {
                        this.data = res.data;
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                });
        },
        customShow() {
            this.visible = true;
            this.getTableList();
        },
        addItem() {
            this.$refs.customAddChangeDialog.customShow('add');
        },
        handleOk(e) {},
        handleCancel(e) {
            this.visible = false;
            const table = this.$store.state.table;
            this.queryTradeTree().then((res) => {
                if (res && res.length) {
                    const children = res[0].children[3].children;
                    if (children && children.length) {
                        if (table.classifyCode) {
                            const state = children.some(
                                (el) => el.classifyCode == table.classifyCode
                            );
                            if (state) {
                                table.queryMerchantInsideClassify(
                                    '4',
                                    table.classifyCode
                                );
                            }
                        } else {
                            table.queryMerchantInsideClassify(
                                '4',
                                children[0].classifyCode
                            );
                        }
                    }
                }
            });
        },
    },
};
</script>

<style lang="scss" scoped>
.button {
    text-align: right;
    margin-bottom: 20px;
    margin-top: -8px;
}
</style>


