<template>
    <div class="insideTransactionSummary">
        <a-layout>
            <a-layout-sider
                :style="{
                    backgroundColor: 'white',
                    flex: state ? ' 0 0 230px' : ' 0 0 0px',
                    maxWidth: '300px',
                    minWidth: state ? '200px' : '0px',
                }"
            >
                <div class="asider-container">
                    <a-button
                        type="primary"
                        class="shrink-button"
                        @click="hideMenu"
                    >
                        <a-icon type="caret-left" v-if="state" />
                        <a-icon type="caret-right" v-if="!state" />
                    </a-button>
                    <a-spin
                        tip="加载中..."
                        size="large"
                        :spinning="treeLoading"
                    >
                        <a-directory-tree
                            v-if="state&&treeData.length"
                            multiple
                            :defaultExpandAll="defaultAll"
                            :showIcon="false"
                            :tree-data="treeData"
                            @select="onSelect"
                            @expand="onExpand"
                            :replaceFields="{
                                children: 'children',
                                title: 'classifyName',
                                key: 'key',
                            }"
                            :style="{
                                fontSize: '16px',
                                padding: '20px 0',
                            }"
                        >
                        </a-directory-tree>
                    </a-spin>
                </div>
            </a-layout-sider>
            <a-layout-content
                :style="{
                    height: 'calc(100vh - 100px)',
                    margin: '0px 16px 30px',
                    overflow: 'auto',
                    backgroundColor: 'white',
                    marginLeft: '15px!important',
                }"
            >
                <showData
                    ref="showData"
                    :publicData="publicData"
                    :curTableData="tableData"
                    :curInfluenceTableData="influenceTableData"
                    :editCell="editCell"
                    :loading="loading"
                    :influenceLoading="influenceLoading"
                    :userinfo="userinfo"
                    :publicParam="publicParam"
                    :butState="butState"
                ></showData>
            </a-layout-content>
        </a-layout>
    </div>
</template>

<script>
import showData from './showData';
import { mixin } from './mixins/drageTable';
import sessionUtils from '../../data/session/sessionUtils.js';

export default {
    name: 'InsideTransactionSummary',
    mixins: [mixin],
    provide() {
        return {
            updateTreeData: (data) => this.treeData = data || []
        };
    },
    data() {
            this.components = {
        header: {
        cell: (h, props, children) => {
          const { key, ...restProps } = props
        //   console.log('ResizeableTitle', key)
        console.log(this.columns);
          const col = this.columns.find(col => {
            const k = col.dataIndex || col.key
            return k === key
          })
 
          if (!col || !col.width) {
            return h('th', { ...restProps }, [...children])
          }
 
          const dragProps = {
            key: col.dataIndex || col.key,
            class: 'table-draggable-handle',
            attrs: {
              w: 10,
              x: col.width,
              z: 1,
              axis: 'x',
              draggable: true,
              resizable: false
            },
            on: {
              dragging: (x, y) => {
                col.width = Math.max(x, 1)
              }
            }
          }
          const drag = h('vue-draggable-resizable', { ...dragProps })
          return h('th', { ...restProps, class: 'resize-table-th' }, [...children, drag])
        }
      }
    };
        return {
            // 关联方下拉
            publicData: {
                mergeCustomerName: [],
                AdministrativeExpenses: [],
                validateData: this.validateData,
                title: '存货采购',
            },
            state: true,
            code: [],
            tableData: [],
            influenceTableData: [],
            classifyCode: '',
            NewclassifyCode: '',
            tableDataCategory: [],
            insideType: 1,
            editCell: [],
            menuNode: 0,
            loading: false,
            influenceLoading: false,
            tableDataFormat: [],
            influenceTable: [],
            treeData: [],
            treeLoading: false,
            userinfo: {},
            publicParam: {},
            butState: false,
        };
    },
    components: {
        showData,
    },
    computed: {
        zoomState() {
            return this.$store.state.zoomState;
        },
    },
    watch: {
        zoomState: {
            handler(val, oldVal) {
                if (val) {
                    this.state = false;
                }
            },
        },
        userinfo: {
            handler(val, oldVal) {
                if (val) {
                    this.$store.dispatch('handlerUserInfo', val);
                }
            },
            immediate: true,
        },
        publicParam: {
            handler(val, oldVal) {
                if (val) {
                    this.$store.dispatch('handlerPublicParam', val);
                }
            },
            immediate: true,
        }
    },
    methods: {
        customShow() {
            this.$refs.showData.customShow();
        },
        onSelect(keys, e) {
            const { selected, selectedNodes, node, event } = e;
            const { classifyCode, insideType, classifyName } =
                selectedNodes[0].dataRef;
                // console.log(classifyCode,"111111")
            // debugger
            if (
                (classifyCode !== 'all' && isNaN(classifyCode * 1)) ||
                classifyCode == 4
            ) {
                this.$refs.showData.addButState = true;
            } else {
                this.$refs.showData.addButState = false;
            }
            if (classifyCode !== 'all' && isNaN(classifyCode * 1)) {
                this.NewclassifyCode = classifyCode;
                this.insideType = insideType;
                this.publicData.title = classifyName;
                // 表头
                this.queryMerchantInsideClassify(insideType, classifyCode);
            } else {
                const table = {
                    tableData: [],
                    originalTableData: [],
                    editCell: [],
                    header: [],
                    field: [],
                    influenceTable: [],
                    queryMerchantInsideShowList:
                        this.queryMerchantInsideShowList, //queryMerchantInsideList
                    queryMerchantEffect: this.queryMerchantEffect,
                    queryMergeCustomer: this.queryMergeCustomer,
                    insideType: this.insideType,
                    menuNode: this.menuNode,
                    classifyCode: this.NewclassifyCode,
                    queryMerchantInsideClassify:
                        this.queryMerchantInsideClassify,
                };
                this.tableData = [];
                this.editCell = [];
                this.$store.dispatch('tableColumn', table);
            }
            this.$store.dispatch('hanlderCustomTransactionBut', true);
        },
        onExpand() {
            // console.log('Trigger Expand');
        },
        // 关联方下拉选择
        queryMergeCustomer() {
            // this.spinning = true;
            if (!this.userinfo) return;
            const { curCustomerId, curProjectId } = this.userinfo;
            const param = {
                param1: curCustomerId,
                param2: curProjectId,
                ...this.publicParam,
            };
            this.$sacpApis
                .queryMergeCustomer(param)
                .then((res) => {
                    if (res.success) {
                        // console.log('关联方下拉选择', res.data);
                        // res.data.forEach((el) => {
                        //     this.publicData.mergeCustomerName.push({
                        //         label: el.mergeCustomerName,
                        //         value: el.mergeCustomerId,
                        //     });
                        // });
                        this.publicData.mergeCustomerName = res.data;
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => (this.spinning = false));
        },
        // 接受劳务下拉选择
        findSelectApi() {
            this.$sacpApis
                .findSelectApi()
                .then((res) => {
                    if (res.success) {
                        // console.log('下拉', res.data);
                        this.publicData.AdministrativeExpenses = res.data[0].tb;
                    } else {
                        return this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => (this.spinning = false));
        },
        hideMenu() {
            if (this.state) {
                this.state = false;
            } else {
                this.state = true;
            }
        },
        // 查询页面表格格式,对表头数据进行第一次筛选
        queryMerchantInsideClassify(insideType, classifyCode) {
            // debugger
            this.$sacpApis
                .queryMerchantInsideClassify(this.publicParam)
                .then((res) => {
                    if (res.success) {
                        // console.log('queryMerchantInsideClassify', res);
                        this.tableDataCategory = [];
                        const tableDataCategory = [];
                        if (!Array.isArray(res.data)) return;
                        // 根据insideType判断数据是属于哪个表格，从而进行分类
                        res.data.forEach((x) => {
                            if (
                                x.classifyCode === classifyCode &&
                                x.insideType === insideType
                            ) {
                                tableDataCategory.push(x);
                            }
                        });

                        const tableFormat =
                            this.tableCategory(tableDataCategory);
                        this.$store.dispatch('tableColumn', tableFormat);
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    console.error(err);
                    this.$message.error(err);
                });
        },
        // 转换成表头
        tableCategory(column) {
            // debugger
            const tree = [];
            this.code = [];
            const editCell = [];
            const header = [];
            const field = [];
            this.influenceTable = [];
            this.tableDataFormat = [];
            // 筛选、组建复杂表头
            if (!Array.isArray(column)) return;
            column.forEach((x, idx) => {
                if (this.code.indexOf(x.classifyCode) == -1) {
                    this.code.push(x.classifyCode);
                    tree.push({
                        title: x.classifyName,
                        prop: x.classifyCode,
                        code: x.classifyCode,
                        isCur: x.isCur,
                        children: [],
                        data: [
                            {
                                ...x,
                            },
                        ],
                    });
                    field.push({ ...x });
                    if (
                        x.classifyCode != 'A0' &&
                        x.classifyCode != 'E0' &&
                        x.classifyCode != 'G0'
                    ) {
                        header.push({ title: x.classifyName, idx: idx });
                    }
                }
            });
            // 往子列里添加字段，并筛选可编辑单元格
            tree.forEach((x) => {
                column.forEach((l, lx) => {
                    if (x.prop == l.classifyCode) {
                        x.children.push({
                            title: l.fieldName,
                            prop: l.fieldCode,
                            dataField: l.dataField,
                            classifyCode: l.classifyCode,
                            isEdit: l.isEdit,
                            pageFieldType: l.pageFieldType,
                            dicCode: l.dicCode,
                        });
                        // debugger
                        x.data[0][l.fieldCode] = '';
                        if (l.isEdit == 1) {
                            editCell.push({
                                title: l.fieldName,
                                prop: l.fieldCode,
                                isEdit: l.isEdit,
                                pageFieldType: l.pageFieldType,
                                classifyCode: l.classifyCode,
                                insideType: l.insideType,
                            });
                        }
                    }
                });
            });
            // 分离影响表
            this.classifyCode = '';
            tree.forEach((x) => {
                if (x.prop == 'G0' || x.prop == 'A0' || x.prop == 'E0') {
                    this.classifyCode = x.prop;
                    this.influenceTable.push({ columns: [x], data: [] }); //{ columns: [x], data: x.data }
                    delete x.data;
                } else {
                    this.tableDataFormat.push({ columns: [x], data: [] }); //x.data
                    delete x.data;
                }
            });
            // console.log('this.tableDataFormat',this.tableDataFormat)
            this.tableDataFormat.unshift({
                columns: [
                    {
                        title: '关联方名称',
                        prop: 'mergeCustomerName',
                        isEdit: '0',
                        pageFieldType: '2',
                        code: 'mcn',
                        width: 300,
                    },
                ],
                data: [],
            });
            this.influenceTable.unshift({
                columns: [
                    {
                        title: '关联方名称',
                        prop: 'mergeCustomerName',
                        isEdit: '0',
                        pageFieldType: '2',
                        code: 'mcn',
                        width: 300,
                    },
                ],
                data: [], //[{ mergeCustomerName: '' }]
            });
            field.unshift({ mergeCustomerName: '' });
            // debugger
            const originalTableData = JSON.parse(
                JSON.stringify(this.tableDataFormat)
            );
            this.editCell = editCell;
            // console.log('this.influenceTable', this.influenceTable);
            this.tableDataFormat && this.tableDataFormat.length
                ? this.queryMerchantInsideShowList() //this.queryMerchantInsideList()
                : '';
            this.influenceTable && this.influenceTable.length
                ? this.queryMerchantEffect()
                : '';
            this.tableData = this.tableDataFormat;
            return {
                tableData: this.tableDataFormat,
                originalTableData,
                editCell,
                header,
                field,
                influenceTable: this.influenceTable,
                queryMerchantInsideShowList: this.queryMerchantInsideShowList, //queryMerchantInsideShowList
                queryMerchantEffect: this.queryMerchantEffect,
                queryMergeCustomer: this.queryMergeCustomer,
                insideType: this.insideType,
                menuNode: this.menuNode,
                classifyCode: this.NewclassifyCode,
                queryMerchantInsideClassify: this.queryMerchantInsideClassify,
            };
        },
        // 查询内部交易数据
        queryMerchantInsideShowList() {
            this.loading = true;
            const params = {
                param4: this.insideType,
                param5: this.NewclassifyCode,
                ...this.publicParam,
            };
            this.$sacpApis
                .queryMerchantInsideShowList(params)
                .then((res) => {
                    if (res.success) {
                        this.loading = false;
                        let tableData = this.fillData(
                            this.tableDataFormat,
                            res
                        );
                        // 向后端传递节点名称并用'|'分隔id和name(转换成id,回显)
                        if (tableData[1] && tableData[1].data) {
                            tableData[1].data.forEach((x) => {
                                this.editCell.forEach((l) => {
                                    if (l.pageFieldType === '6' && x[l.prop]) {
                                        x[l.prop] = x[l.prop].replace('|', '-');
                                    }
                                });
                            });
                        }

                        this.mergeTable(tableData);
                        this.tableSum(tableData);
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                    // console.log('this.tableDataFormat', this.tableDataFormat);
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        // 查询影响表
        queryMerchantEffect() {
            if (!this.userinfo) return;
            const { preCustomerId, preProjectId } = this.userinfo;
            const param = {
                param1: preCustomerId, //curCustomerId
                param2: preProjectId, //curProjectId
                param3: this.classifyCode,
                param4: '',
            };
            let p4 = param.param4;
            p4 = '';
            switch (this.classifyCode) {
                case 'A0':
                    p4 = 'A1';
                    break;
                case 'G0':
                    p4 = 'G1';
                    break;
                case 'E0':
                    p4 = 'E1';
                    break;
            }
            if (!p4) {
                this.influenceTableData = [];
                return;
            }
            this.influenceLoading = true;
            this.$sacpApis
                .queryMerchantEffect(param)
                .then((res) => {
                    if (res.success) {
                        this.influenceLoading = false;
                        this.influenceTableData = this.fillData(
                            this.influenceTable,
                            res
                        );
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                    // console.log('this.influenceTable', this.influenceTable);
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.influenceLoading = false;
                });
        },

        fillData(tableData, res) {
            const requestData = res.data[0];
            tableData.forEach((x, xs) => {
                x.data = [];
                const code = x.columns[0].code;
                if (code != 'mcn' && requestData && requestData[code]) {
                    const colData = requestData[code];
                    x.data = colData.concat(...x.data);
                    // 填充第一列数据
                    x.data.forEach((l, lx) => {
                        // if (l.mergeCustomerName) {
                        tableData[0].data[lx] = {
                            mergeCustomerName: l.mergeCustomerName,
                            mergeProjectName: l.mergeProjectName,
                            mergeProjectId: l.mergeProjectId,
                            uscc: l.uscc,
                            mergeCustomerId: l.mergeCustomerId,
                            // };
                        };
                    });
                }
            });
            return tableData;
        },
        //查询交易数据维护(新)
        queryMerchantInsideList() {
            // this.spinning = true;
            const params = {
                param4: this.insideType,
                param5: this.NewclassifyCode,
            };
            this.$sacpApis
                .queryMerchantInsideList(params)
                .then((res) => {
                    if (res.success) {
                        if (res.data && res.data.length) {
                            let tableData = this.fillData(
                                this.tableDataFormat,
                                res
                            );
                            this.mergeTable(tableData);
                            this.tableSum(tableData);
                        } else {
                            // this.treeData = [];
                        }
                    } else {
                        return this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => (this.spinning = false));
        },
    },
    mounted() {
        const routerQuery = JSON.parse(
            window.sessionStorage.getItem('mergeQuery')
        );
        if (routerQuery&&routerQuery.type) {
            const {
                curProjectName,
                curCustomerName,
                mergeProjectId,
                mergeCustomerId,
                curCustomerId,
                curProjectId,
                menuID,
                curAccYear,
                type,
            } = routerQuery;
            this.butState = true;
            this.publicParam = {
                lockProjectId: curProjectId,
                lockCustomerId: curCustomerId,
                customerId: mergeCustomerId,
                projectId: mergeProjectId,
                menuID: menuID,
                lockYyyy: curAccYear,
            };
            this.userinfo = {
                curProjectId,
                curCustomerId,
                mergeCustomerId,
                mergeProjectId,
                curCustomerName,
                curProjectName,
                curAccYear,
            };
            // this.$router.replace({
            //     name: '/index',
            //     query: {},
            // });
        } else {
            this.userinfo = sessionUtils.findUserInfo();
            const { curCustomerId, curProjectId , curAccYear} = this.userinfo;
            this.userinfo.mergeCustomerId = curCustomerId;
            this.userinfo.mergeProjectId = curProjectId;
            this.publicParam = {
                lockProjectId: curProjectId,
                lockCustomerId: curCustomerId,
                lockYyyy: curAccYear,
            };
            this.butState = false;
        }
        this.queryMergeCustomer();
        this.findSelectApi();
        this.queryTradeTree();
    },
};
</script>

<style lang="scss" scoped>
.ant-layout-sider-children {
    .asider-container {
        position: relative;
        height: 100%;
        .shrink-button {
            position: absolute;
            top: 50%;
            right: 0px;
            z-index: 1;
            padding: 0 5px;
        }
    }
}

/deep/ h3 {
    margin: 20px 0;
    font-weight: bold;
}

/deep/ .ant-table {
    .ant-table-content {
        .ant-table-scroll {
            margin-right: 10px;
            .ant-table-footer {
                text-align: right;
            }
        }
    }
}
.resize-table-th {
  position: relative;
}
.table-draggable-handle {
  transform: none !important;
  position: absolute;
  height: 100% !important;
  left: auto !important;
  right: -5px;
  bottom: 0;
  cursor: col-resize;
  touch-action: none;
  border: none;
}
</style>


