<template>
    <div class="related-part-detail-form" style="overflow: auto">
        <a-spin tip="加载中..." :spinning="spinning" size="large">
            <div class="tableTip">
                <span>
                    <a-icon type="tag" style="margin-right: 8px" />{{
                        `共查询到${total || 0}条数据`
                    }}
                </span>
                <span
                    @click="manualRefesh"
                    style="color: #409eff; margin-left: 10px; cursor: pointer"
                >
                    <a-icon type="sync" style="margin-right: 5px" />刷新
                </span>
                <span
                    >（注：浅红色背景色表示调整后期末余额与账龄合计值不等）</span
                >
                <a-anchor
                    :affix="false"
                    wrapperClass="anchorClass"
                    :get-current-anchor="getCurrentAnchor"
                >
                    <template>
                        <a-anchor-link
                            v-for="(item, idx) in columnsList"
                            :key="idx"
                            :href="`#${idx}`"
                            :title="`账龄区间${idx + 1}`"
                        ></a-anchor-link>
                    </template>
                </a-anchor>
            </div>
            <template v-for="(item, idx) in columnsList">
                <!-- <a-card :bordered ="false"> -->
                <a-table
                    :id="idx"
                    :key="idx"
                    :columns="columns"
                    :data-source="dataList[idx]"
                    bordered
                    :scroll="{ y: 'calc(100% - 55px)', x: 'scollXWidth' }"
                    :pagination="false"
                    rowKey="autoId"
                    :components='components'
                >
                    <template slot="badDebt" slot-scope="text, record">
                        <editable-cell
                            :text="text && text != 0 ? String(text) : ''"
                            :formatter="formatter"
                            @change="onCellChange(record, 'badDebt', $event)"
                        />
                    </template>
                    <template slot="deferredTax" slot-scope="text, record">
                        <editable-cell
                            :text="text && text != 0 ? String(text) : ''"
                            :formatter="formatter"
                            @change="
                                onCellChange(record, 'deferredTax', $event)
                            "
                        />
                    </template>
                </a-table>
                <page
                    :key="idx + 100"
                    :data="dataList"
                    :idx="idx"
                    :currentData="dataListCopy[idx] ? dataListCopy[idx] : []"
                    :dataListCopy="dataListCopy"
                    @pageData="pageData"
                    @initData="initData"
                ></page>
            </template>

            <a-modal
                v-model="searchDialogVisible"
                title="查询"
                ok-text="确认"
                cancel-text="取消"
                @ok="onSubmitSeachItem"
                @cancel="cancelSearch"
            >
                <a-form-model
                    :model="searchForm"
                    :label-col="labelCol"
                    :wrapper-col="wrapperCol"
                >
                    <a-form-model-item label="关联方名称">
                        <a-input
                            v-model="searchForm.merchantName"
                            placeholder="请输入"
                        />
                    </a-form-model-item>
                    <!-- <a-form-model-item label="统一信用代码">
                        <a-input v-model="searchForm.customerUscc" placeholder="请输入"/>
                    </a-form-model-item> -->
                </a-form-model>
            </a-modal>
        </a-spin>
    </div>
</template>
<script>
import XEUtils from 'xe-utils';
import { formatter } from '../libs/utils/formatter';
import session from '../data/session/sessionUtils';
import { trim } from 'xe-utils/methods';
import _ from 'lodash';
import page from '../components/page';

// import Vue from 'vue';
// import VueDraggableResizable from 'vue-draggable-resizable';
// Vue.component('vue-draggable-resizable', VueDraggableResizable);


const EditableCell = {
    template: `
        <div class="editable-cell">
            <div v-if="editable" class="editable-cell-input-wrapper">
                <a-input :value="value" @change="handleChange" @pressEnter="check" /><a-icon
                    type="check"
                    class="editable-cell-icon-check"
                    @click="check"
                />
                </div>
                <div v-else class="editable-cell-text-wrapper">
                {{formatter.commafy(value)}}
                <a-icon type="edit" class="editable-cell-icon" @click="edit" />
            </div>
        </div>
    `,
    props: {
        text: String,
        formatter: Object,
    },
    data() {
        return {
            value: XEUtils.commafy(this.text, { digits: 2 }),
            editable: false,
        };
    },
    methods: {
        handleChange(e) {
            const regExp = /^(-?[1-9]\d+|-?\d?)(\.(\d){0,2})?$/; //浮点数保留2位小数点
            const numExp = /^-?[1-9]\d*$/; //整数

            if (String(e.target.value).indexOf(',') != -1) {
                //去除千分位
                e.target.value = e.target.value.replace(/,/gi, '');
            }
            e.target.value = XEUtils.trim(e.target.value);
            if (
                (!numExp.test(e.target.value) &&
                    !regExp.test(e.target.value)) ||
                (numExp.test(e.target.value) && e.target.value.length > 30) ||
                (regExp.test(e.target.value) && e.target.value.length > 31)
            ) {
                this.$message.warning(
                    '金额长度为30位数(包含小数点后2位数)！请重新输入'
                );
                this.value = '0.00';
                return;
            } else {
                this.value = e.target.value;
            }
        },
        check() {
            this.editable = false;
            this.$emit('change', this.value);
        },
        edit() {
            this.editable = true;
        },
    },
};
export default {
    name: 'relatedPartyDetailForm',
    components: {
        page,
    },
     computed: {
        rowSelection() {
            return {
                onChange: (selectedRowKeys, selectedRows) => {},
            };
        },
    },
    data() {
        this.components = {
        header: {
        cell: (h, props, children) => {
          const { key, ...restProps } = props
          const col = this.columns.find(col => {
            const k = col.dataIndex || col.key
            return k === key
          })
 
          if (!col || !col.width) {
            return h('th', { ...restProps }, [...children])
          }
 
          const dragProps = {
            key: col.dataIndex || col.key,
            class: 'table-draggable-handle',
            attrs: {
              w: 10,
              x: col.width,
              z: 1,
              axis: 'x',
              draggable: true,
              resizable: false
            },
            on: {
              dragging: (x, y) => {
                col.width = Math.max(x, 1)
              }
            }
          }
          const drag = h('vue-draggable-resizable', { ...dragProps })
          return h('th', { ...restProps, class: 'resize-table-th' }, [...children, drag])
        }
      }
    };
        return {
            columns: [
                {
                    title: '序号',
                    dataIndex: 'index',
                    key: 'index',
                    align: 'center',
                    width: 50,
                    customRender: (text, record, index) => `${index + 1}`,
                },
                {
                    title: '关联方名称',
                    dataIndex: 'mergeCustomerName',
                    ellipsis: true,
                    width: 300,
                    align: 'left',
                    customCell: this.cellStyle,
                },
                {
                    title: '一级科目',
                    dataIndex: 'oneSubjectName',
                    width: 200,
                    ellipsis: true,
                    align: 'left',
                    customCell: this.cellStyle,
                },
                {
                    title: '科目全路径',
                    dataIndex: 'subjectFullName',
                    width: 240,
                    ellipsis: true,
                },
                {
                    title: '客商编号',
                    dataIndex: 'merchantId',
                    ellipsis: true,
                    width: 170,
                },
                {
                    title: '客商名称',
                    dataIndex: 'merchantName',
                    ellipsis: true,
                    width: 250,
                },
                {
                    title: '期初余额',
                    dataIndex: 'remain',
                    width: 170,
                    align: 'right',
                    ellipsis: true,
                    customRender: formatter.commafy,
                },
                {
                    title: '借方发生额',
                    width: 170,
                    dataIndex: 'debitOcc',
                    align: 'right',
                    ellipsis: true,
                    customRender: formatter.commafy,
                },
                {
                    title: '贷方发生额',
                    width: 170,
                    dataIndex: 'creditOcc',
                    align: 'right',
                    ellipsis: true,
                    customRender: formatter.commafy,
                },
                {
                    title: '期末余额',
                    width: 170,
                    dataIndex: 'balance',
                    align: 'right',
                    ellipsis: true,
                    customRender: formatter.commafy,
                },
                {
                    title: '借方调整',
                    width: 170,
                    dataIndex: 'debitAdjust',
                    align: 'right',
                    ellipsis: true,
                    customRender: formatter.commafy,
                },
                {
                    title: '贷方调整',
                    width: 170,
                    dataIndex: 'creditAdjust',
                    align: 'right',
                    ellipsis: true,
                    customRender: formatter.commafy,
                },
                {
                    title: '调整后期末余额',
                    width: 170,
                    dataIndex: 'approvalBalance',
                    align: 'right',
                    ellipsis: true,
                    customRender: formatter.commafy,
                    customCell: this.cellStyle,
                },
                {
                    title: '账龄',
                    dataIndex: 'accountAge',
                    width: 550,
                    children: [
                        {
                            title: '1年内',
                            width: 170,
                            dataIndex: 'ara01',
                            align: 'right',
                            ellipsis: true,
                            customRender: formatter.commafy,
                        },
                        {
                            title: '1-2年',
                            width: 170,
                            dataIndex: 'ara02',
                            align: 'right',
                            ellipsis: true,
                            customRender: formatter.commafy,
                        },
                        {
                            title: '2-3年',
                            width: 170,
                            dataIndex: 'ara03',
                            align: 'right',
                            ellipsis: true,
                            customRender: formatter.commafy,
                        },
                        {
                            title: '3-4年',
                            width: 170,
                            dataIndex: 'ara04',
                            align: 'right',
                            ellipsis: true,
                            customRender: formatter.commafy,
                        },
                        {
                            title: '4-5年',
                            width: 170,
                            dataIndex: 'ara05',
                            align: 'right',
                            ellipsis: true,
                            customRender: formatter.commafy,
                        },
                        {
                            title: '5年以上',
                            width: 170,
                            dataIndex: 'ara06',
                            align: 'right',
                            ellipsis: true,
                            customRender: formatter.commafy,
                        },
                    ],
                },
                {
                    title: '坏账准备',
                    width: 170,
                    dataIndex: 'badDebt',
                    align: 'right',
                    ellipsis: true,
                    scopedSlots: { customRender: 'badDebt' },
                },
                {
                    title: '递延所得税资产',
                    width: 170,
                    dataIndex: 'deferredTax',
                    align: 'right',
                    ellipsis: true,
                    scopedSlots: { customRender: 'deferredTax' },
                },
            ],
            data: [],
            num: 0,
            searchDialogVisible: false,
            searchForm: {
                merchantName: null,
                // customerUscc:null,
            },
            queryParams: {
                sqlId: 'DG40017',
                start: 0,
                limit: 10,
            },
            labelCol: { span: 6 },
            wrapperCol: { span: 13 },
            spinning: false,
            formatter,
            columnsList: [],
            dataList: [],
            total: 0,
            dataListCopy: [],
            pagination: {
                current: 1,
                pageSize: 30,
                showSizeChanger: true,
                pageSizeOptions: ['10', '20', '30', '40', '50'],
                showQuickJumper: false,
                size: 'small',
                total: 0,
                showTotal: () => `共${this.pagination.total}条`,
                change: true,
                onShowSizeChange: (current, pageSize) => {
                    this.queryParams.start = current - 1;
                    this.queryParams.limit = pageSize;
                    this.pagination.pageSize = pageSize;
                    // this.handleQueryRelatedPartDetailFormData();
                },
                onChange: (current, size) => {
                    //start 0 -10 -20 -30
                    console.log('current', current, this.pagination.index);
                    this.queryParams.start = (current - 1) * size;
                    this.queryParams.limit = this.pagination.pageSize;
                    this.pagination.current = current;
                    // this.handleQueryRelatedPartDetailFormData();
                },
            },
        };
    },
    methods: {
        onCellChange(record, name, value) {
            // 单元格修改后实时保存
            console.log('record', record);
            const params = {
                param1: record.autoId || null,
                param2: name === 'badDebt' ? value : null,
                param3: name === 'deferredTax' ? value : null,
            };
            this.$sacpApis
                .saveContactsDetail(JSON.parse(JSON.stringify(params)))
                .then((res) => {
                    if (!res.success)
                        return this.$message.error(res.resultInfo.statusText);
                    this.handleQueryRelatedPartDetailFormData();
                })
                .catch((err) => this.$message.error(err))
                .finally(() => (this.spinning = false));
        },
        //页面表格查询接口
        handleQueryRelatedPartDetailFormData() {
            const { curCustomerId, curProjectId ,curAccYear} = session.findUserInfo();
            const { param3, start, limit } = this.queryParams;
            const param = XEUtils.trim(param3);
            this.spinning = true;
            let obj = {
                sqlId: 'DG40017',
                lockCustomerId: curCustomerId,
                lockProjectId: curProjectId,
                lockYyyy:curAccYear,
                // start: param ? 0 : start,
                // limit: param ? 10 : limit,
                param3: param,
            };
            this.$sacpApis
                .queryMerchantRelatedDetail(JSON.parse(JSON.stringify(obj)))
                .then((res) => {
                    if (res.success) {
                        const obj = res.data[0];
                        if (res.data && obj) {
                            let index = 0;
                            for (const key in obj) {
                                const item = obj[key];
                                // 剪切成数组后传给该函数
                                const handleList = this.getAccountAgeName(
                                    key.split(';')[0].split(',')
                                );
                                let children = this.columns[13].children;
                                const accountAge = _.cloneDeep(children);
                                accountAge.forEach((x, idx) => {
                                    x.title = handleList[idx];
                                });
                                this.$set(
                                    this.columns[13],
                                    'children',
                                    accountAge
                                );
                                this.columnsList[index] = _.cloneDeep(
                                    this.columns
                                );
                                this.dataList[index] = item;
                                index++;
                                // this.pagination.total = res.limit;
                                this.total += item.length;
                            }
                            this.dataListCopy = JSON.parse(
                                JSON.stringify(this.dataList)
                            );
                            this.data = res.data;
                        } else {
                            this.data = [];
                            this.columnsList[1] = this.columns;
                            this.dataList = [];
                            this.pagination.total = 0;
                        }
                    } else {
                        return this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.spinning = false;
                    this.cancelSearch();
                });
        },
        initRelatedPartyForm() {
            //初始化功能接口
            this.spinning = true;
            this.$sacpApis
                .initContactsDetail()
                .then((res) => {
                    if (!res.success)
                        return this.$message.error(res.resultInfo.statusText);
                    this.$message.success('初始化成功!');
                    this.handleQueryRelatedPartDetailFormData();
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.spinning = false;
                });
        },
        openSearchDialog() {
            this.searchDialogVisible = true;
        },
        cancelSearch() {
            this.searchDialogVisible = false;
            // this.queryParams.param3=undefined;
            Object.keys(this.searchForm).forEach(
                (key) => (this.searchForm[key] = null)
            );
        },
        onSubmitSeachItem() {
            this.queryParams.start = 0;
            this.queryParams.limit = 10;
            XEUtils.set(
                this.queryParams,
                'param3',
                XEUtils.trim(this.searchForm.merchantName)
            );
            // XEUtils.set(this.queryParams,'param5',this.searchForm.customerUscc);
            this.handleQueryRelatedPartDetailFormData();
        },
        // 处理表头账龄
        getAccountAgeName(arr) {
            var result = [];
            var accountArr = arr[0].split(':');
            result.push(accountArr[1] + '以内');
            var tmp = accountArr[1];
            for (var i = 1; i < arr.length; i++) {
                accountArr = arr[i].split(':');
                result.push(tmp + ' -' + accountArr[1]);
                tmp = accountArr[1];
            }
            result.push(tmp + '以上');
            // console.log('result',result)
            return result;
        },
        pageData(val) {
            this.dataList[val.idx] = val.pageData;
            this.$forceUpdate();
        },
        initData(val) {
            this.dataList = val;
        },
        cellStyle(record) {
            if (record.approvalBalance != record.araTotal) {
                return {
                    style: {
                        'background-color': 'oldlace',
                    },
                };
            }
        },
        getCurrentAnchor() {
            return '#0';
        },
        manualRefesh() {
            this.handleQueryRelatedPartDetailFormData();
        },
    },
    created() {
        this.handleQueryRelatedPartDetailFormData();
    },
};
</script>

<style lang="scss">
.compare {
    background-color: #f4aeae99;
}
.page {
    text-align: right;
    // margin-top: -50px;
    margin-bottom: 20px;
}

.tableTip {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 0;

    .anchorClass {
        display: inline-block;
        background-color: initial;
        margin-left: 30px;
        .ant-anchor {
            display: inline-flex;
            padding: 0 2px;
            & > div {
                padding: 4px 7px;
            }
            & > div:first-child {
                display: none;
            }
        }
    }
}
.resize-table-th {
  position: relative;
}
.table-draggable-handle {
  transform: none !important;
  position: absolute;
  height: 100% !important;
  left: auto !important;
  right: -5px;
  bottom: 0;
  cursor: col-resize;
  touch-action: none;
  border: none;
}
</style>


