<template>
    <div>
        <a-modal
            title="科目编号"
            class="assSubjectDialog"
            width="60%"
            :visible="subjectVisible"
            :confirm-loading="confirmLoading"
            :footer="null"
            @ok="subjectHandleOk"
            @cancel="subjectCancel"
        >
            <a-tree
                :checkedKeys="checkedKeys"
                checkable
                :expanded-keys="expandedKeys"
                :selected-keys="selectedKeys"
                :tree-data="subjectTree"
                :checkStrictly="checkStrictly"
                :replace-fields="{}"
                @expand="onExpand"
                @check="checkSubject"
            />
        </a-modal>

        <a-modal
            title="辅助核算"
            class="assSubjectDialog"
            width="60%"
            :visible="checkVisible"
            :confirm-loading="confirmLoading"
            :footer="null"
            @ok="handleOk"
            @cancel="handleCancel"
        >
            <a-spin tip="加载中..." :spinning="checkSpinning">
                <a-tree
                    v-if="assitemIsBankTree && assitemIsBankTree.length"
                    :checkedKeys="assitemIsBankCheckedKeys"
                    checkable
                    :tree-data="assitemIsBankTree"
                    :expanded-keys="assitemIsBankExpandedKeys"
                    :selected-keys="assitemIsBankSelectedKeys"
                    :checkStrictly="checkStrictly"
                    @expand="assitemIsBankOnExpand"
                    @check="check"
                />
                <a-empty v-else />
            </a-spin>
        </a-modal>

        <a-modal
            title="添加"
            width="90%"
            :visible="addTableVisible"
            :confirm-loading="addTableConfirmLoading"
            :footer="null"
            @ok="addTableOk"
            @cancel="addTableCancel"
        >
            <a-spin :tip="tip" :spinning="spinning">
                <a-form-model
                    layout="inline"
                    :model="formInline"
                    style="margin-bottom: 20px"
                >
                    <a-form-model-item label="科目编号">
                        <a-input
                            ref="subject"
                            allow-clear
                            v-model="formInline.subjectName"
                            placeholder="科目编号"
                            @focus="subjectDialog"
                        >
                        </a-input>
                    </a-form-model-item>
                    <a-form-model-item label="辅助核算">
                        <a-input
                            ref="check"
                            allow-clear
                            v-model="formInline.checkedName"
                            type="test"
                            placeholder="辅助核算"
                            :disabled="querBut"
                            @focus="checkDialog"
                        >
                        </a-input>
                    </a-form-model-item>
                    <a-form-model-item label="">
                        <a-button type="primary" @click="queryMerchantAdd"
                            >查询
                        </a-button>
                    </a-form-model-item>
                </a-form-model>
                <a-table
                    :columns="columns"
                    :data-source="data"
                    bordered
                    rowKey="merchantId"
                    :pagination="pagination"
                    :scroll="{ y: 350, x: 'scollXWidth' }"
                >
                    <template slot="autoMatch" slot-scope="text, record">
                        <a-button
                            type="primary"
                            size="small"
                            title="添加"
                            class="del-match-btn"
                            @click="saveMerchantBysingle(record)"
                        >
                            添加
                        </a-button>
                    </template>
                </a-table>
            </a-spin>
        </a-modal>
    </div>
</template>
<script>
import session from '../data/session/sessionUtils.js';
import { formatter } from '../libs/utils/formatter';
// import { buildResizeableTitle } from '../plugins/vueDraggableResizable';

const columns = [
    {
        title: '操作',
        dataIndex: 'autoMatch',
        width: '80px',
        scopedSlots: { customRender: 'autoMatch' },
        fixed: 'left',
    },
    {
        title: '客商编号',
        dataIndex: 'merchantId',
        ellipsis: true,
        width: 150,
    },
    {
        title: '客商名称',
        dataIndex: 'merchantName',
        ellipsis: true,
        width: 220,
    },
    {
        title: '科目编号',
        dataIndex: 'subjectId',
        ellipsis: true,
        width: 140,
    },
    {
        title: '期初余额',
        dataIndex: 'remain',
        align: 'right',
        width: 170,
        customRender: formatter.commafy,
    },
    {
        title: '借方发生额',
        dataIndex: 'debitOcc',
        align: 'right',
        width: 170,
        customRender: formatter.commafy,
    },
    {
        title: '贷方发生额',
        dataIndex: 'creditOcc',
        align: 'right',
        width: 170,
        customRender: formatter.commafy,
    },
    {
        title: '期末余额',
        dataIndex: 'balance',
        align: 'right',
        width: 170,
        customRender: formatter.commafy,
    },
];

export default {
    name: 'add',
    data() {
        // const ResizeableTitle = buildResizeableTitle(columns);
        return {
            // components: {
            //     header: {
            //         cell: ResizeableTitle,
            //     },
            // },
            visible: false,
            confirmLoading: false,
            checkStrictly: true,
            tip: '加载中...',
            checkedKeys: [],
            expandedKeys: [],
            selectedKeys: [],
            subjectTree: [],
            customerInformation: [],
            assitemIsBankExpandedKeys: [],
            assitemIsBankSelectedKeys: [],
            assitemIsBankTree: [],
            assitemIsBankCheckedKeys: [],
            currentRow: {},
            columns,
            data: [],
            addTableVisible: false,
            addTableConfirmLoading: false,
            querBut: true,
            querCheckyBut: true,
            formInline: {
                checkedKeys: '',
                checkedName: '',
                subjectKeys: '',
                subjectName: '',
            },
            checkVisible: false,
            subjectVisible: false,
            checkSpinning: false,
            spinning: false,
            queryParams: {
                start: 0,
                limit: 10,
            },
            pagination: {
                current: 1,
                pageSize: 10,
                showSizeChanger: true,
                pageSizeOptions: ['10', '20', '30', '40', '50'],
                onShowSizeChange: (current, pageSize) => {
                    this.queryParams.start = 0;
                    this.queryParams.limit = pageSize;
                    this.pagination.pageSize = pageSize;
                    this.queryMerchantAdd();
                },
                showQuickJumper: false,
                size: 'small',
                total: 0,
                showTotal: () => `共${this.pagination.total}条`,
                change: true,
                onChange: (current, size) => {
                    //start 0 -10 -20 -30
                    this.queryParams.start = (current - 1) * size;
                    this.queryParams.limit = this.pagination.pageSize;
                    this.pagination.current = current;
                    this.queryMerchantAdd();
                },
            },
        };
    },
    watch: {
        'formInline.subjectName': {
            handler(val, oldVal) {
                if (val && val.length) {
                    this.querBut = false;
                } else {
                    this.querBut = true;
                    this.formInline.subjectKeys = '';
                    this.checkedKeys.checked = [];
                }
                this.formInline.checkedKeys = '';
                this.formInline.checkedName = '';
                this.assitemIsBankCheckedKeys.checked = [];
            },
            deep: true,
        },
        'formInline.checkedName': {
            handler(val, oldVal) {
                if (val && val.length) {
                    this.querCheckyBut = false;
                } else {
                    this.querCheckyBut = true;
                    this.formInline.checkedKeys = '';
                    this.assitemIsBankCheckedKeys.checked = [];
                }
            },
        },
    },
    methods: {
        showModal() {
            this.addTableVisible = true;
        },
        handleOk(e) {
            this.confirmLoading = true;
            setTimeout(() => {
                this.checkVisible = false;
                this.confirmLoading = false;
            }, 2000);
        },
        handleCancel(e) {
            this.checkVisible = false;
        },
        addTableOk(e) {
            this.addTableConfirmLoading = true;
            setTimeout(() => {
                this.addTableVisible = false;
                this.addTableConfirmLoading = false;
            }, 2000);
        },
        addTableCancel(e) {
            this.addTableVisible = false;
            this.formInline = {
                checkedKeys: '',
                checkedName: '',
                subjectKeys: '',
                subjectName: '',
            };
            this.data = [];
            this.checkedKeys = [];
            this.expandedKeys = [];
            this.selectedKeys = [];
            this.assitemIsBankExpandedKeys = [];
            this.assitemIsBankSelectedKeys = [];
            this.assitemIsBankCheckedKeys = [];
        },
        onExpand(expandedKeys) {
            this.expandedKeys = expandedKeys;
        },
        // 查科目核对
        findAssitemTypeTree() {
            // this.subjectVisible = false;
            this.checkSpinning = true;
            const { curCustomerId, curAccYear } = this.customerInformation;
            let params = {
                lockCustomerId: curCustomerId,
                lockYyyy: curAccYear,
                param11: this.formInline.subjectKeys,
                param20: 1,
            };
            this.$sacpApis
                .findAssitemTypeTree(params)
                .then((res) => {
                    if (res.success) {
                        this.checkSpinning = false;
                        res.data.map((item) => {
                            item.isLeaf =
                                (item.parentId === '' && !item.isLeaf) ||
                                !item.isLeaf
                                    ? false
                                    : true;
                        });
                        this.assitemIsBankTree = res.data;
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => {
                    this.checkSpinning = false;
                    // this.assitemIsBankExpandedKeys=[];
                });
        },
        assitemIsBankOnExpand(expandedKeys, { expanded: bool, node }) {
            this.assitemIsBankExpandedKeys = expandedKeys;
        },
        // 查表
        queryMerchantAdd() {
            this.tip = '加载中...';
            const checked = this.formInline.subjectKeys;
            if (!checked) {
                return this.$message.error('请选择科目!');
            }
            this.addTableVisible = true;
            this.spinning = true;
            const { start, limit } = this.queryParams;
            const param = {
                start,
                limit,
                param1: checked,
                param2: this.formInline.checkedKeys,
            };
            this.$sacpApis
                .queryMerchantAdd(param)
                .then((res) => {
                    if (res.success) {
                        // console.log('res.data', res.data);
                        this.data = res.data;
                        this.pagination.total = res.totalCount;
                        this.spinning = false;
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.spinning = false;
                });
        },
        // 添加
        saveMerchantBysingle(record) {
            this.tip = '添加中...';
            this.spinning = true;
            const param = {
                param1: record.merchantType,
                param2: record.merchantId,
                param3: record.subjectId,
            };
            this.$sacpApis
                .saveMerchantBysingle(param)
                .then((res) => {
                    if (res.success) {
                        this.$message.success('添加成功!');
                        this.spinning = false;
                        // this.queryBankAdd()
                        this.$emit('refresh', true);
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    console.log('sss')
                    this.$message.error(err);
                })
                .finally(() => {});
        },
        subjectDialog() {
            this.subjectTree = session.findMerchantRangeTree();
            this.subjectVisible = true;
            this.$refs.subject.blur();
        },
        checkDialog() {
            this.checkVisible = true;
            this.customerInformation = session.findUserInfo();
            this.$refs.check.blur();
            this.findAssitemTypeTree();
        },
        subjectHandleOk() {
            this.subjectVisible = false;
        },
        subjectCancel() {
            this.subjectVisible = false;
        },
        checkSubject(checkedKeys, e) {
            const { checked, checkedNodes, node, event } = e;
            // console.log(node);
            if (checked) {
                this.checkedKeys = {
                    checked: [node.eventKey],
                    halfChecked: [],
                };
                this.formInline.subjectKeys = node.eventKey;
                this.formInline.subjectName = node.title;
            } else {
                this.checkedKeys = {
                    checked: [],
                    halfChecked: [],
                };
                this.formInline.subjectKeys = '';
                this.formInline.subjectName = '';
            }
        },
        check(checkedKeys, e) {
            const { checked, checkedNodes, node, event } = e;
            if (checked) {
                this.assitemIsBankCheckedKeys = {
                    checked: [node.eventKey],
                    halfChecked: [],
                };
                this.formInline.checkedKeys = node.eventKey;
                this.formInline.checkedName = node.title;
            } else {
                this.assitemIsBankCheckedKeys = {
                    checked: [],
                    halfChecked: [],
                };
                this.formInline.checkedKeys = '';
                this.formInline.checkedName = '';
            }
        },
    },
    mounted() {},
};
</script>

<style lang="scss" scoped>
.tree {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    & > div {
        flex-basis: 50%;
    }
    .ant-tree {
        height: 100%;
        overflow-y: auto;
    }
}
</style>



