<template>
    <div>
        <div v-if="showInfluenceTable()">
            <a-spin tip="加载中..." size="large" :spinning="influenceLoading">
                <div class="title">
                    <h3>上年交易影响</h3>
                    <a-button type="primary" @click="influenceEdit"
                        >编辑</a-button
                    >
                </div>
                <grid-mock-table
                    border
                    v-model="influenceTableData"
                    :rowDragable="false"
                    :colDragable="false"
                    :rowDragableState="false"
                    :editIcon="true"
                >
                    <template
                        :slot="`col-${edit.prop}`"
                        slot-scope="{
                            row,
                            col,
                            curCol,
                            colIndex,
                            rowIndex,
                            columns,
                        }"
                        v-for="(edit, tx) in cellShow"
                    >
                        <div :key="tx" class="slot-div">
                            <a-select
                                v-if="edit.pageFieldType === '3'"
                                v-model="row[curCol]"
                                show-search
                                :default-value="col"
                                placeholder="请选择"
                                option-filter-prop="children"
                                style="width: 200px"
                            >
                                <a-select-option
                                    :value="item.value"
                                    v-for="(item, idx) in selectData(columns)"
                                    :key="idx"
                                    >{{ item.label }}</a-select-option
                                >
                            </a-select>
                        </div>
                    </template>
                </grid-mock-table>
            </a-spin>
        </div>

        <a-spin tip="加载中..." size="large" :spinning="loading">
            <div class="title">
                <!-- <h3>{{ publicData.title }}</h3> -->
                <span>
                    <a-button type="danger" ghost v-if="checkBut" @click="check"
                        >校验不平</a-button
                    >
                </span>
                <a-button type="primary" v-if="addButState" @click="edit"
                    >新增</a-button
                >
            </div>
            <grid-mock-table
                border
                v-model="tableData"
                :rowDragable="false"
                :colDragable="false"
                :rowDragableState="false"
                :editIcon="true"
            >
                <template
                    :slot="`col-${edit.prop}`"
                    slot-scope="{
                        row,
                        col,
                        curCol,
                        colIndex,
                        rowIndex,
                        columns,
                        record,
                    }"
                    v-for="(edit, tx) in cellShow"
                >
                    <div :key="tx" class="slot-div">
                        <a-select
                            v-if="edit.pageFieldType === '3' && row.uscc"
                            disabled
                            v-model="row[curCol]"
                            show-search
                            :default-value="col"
                            placeholder="请选择"
                            option-filter-prop="children"
                            style="width: 200px"
                        >
                            <a-select-option
                                :value="item.value"
                                v-for="(item, idx) in selectData(columns)"
                                :key="idx"
                                >{{ item.label }}</a-select-option
                            >
                        </a-select>
                        <div v-else-if="edit.pageFieldType === '5'">
                            {{ col ? col.slice(0, 7) : '' }}
                        </div>
                    </div>
                </template>
                <template
                    slot="col-mergeCustomerName"
                    slot-scope="{ row, col, curCol }"
                >
                    <div
                        v-if="
                            curCol === 'mergeCustomerName' &&
                            col != '合计' &&
                            col != '小计'
                        "
                    >
                        <a @click.stop="mergeCustomerClick(row)">
                            {{ row.mergeProjectName }}
                        </a>
                        <a-tooltip placement="topLeft" title="企业微信提醒">
                            <a-button
                                v-if="row.rowspan"
                                type="primary"
                                icon="wechat"
                                size="small"
                                :loading="row.submitLoading"
                                @click="sendCheckMessageConfirm(row)"
                            />
                        </a-tooltip>
                    </div>
                    <span v-else>
                        {{ col }}
                    </span>
                </template>
            </grid-mock-table>

            <drage-table-dialog
                ref="drageTableDialog"
                :mergeCustomerName="publicData.mergeCustomerName"
                :dialogTitle="publicData.title"
                :userinfo="userinfo"
                :publicParam="publicParam"
                :butState="butState"
            ></drage-table-dialog>
            <influence-dialog ref="influenceDialog"></influence-dialog>
            <custom-dialog ref="customDialog"></custom-dialog>
            <!-- 校验  -->
            <a-modal
                class="drageDialog"
                title="校验"
                :visible="checkVisible"
                :confirm-loading="checkConfirmLoading"
                :destroyOnClose="true"
                width="65%"
                @ok="saveTradeFromEntry"
                okText="保存"
                @cancel="checkCancel"
                :footer="null"
            >
                <a-spin size="large" :spinning="lookSpinning" tip="加载中...">
                    <a-table
                        :columns="checkColumns"
                        :data-source="checkData"
                        bordered
                        :scroll="{ y: 'calc(100vh - 300px)', x: 'scollXWidth' }"
                        row-key="uscc"
                        :footer="null"
                        :pagination="false"
                        :components="components"
                    >
                    </a-table>
                </a-spin>
            </a-modal>
        </a-spin>
    </div>
</template>

<script>
import { throttle } from 'lodash';
import GridMockTable, { datadrag } from '../../GridMockTable/GridMockTable.vue';
import drageTableDialog from './drageTableDialog';
import influenceDialog from './influenceDialog';
import customDialog from './customDialog';
import sessionUtils from '../../data/session/sessionUtils.js';
// import { mixin } from './mixins/drageTable';

export default {
    // mixins: [mixin],
    props: {
        publicData: Object,
        curTableData: Array,
        curInfluenceTableData: Array,
        editCell: Array,
        loading: Boolean,
        influenceLoading: Boolean,
        publicParam: Object,
        userinfo: Object,
        butState: Boolean,
    },
    // inject: ['userinfo', 'publicParam'],
    data() {
        this.components = {
            header: {
                cell: (h, props, children) => {
                    const { key, ...restProps } = props;
                    //   console.log('ResizeableTitle', key)
                    // console.log(this.columns);
                    const col = this.columns.find((col) => {
                        const k = col.dataIndex || col.key;
                        return k === key;
                    });

                    if (!col || !col.width) {
                        return h('th', { ...restProps }, [...children]);
                    }

                    const dragProps = {
                        key: col.dataIndex || col.key,
                        class: 'table-draggable-handle',
                        attrs: {
                            w: 10,
                            x: col.width,
                            z: 1,
                            axis: 'x',
                            draggable: true,
                            resizable: false,
                        },
                        on: {
                            dragging: (x, y) => {
                                col.width = Math.max(x, 1);
                            },
                        },
                    };
                    const drag = h('vue-draggable-resizable', { ...dragProps });
                    return h('th', { ...restProps, class: 'resize-table-th' }, [
                        ...children,
                        drag,
                    ]);
                },
            },
        };
        return {
            tableData: [],
            influenceTableData: [],
            assemblyCode: '0',
            cellShow: [],
            addButState: false,
            selectTreeData: {
                stock: [],
                AdministrativeExpenses: [],
                mainBusinessIncome: [],
                otherBusinessIncome: [],
                fixedAssets: [],
                intangibleAssets: [],
                mergeCustomerName: [],
                purpose: [],
                depreciation: [],
                assetLiquidationStatus: [],
                purchaseMethod: [],
            },
            checkVisible: false,
            checkColumns: [
                {
                    title: '关联方名称',
                    dataIndex: 'mergeCustomerName',
                    key: 'mergeCustomerName',
                },
                {
                    title: '',
                    children: [
                        {
                            title: '销售-采购',
                            dataIndex: 'amountA',
                            key: 'amountA',
                            align: 'right',
                        },
                        {
                            title: '采购-销售',
                            dataIndex: 'amountB',
                            key: 'amountB',
                            align: 'right',
                        },
                    ],
                },
            ],
            checkData: [],
            checkConfirmLoading: false,
            checkBut: false,
            lookSpinning: false,
        };
    },
    components: {
        GridMockTable,
        drageTableDialog,
        influenceDialog,
        customDialog,
    },
    watch: {
        curTableData: {
            handler(val, oldVal) {
                if (val) {
                    this.tableData = val;
                    this.$forceUpdate();
                }
            },
            deep: true,
            immediate: true,
        },
        curInfluenceTableData: {
            handler(val, oldVal) {
                if (val) {
                    // this.loading = false;
                    this.influenceTableData = val;
                }
            },
            deep: true,
            immediate: true,
        },
        editCell: {
            handler(val, oldVal) {
                if (val) {
                    this.cellShow = [];
                    val.forEach((x) => {
                        if (x.pageFieldType == 3 || x.pageFieldType == 5) {
                            this.cellShow.push(x);
                        }
                    });
                }
            },
            deep: true,
            immediate: true,
        },
        userinfo: {
            handler(val, oldVal) {
                if (typeof val == 'object') {
                    this.checkColumns[1].title = val.curCustomerName;
                }
            },
        },
    },
    methods: {
        customShow() {
            this.$refs.customDialog.customShow();
        },
        handlerData(keys) {
            this.assemblyCode = keys;
        },
        // 分类选择
        queryChildSubjects(param) {
            // this.spinning = true;
            this.$sacpApis
                .queryChildSubjects({ param1: param, ...this.publicParam })
                .then(({ data }) => {
                    if (data && data.length) {
                        // console.log('分类', data);
                        const treeData = this.selectTreeData;
                        switch (param) {
                            case '存货':
                                treeData.stock = data;
                                break;
                            case '主营业务收入':
                                treeData.mainBusinessIncome = data;
                                break;
                            case '其他业务收入':
                                treeData.otherBusinessIncome = data;
                                break;
                            case '固定资产':
                                treeData.fixedAssets = data;
                                break;
                            case '无形资产':
                                treeData.intangibleAssets = data;
                                break;
                        }
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => (this.spinning = false));
        },
        // 用途下拉
        findAccSubjectType() {
            if (!this.userinfo) return;
            const { curCustomerId, curAccYear } = this.userinfo;
            const param = {
                lockCustomerId: curCustomerId,
                lockYyyy: curAccYear,
                ...this.publicParam,
            };
            this.$sacpApis
                .findAccSubjectType(param)
                .then((res) => {
                    // console.log('用途下拉', res);
                    if (res.success) {
                        this.selectTreeData.purpose = res.data;
                    } else {
                        return this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => (this.spinning = false));
        },
        queryDictionary() {
            const treeData = this.selectTreeData;
            this.$sacpApis
                .queryDictionary()
                .then((res) => {
                    // debugger
                    if (res) {
                        treeData.depreciation = [];
                        treeData.assetLiquidationStatus = [];
                        treeData.purchaseMethod = [];
                        for (const key in res['固定资产折旧方法']) {
                            if (
                                Object.hasOwnProperty.call(
                                    res['固定资产折旧方法'],
                                    key
                                )
                            ) {
                                treeData.depreciation.push({
                                    label: res['固定资产折旧方法'][key],
                                    value: key,
                                });
                            }
                        }

                        for (const key in res['资产清理状态']) {
                            if (
                                Object.hasOwnProperty.call(
                                    res['资产清理状态'],
                                    key
                                )
                            ) {
                                treeData.assetLiquidationStatus.push({
                                    label: res['资产清理状态'][key],
                                    value: key,
                                });
                            }
                        }

                        for (const key in res['固定资产购入方式']) {
                            if (
                                Object.hasOwnProperty.call(
                                    res['固定资产购入方式'],
                                    key
                                )
                            ) {
                                treeData.purchaseMethod.push({
                                    label: res['固定资产购入方式'][key],
                                    value: key,
                                });
                            }
                        }
                    } else {
                        return this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => (this.spinning = false));
        },
        edit() {
            this.$refs.drageTableDialog.add(
                this.assemblyCode,
                this.selectTreeData
            );
        },
        check() {
            this.checkVisible = true;
            this.queryInsideCheck();
        },
        influenceEdit() {
            this.$refs.influenceDialog.showModal(
                this.assemblyCode,
                this.selectTreeData
            );
        },
        //下拉选择数据
        selectData(columns) {
            switch (columns.dicCode) {
                case '固定资产购入方式':
                    return this.selectTreeData.purchaseMethod;
                case '固定资产折旧方法':
                    return this.selectTreeData.depreciation;
            }
        },
        showInfluenceTable() {
            const influence = this.influenceTableData[0];
            if (influence && influence.data && influence.data.length) {
                return true;
            } else {
                return false;
            }
        },
        checkCancel() {
            this.checkVisible = false;
        },
        queryInsideCheck() {
            this.subjectVisible = true;
            this.$sacpApis
                .queryInsideCheck(this.publicParam)
                .then((res) => {
                    if (res.success) {
                        const {
                            checkNum,
                            completeNum,
                            relatedNum,
                            checkData,
                            amountNum,
                        } = res.data[0];

                        if (amountNum < relatedNum) {
                            this.checkBut = ture;
                        } else {
                            this.checkBut = false;
                        }
                        this.checkData = checkData;
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                    this.resetLoading = false;
                })
                .catch((err) => {
                    // this.$message.error(err);
                });
        },
        saveTradeFromEntry() {},
        mergeCustomerClick(cell) {
            let routeData = this.$router.resolve({
                path: '/mergeCustomerTable',
                query: {
                    mergeProjectId: cell.mergeProjectId,
                    mergeCustomerId: cell.mergeCustomerId,
                    mergeCustomerName: cell.mergeCustomerName,
                    mergeProjectName: cell.mergeProjectName,
                    userinfo: JSON.stringify(this.userinfo),
                    publicParam: JSON.stringify(this.publicParam),
                },
            });
            sessionStorage.setItem(
                'selectTreeData',
                JSON.stringify(this.selectTreeData)
            );
            window.open(routeData.href, '_blank');
        },
        sendCheckMessageConfirm(row) {
            this.$confirm({
                title: `提示`,
                content: '请确认是否要发送消息提醒对方完成关联方对账？',
                onOk: () => {
                    this.sendCheckMessage(row);
                },
                onCancel: () => {},
            });
        },
        // 企业微信提醒
        sendCheckMessage(row) {
            const param = {
                param1: row.mergeCustomerId,
                param2: row.mergeProjectId,
                param3: '请去往来做关联方交易汇总',
            };
            row.submitLoading = true;
            this.$sacpApis
                .sendCheckMessage(param)
                .then((res) => {
                    row.submitLoading = false;
                    if (res.success) {
                        this.$message.success('发送成功!');
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                });
        },
        mouseDownHandler(ev) {
            if (ev.target.classList.contains('resize-handler')) {
                this.dragStartX = ev.clientX;
                this.dragTarget = ev.target.parentElement;
                this.startWidth = parseInt(
                    getComputedStyle(this.dragTarget).width
                );
                document.addEventListener('mousemove', this.mouseMoveHandler);
            }
        },
        mouseMoveHandler(ev) {
            if (this.timer) {
                return;
            }

            if (!this.isDraging) {
                this.isDraging = true;
                this.calHandler(ev);
            }

            this.timer = setTimeout(() => {
                this.calHandler(ev);
            }, 30);
        },
        calHandler(ev) {
            if (!this.dragTarget) {
                return
            }

            const nextWidth = ev.clientX - this.dragStartX + this.startWidth;
            this.dragTarget.style.width = nextWidth + 'px';
            this.timer = null;
        },
        mouseUpHandler() {
            document.removeEventListener('mousemove', this.mouseMoveHandler);
            clearTimeout(this.timer);
            this.timer =  null;
            this.dragTarget = null;
            this.isDraging = false;
        },
        initTableResize() {
            document.addEventListener('mousedown', this.mouseDownHandler);
            document.addEventListener('mouseup', this.mouseUpHandler);
        },
        removeTableResizeHandler() {
            document.removeEventListener('mousedown', this.mouseDownHandler);
            document.removeEventListener('mousemove', this.mouseMoveHandler);
        },
    },
    destroyed() {
        this.removeTableResizeHandler();
    },
    mounted() {
        this.queryChildSubjects('存货');
        this.queryChildSubjects('固定资产');
        this.queryChildSubjects('无形资产');
        this.findAccSubjectType();
        this.queryDictionary();
        this.queryInsideCheck();
        this.initTableResize();
    },
};
</script>

<style lang="scss" scoped>
.title {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

</style>


